import * as genUtil from '../../../../../../../util/generalUtil'
import { PMData } from '../../../../../single/model/PMData'
import * as util from './DictUtil'

const pmData = () => PMData.Instance

export class Dict {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Project
  get Project() {
    return pmData().CurrentProject
  }

  // Item
  OnSet_Items_Func = []

  Refresh_Items = () => {
    this.Items = util.GetItems(this);
  }

  items
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this)
    }
    return this.items;
  } set Items(v){
    this.items = v;
    if (this.OnSet_Items){
      this.OnSet_Items(v)
    }
  }
}