import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import TableView from '../table/TableView';
import * as iUtil from '../item/base/ItemVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import './Dict.css'
import { Button, message } from 'antd';
import * as rs from '../../../../../text/TextRenderState';
import { SaveFilled } from '@ant-design/icons';

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem();
    var qI = iUtil.get(q, this);
    qI.RenderState = rs.edit

    this.Items.push(qI)
    return qI;
  }

  // Save
  Save = () => {
    var list = this.Items;
    var count = list.length;
    var i = 0;

    const func = () => {
      i++;
      if (i === count){
        message.success('save successfully!')
      }
    }

    list.forEach(x => {
      x.SaveDatabase(false, func)
    })
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get View() {
    return <div className='mt__wrapper'>
      <div className='f-jc mt__title'>
        <span>RNG BUC CATEGORY</span>
      </div>

      <div className='f'>
        <div className='mt__button__wrapper f'>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.AddItem}>+</Button>
          <Button type='primary' size='small'
            onClick={this.Save}>
            <SaveFilled />
          </Button>
        </div>

        <div className='f1'>
          <TableView store={this} />
        </div>
      </div>
    </div>
  }
}