import * as api from '../../../../../../../util/apiUtil'
import { PageItem } from './PageItem'
import * as typeUtil from '../type/base/TypeUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  qI.Name = data.name;
  qI.Types = data.items.map(x => typeUtil.GetFromDb(x, qI))

  return qI;
}

export const get = (dict) => {
  var qI = new PageItem();

  qI.Dict = dict;
  
  return qI;
}

