import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './PageItemVMUtil'
import './PageItem.css'
import * as rs from '../../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../../text/EditableTextView';
import * as typeUtil from '../type/base/TypeVMUtil'

export class PageItemVM {
  constructor() {
    makeObservable(this, {
      types: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.NameVM.RenderState = v;

    // if (v === rs._default) {
    //   this.SaveDatabase(false);
    // }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      var obj = this.nameVM = editTextUtil.get(this.Name, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.Name = obj.Content
            this.RenderState = rs._default
            break;
          case inputUtil.esc:
            obj.Content = this.Name
            this.RenderState = rs._default
            break;
        }
      }
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  get Wrapper_View() {
    return <div>
      <div className='f mes__cell__buttonwrapper'>
        <div className='f1' />
        <Button type='primary' size='small'
          className='btn-success'
          onClick={this.AddType}>
          +
        </Button>
        {this.DeleteVM.View}
      </div>

      <div onDoubleClick={this.Edit}
        onMouseEnter={this.OnMouseEnter}
        onMouseLeave={this.OnMouseLeave}>
        {this.NameView}
      </div>
    </div>
  }

  get RowSpan() {
    var span = this.Types.reduce((a, b) => a + b.RowSpan, 0);
    return span !== 0 ? span : 1
  }

  // Type
  types = undefined
  get Types() {
    if (!this.types) {
      this.types = util.GetTypes(this)
    } return this.types;
  }

  get NotFirst_Types() {
    var list = [...this.Types];
    list.splice(0, 1);
    return list;
  }

  AddType = () => {
    var q = this.ModelItem.AddType();
    var qI = typeUtil.get(q, this)
    qI.RenderState = rs.edit

    this.Types.push(qI)
    return qI
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return <>
      <tr className='mes__row'>
        <td rowSpan={this.RowSpan}>{this.Wrapper_View}</td>
        {this.Types.length > 0 ?
          this.Types[0].RowView
          : ''}
      </tr>

      {/* MassInfo Index > 0 */}
      {this.Types.length > 0 ?
        this.Types[0].NotFirst_MassInfos?.map(x => x.RowView)
        : ''}

      {/* Type Index > 0 */}
      {this.NotFirst_Types?.map(x => x.RowView)}
    </>
  }

  // Save
  Save = () => {
    this.Name = this.NameVM.Content;
    this.Types.forEach(x => x.Save())
  }

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}