import * as genUtil from '../../../../../../../util/generalUtil'
import { PMData } from '../../../../../single/model/PMData'
import * as util from './MDS_Dict_DictUtil'

const pmData = () => PMData.Instance

export class MDS_Dict_Dict {
  items
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this)
    } return this.items;
  }

  current_MDS
  get Current_MDS(){
    return this.current_MDS
  } set Current_MDS(v){
    this.current_MDS = v;

    if (v){
      var data = v.Data;
      this.Table.Refresh_Columns(data.infos, data.values);
    }
  }

  // Table
  get Table(){
    return pmData().MassDetail_Table
  }
}