import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import * as genUtil from '../../../../../../util/generalUtil'
import * as comUtil from '../../../../../../util/commandUtil'
import * as util from './ItemVMUtil'
import * as rs from '../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../text/EditableTextView';
import './Item.css'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pct from '../../../command/ProjectCommand'
import * as d_ut from '../dict/base/Dict_UseType'

export const pmDataVM = () => PMDataVM.Instance

export class ItemVM {
  constructor() {
    makeObservable(this, {
      nameForView : observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.NameVM.RenderState = v;

    if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  Edit = () => {
    if (this.CanEdit && comUtil.Validate(pct.edit)) {
      if (this.RenderState === rs._default) {
        this.RenderState = rs.edit
        this.Hide_Tool();
      }
    }
  }

  // UseType
  get CanEdit(){
    return this.Dict.QueryTab.CurrentItem.UseType === d_ut.partial
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
    this.NameForView = util.GetNameForView(this)
  }

  nameForView = undefined
  get NameForView(){
    if (!this.nameForView){
      this.nameForView = util.GetNameForView(this)
    } return this.nameForView;
  } set NameForView(v){
    this.nameForView = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      var obj = this.nameVM = editTextUtil.get(this.Name, this.CanEdit);

      obj.Placeholder = 'nhp thng tin ...'
      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.RenderState = rs._default
            break;
        }
      }

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === rs._default) {
            this.Name = obj.Content;
          }
        },
        false
      )

    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='sg__cell__main'
      store={this.NameVM} />
  }

  get NameWrapper_View() {
    return <div className='sg__cell__wrapper f'>
      <div className='sg__cell_name__wrapper'>
        {this.CanEdit ? this.NameView : this.NameForView}
      </div>
      {comUtil.Validate(pct.edit) ?
        <div className='sg__item__delete'>
          {comUtil.Validate(pct.edit) ? this.DeleteVM.View : ''}
        </div>
        : ''}
    </div>
  }

  // MassType
  get MassType(){
    return this.ModelItem.MassType
  }

  get Field(){
    return this.ModelItem.Field
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return <div className={`sg__row`}
      onDoubleClick={this.Edit}
      onMouseEnter={this.OnMouseEnter}
      onMouseLeave={this.OnMouseLeave}>
      {this.NameWrapper_View}
    </div>
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Data
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    // Cancel Close Popover
    this.Dict.CurrentPopover.IsCancel = true;

    this.CurrentList.splice(this.Index, 1)
    this.Dict.Items.forEach(x => {
      x.Items.splice(x.Items.indexOf(this), 1)
    })

    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  get Commands() {
    return pmDataVM().Commands;
  }

  // SetValue
  SetValue = () => {
    this.Dict.SetValue(this.Name);
  }
}