import * as util from './BulidUtil'

export class Bulid {
  Table

  get Data(){
    return this.Table.Data;
  }

  bycell
  get ByCell(){
    if (!this.bycell){
      this.bycell = util.GetByCell(this)
    } return this.bycell
  }

  byitem
  get ByItem(){
    if (!this.byitem){
      this.byitem = util.GetByItem(this)
    } return this.byitem
  }

  Run = () => {
    util.Run(this)
  }

  Finish = () => {
    util.Finish(this)
  }

  Initial = () => {
    util.Initial(this)
  }
}