import { ViewDetail } from "./ViewDetail"
import {url} from './ViewDetail_Url'
import * as api from '../../../../../../../util/apiUtil'

// Instance
export const get = (dict) => {
  var qI = new ViewDetail();
  qI.Dict = dict;
  return qI;
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}