import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './ItemVMUtil'
import './Item.css'
import * as rs from '../../../../../../text/TextRenderState';
import * as systemUtil from '../system/base/SystemVMUtil'

export class ItemVM {
  constructor() {
    makeObservable(this, {
      systems: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    // this.NameVM.RenderState = v;

    if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // Name
  get MassType() {
    return this.ModelItem.MassType
  } set MassType(v) {
    this.ModelItem.MassType = v
  }

  massType_SelectVM
  get MassType_SelectVM() {
    if (!this.massType_SelectVM) {
      this.massType_SelectVM = util.GetMassType_SelectVM(this)
    } return this.massType_SelectVM;
  }

  get MassTypeWrapper_View() {
    return <div className='mt__cell__wrapper'>
      {this.MassType_SelectVM.View}

      <Button type='primary' size='small'
        className='btn-success'
        onClick={this.AddSystem}>
        +
      </Button>

      {this.DeleteVM.View}
    </div>
  }

  get RowSpan() {
    var span = this.Systems.reduce((a, b) => a + b.RowSpan, 0);
    return span !== 0 ? span : 1
  }

  // Systems
  systems = undefined
  get Systems() {
    if (!this.systems) {
      this.systems = util.GetSystems(this)
    } return this.systems;
  }

  get NotFirst_Systems() {
    var list = [...this.Systems];
    list.splice(0, 1);
    return list;
  }

  AddSystem = () => {
    var q = this.ModelItem.AddSystem();
    var qI = systemUtil.get(q, this);
    qI.RenderState = rs.edit;

    this.Systems.push(qI)
    return qI;
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return <>
      <tr className='mt__row'>
        <td rowSpan={this.RowSpan}>{this.MassTypeWrapper_View}</td>
        {this.Systems.length > 0 ?
          this.Systems[0].RowView
          : ''}
      </tr>

      {/* SystemType Index > 0 */}
      {this.Systems.length > 0 ?
        this.Systems[0].NotFirst_Types?.map(x => x.RowView)
        : ''}

      {/* System Index > 0 */}
      {this.NotFirst_Systems?.map(x => x.RowView)}
    </>
  }

  // Save
  Save = (showMessage = true, handle) => {
    this.Systems.forEach(x => {
      x.Classification = x.ClassificationVM.Content
      x.Save();
    })

    this.SaveDatabase(showMessage, handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}