import { Project_Dict } from "./Project_Dict"
import * as itemUtil from '../base/ProjectUtil'
import { PMData } from "../../../single/model/PMData";
import { url } from '../base/Project_Url'

import * as genUtil from '../../../../../util/generalUtil'
import * as comUtil from '../../../../../util/commandUtil'

const pmData = () => PMData.Instance;

// Instance
export const get = (isAuto_GetData = true) => {
  var qI = new Project_Dict();

  // if (isAuto_GetData) {
  //   itemUtil.getAll(qI, (items) => {
  //     qI.Items = items;
  //   })
  // }

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => itemUtil.GetFromDb(x, q));
    q.IsGet_Items = false;
  }

  const func1 = () => {
    var apiData = {}

    // if (commands && commands.includes('admin')) {
    if (comUtil.Validate('admin')) {
      apiData.fortest = true;
    }

    itemUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  var host = pmData();
  func1(host.AdminCommands);

  genUtil.Add_2(host.OnSet_AdminCommands_Func,
    () => Handle_Items(q), false);
}