import { ValueInfo } from './ValueInfo'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  
  qI.RetrieveTypes = data.valueRetrieveTypes
  qI.Parameter = data.parameter
  qI.ParameterList = data.parameterList ? data.parameterList : []

  return qI;
}

export const get = (dict) => {
  var qI = new ValueInfo();

  qI.Dict = dict;
  
  return qI;
}

