// SiteDict
export const Handle_SG_Dict_Dict = (q) => {
  if (q.IsGet_SG_Dict_Dict) return undefined;
  q.IsGet_SG_Dict_Dict = true;

  const setObj = (data) => {
    if (data) {
      q.SG_Dict_Dict = data.length !== 0 ?
        sgddUtil.GetFromDb(data[0])
        : sgddUtil.get()
    }
    q.IsGet_SG_Dict_Dict = false;
  }

  var host = q.CurrentProject;
  if (!host) {
    setObj();
  } else {
    var apiData = {
      projectId: host._id,
    }

    sgddUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }
}

// UserDict
export const GetUserDict = (data) => {
  var qI = userDictUtil.get(
    "https://api.bimdev.vn/api/hbu",
    data);

  qI.OnSet_LoginUser = (v) => {
    if (data.CurrentProject) {
      data.CurrentProject.Reset_Commands();
    }
    data.Reset_Commands();
    data.Reset_AdminCommands();
  }

  return qI;
}

export const GetAdmin = (data) => {
  return adminUtil.get(data);
}

// Commands
export const Handle_Commands = (q) => {
  if (q.IsGet_Commands) return undefined;
  q.IsGet_Commands = true;

  const setObj = (data, fromType) => {
    // u tin data c projectId

    if (fromType === 'api') {
      // Nu  c data phn project, b qua thit lp ny
      if (q.Command_FromType === 'project') {
        return
      } else {
        q.Commands = data && data.length !== 0 ? data[0].commands : []
      }
    } else {
      q.Commands = data
    }

    q.Command_FromType = fromType;
    q.IsGet_Commands = false;
  }

  var host = q.CurrentProject;
  if (host) {
    if (host.Commands) {
      setObj(host.Commands)
    } else {
      genUtil.Add_2(host.OnSet_Commands_Func,
        () => {
          setObj(host.Commands, 'project')
        })
    }
  } else {
    var user = q.UserDict.LoginUser
    if (!user) {
      setObj(undefined, 'api');
    } else {
      var apiData = {
        userId: user._id
      }

      const apiFunc = () => api.post(`${pgc_url}/list`, apiData);
      apiFunc().then(response => response.data)
        .then(data => {
          setObj(data, 'api')
        })
    }
  }
}

// Admin Command
export const Handle_AdminCommands = (q) => {
  if (q.IsGet_AdminCommands) return undefined;
  q.IsGet_AdminCommands = true;

  const setObj = (data) => {
    q.AdminCommands = data && data.length !== 0 ? 
      data[0].commands : []
    q.IsGet_AdminCommands = false;
  }

  var user = q.UserDict.LoginUser
  if (!user) {
    setObj();
  } else {
    var apiData = {
      userId: user._id
    }

    const apiFunc = () => api.post(`${uc_url}/list`, apiData);
    apiFunc().then(response => response.data)
      .then(data => {
        setObj(data)
      })
  }
}

