import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Table.css'
import { Phase_DictVM } from '../dict/Phase_DictVM';
import * as itemUtil from '../item/PhaseVMUtil'

import * as comUtil from '../../../../../util/commandUtil'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Phase_DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered'>
          <thead className='es__thead'>
            <tr>
              <th className='phase__table__name'>Tn</th>
              <th className='text-center'>M t</th>
              <th className='phase__table__command' />
            </tr>
          </thead>
          <tbody>
            {data.Items?.map(x => x.RowView)}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
