import { CommandItem } from "./CommandItem"
import * as tct from './CommandType'
import * as tctt from './Command_TargetType'
import * as dataUtil from '../data/base/DataUtil'
import * as rowUtil from '../../../row/base/MassRowUtil'

// Instance
export const get = (data, target, dict) => {
  var qI = new CommandItem();

  qI.Data = dataUtil.GetFromDb(data, qI);
  qI.Target = target
  qI.Dict = dict;

  return qI;
}

export const Clone = (q, dict) => {
  var qI = new CommandItem();

  qI.Data = q.Data.Clone();
  qI.Dict = dict ? dict : q.Dict;

  return qI;
}

// Property
export const GetTarget = (q) => {
  var { Target } = q.Data
  var { type, key } = Target
  if (!key) return undefined

  var table = q.Table

  switch (type) {
    case tctt.row:
      return table.TBody.GetRow(key);
    case tctt.rowlist:
      return key.map(x => table.TBody.GetRow(x))
  }
}

// Method
export const Do = (q, handle) => {
  var target = q.Target
  var data = q.Data;

  var action = data.Action;
  var value = data.Value;
  var result = data.Result;

  var type = data.Target.type;

  const setUndo = (undo) => q.Undo = undo;
  const setHandle = () => {
    if (handle) {
      handle();
    }
  }

  switch (action) {
    case tct.retitle:
      target.ReTitle(value, setHandle, setUndo);
      break;
    case tct.subgroup:
      target.GroupFactory.MainRun(value, setHandle, setUndo);
      break;
    case tct._delete:
      {
        switch (type) {
          case tctt.row:
            target.DeleteData.DeleteHost_Main(setHandle, setUndo)
            break;
        }
      }
      break;
    case tct.replace:
      target.Replace_SubItems_Main(value.RowValue, setHandle, setUndo);
      break;
    case tct.add:
      var row = rowUtil.Add_Main(target, result, setHandle, setUndo)

      // Rng buc command ang set vo lu vt thng tin v sau
      // khi khng tn ti Result
      if (!result) {
        row.BindingCommand = q;
      }
      break
    case tct.move:
      rowUtil.Move_Main(target[0], target[1], setHandle, setUndo);
      break;
    case tct.changeunit:
      target.UnitData.SelectUnit_Main(value, setHandle, setUndo);
      break;
  }
}

export const Result = (q, obj) => {
  var data = q.Data;

  var action = data.Action;

  switch (action) {
    case tct.add:
      data.Result = {
        raw: obj.RawValue,
        title: obj.Title
      }
      break;
  }
}