import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './TypeVMUtil'
import * as rs from '../../../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../../../text/EditableTextView';
import * as massInfoUtil from '../../massinfo/base/MassInfoVMUtil'

export class TypeVM {
  constructor() {
    makeObservable(this, {
      massInfos: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.NameVM.RenderState = v;

    // if (v === rs._default) {
    //   this.SaveDatabase(false);
    // }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      var obj = this.nameVM = editTextUtil.get(this.Name, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.Name = obj.Content
            this.RenderState = rs._default
            break;
          case inputUtil.esc:
            obj.Content = this.Name
            this.RenderState = rs._default
            break;
        }
      }
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  get Wrapper_View() {
    return <div>
      <div className='f mes__cell__buttonwrapper'>
        <div className='f1' />

        <Button type='primary' size='small'
          className='btn-success'
          onClick={this.AddMassInfo}>
          +
        </Button>
        {this.DeleteVM.View}
      </div>

      <div className='mes__cell__innertable'>
        <table>
          <tbody>
            <tr>
              <td className='title small'>Tn: </td>
              <td onDoubleClick={this.Edit}>{this.NameView}</td>
            </tr>
            <tr>
              {this.Filter.View}
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  }

  get RowSpan() {
    return this.MassInfos.length > 1 ? this.MassInfos.length : 1
  }

  // Filter
  filter
  get Filter(){
    if (!this.filter){
      this.filter = util.GetFilter(this)
    } return this.filter
  }

  // MassInfo
  massInfos = undefined
  get MassInfos() {
    if (!this.massInfos) {
      this.massInfos = util.GetMassInfos(this)
    } return this.massInfos
  }

  get NotFirst_MassInfos() {
    var list = [...this.MassInfos];
    list.splice(0, 1);
    return list;
  }

  AddMassInfo = () => {
    var q = this.ModelItem.AddMassInfo()
    var qI = massInfoUtil.get(q, this)
    qI.RenderState = rs.edit;

    this.MassInfos.push(qI);
    return qI;
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return this.Index === 0 ?
      <>
        <td key={this.id} rowSpan={this.RowSpan}>
          {this.Wrapper_View}
        </td>
        {this.MassInfos.length > 0 ?
          this.MassInfos[0].RowView
          : ''}
      </>
      :
      <>
        <tr key={this.id}>
          <td rowSpan={this.RowSpan}>
            {this.Wrapper_View}
          </td>
          {this.MassInfos.length > 0 ?
            this.MassInfos[0].RowView
            : ''}
        </tr>

        {/* MassInfo Index > 0 */}
        {this.NotFirst_MassInfos?.map(x => x.RowView)}
      </>
  }

  // Save
  Save = () => {
    this.Name = this.NameVM.Content;
  }

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Types
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}