import { LS_ItemVM } from "./LS_ItemVM"
import { EntDelete } from "../../../../../delete/EntDelete";
import './LS_Item.css'

import * as genUtil from '../../../../../../util/generalUtil'
import * as inputUtil from "../../../../../../util/inputUtil";
import * as dragUtil from "../../../../../../util/dragUtil";
import * as comUtil from "../../../../../../util/commandUtil";

import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { DragOutlined } from "@ant-design/icons";

// Instance
export const get = (q, dict) => {
  var qI = new LS_ItemVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Proeprty
export const Handle_Value_StorageList = (q) => {
  if (q.IsGet_Value_StorageList) return undefined;
  q.IsGet_Value_StorageList = true;

  var project = q.Project.ModelItem

  var setObj = (levelDict) => {

    var setObj2 = (items) => {
      q.Value_StorageList = items.map(x => x.Name);
      q.IsGet_Value_StorageList = false;
    }

    if (levelDict.Items) {
      setObj2(levelDict.Items)
    } else {
      genUtil.Add_2(
        levelDict.OnSet_Items_Func,
        () => setObj2(levelDict.Items)
      )
    }

  }

  if (project.LevelDict) {
    setObj(project.LevelDict)
  } else {
    genUtil.Add_2(
      project.OnSet_LevelDict_Func_Dict,
      () => {
        setObj(project.LevelDict)
      }, false);
  }
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

