import { PMData } from '../../../../single/model/PMData';
import { TypicalLevelSettingData } from './TypicalLevelSettingData';
import * as pageItemUtil from '../pageItem/TLS_PageItemUtil'

export const pmData = ()  => PMData.Instance;

export const get = (data, dict) => {
  var qI = new TypicalLevelSettingData();
  qI.PageItems = data.pageItems.map(x => pageItemUtil.get(x, qI));
  qI.Dict = dict;
  return qI;
}

export const GetTest = (dict) => {
  var qI = new TypicalLevelSettingData();
  qI.Dict = dict;
  return qI;
}

export const Clone = (q, dict) => {
  var qI = new TypicalLevelSettingData();

  qI.PageItems = q.PageItems.map(x => pageItemUtil.Clone(x, qI));
  qI.Dict = dict ? dict : q.Dict;

  return qI;
}

// Property
export const GetPageItems = (q) => {
  return [ pageItemUtil.GetDefault(q) ]
}

// Method
export const IsEqual = (q1, q2) => {
  // PageItem
  var p1 = q1.PageItems;
  var p2 = q2.PageItems;

  if (p1.length !== p2.length) return false;

  var isValid = true;
  // PageItem phi ng th t v bng nhau
  p1.forEach((x, i) => {
    if (isValid){
      if (x.IsEqual(p2[i])){

      }else {
        isValid = false;
      }
    }
  })

  return isValid;
}