import { makeObservable, observable, action } from 'mobx';
import * as util from './MR_UnitUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as rs from './MR_Unit_RenderState'
import SelectView from '../../../../../../select/SelectView';
import * as u_dt from '../../../../update/Update_DirectionType'

export class MR_Unit {
  constructor() {
    makeObservable(this, {
      unitDict: observable,
      renderState: observable,
      currentUnitItem : observable,
      IsMissing : observable
    })
  }

  get Row(){
    return this.Dict;
  }

  get Table() {
    return this.Row.Table
  }

  // Update
  update_DirectionType
  get Update_DirectionType() {
    if (!this.update_DirectionType) {
      this.update_DirectionType =
        util.GetUpdate_DirectionType(this)
    } return this.update_DirectionType
  } set Update_DirectionType(v) {
    this.update_DirectionType = v
  }

  // Unit
  OnSet_UnitDict_Func = []

  unitDict = undefined
  get UnitDict() {
    if (!this.unitDict) {
      util.Handle_UnitDict(this)
    } return this.unitDict
  } set UnitDict(v) {
    this.unitDict = v;
    genUtil.Handle(this.OnSet_UnitDict_Func,
      (v) => this.OnSet_UnitDict_Func = v)
  }

  Reset_UnitDict = () => {
    util.Handle_UnitDict(this);
  }

  currentUnitItem = undefined
  get CurrentUnitItem() {
    return this.currentUnitItem
  } set CurrentUnitItem(v) {
    this.currentUnitItem = v;

    // Check Missing
    if (this.IsMissing_Pending){
      this.IsMissing_Pending = false;
      this.IsMissing = true;
    } else {
      this.IsMissing = false;
    }

    // Set CurrentUnit
    this.CurrentUnit = v?.Unit
  }

  currentUnit
  get CurrentUnit() {
    return this.currentUnit
  } set CurrentUnit(v) {
    if (this.currentUnit === v) {
      this.Update_DirectionType = u_dt.none
      // return
    }

    this.currentUnit = v
    util.Set_CurrentUnit(this, v);
  }

  SetUnit_FromHost = (v) => {
    const setObj = () => {
      this.IsUndo = false;

      if (!v) {
        this.Unit_SelectVM.SetValue(v);
      }
      else {
        var list = this.UnitDict;
        var index = list.findIndex(x => x.Unit === v);
        if (index !== -1) {
          this.Unit_SelectVM.SetValue(list[index]);
        } else {
          this.IsMissing_Pending = true;
          this.Unit_SelectVM.SetValue(undefined);
        }
      }
    }

    if (this.UnitDict) {
      setObj()
    } else {
      genUtil.Add_2(
        this.OnSet_UnitDict_Func,
        setObj
      )
    }
  }

  Is_Unit_Select_Created

  unit_SelectVM
  get Unit_SelectVM() {
    if (!this.unit_SelectVM) {
      this.unit_SelectVM = util.GetUnit_SelectVM(this)
    } return this.unit_SelectVM;
  }

  get Unit_RenderView() {
    return util.GetUnit_RenderView(this)
  }

  renderState = rs.text
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
  }

  OnMouseEnter = () => {
    this.RenderState = rs.select
  }

  OnMouseLeave = () => {
    if (this.Unit_SelectVM.Open) {
      return
    }
    this.RenderState = rs.text
  }

  get MainView() {
    var obj = this.Unit_SelectVM;

    switch (this.RenderState) {
      case rs.select:
        return <SelectView className='massrow__unit__select'
          store={this.Unit_SelectVM} />
      case rs.text:
        return this.Unit_RenderView
    }
  }

  get Update() {
    return this.Row.Update
  }

  IsUndo = true;

  SelectUnit_Main = (value, handle, handleUndo) => {
    util.SelectUnit_Main(this, value, handle, handleUndo);
  }

  SetUndo = (handleUndo) => {
    util.SetUndo(this, handleUndo)
  }

  // Render
  ItemRender = (v) => {
    return util.ItemRender(this, v)
  }

  IsMissing = false

  get MissingCss(){
    return this.IsMissing ? 'mr__unit__missing' : ''
  }
}