import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Row } from 'antd';
import { withRouter } from "react-router-dom";
import { MassDetailVM } from './MassDetailVM';
import { PMDataVM } from '../../../single/form/PMDataVM';
import './MassDetail.css'
import { SaveFilled } from '@ant-design/icons';

const pmDataVM = () => PMDataVM.Instance;

class MassDetailView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassDetailVM();
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    var data = this.state;
    const id = this.props.match.params.id;

    data.id = id;

    // Set up Main Width
    pmDataVM().Main_Classname = 'massdetail__comp'
  }

  render() {
    var data = this.state;

    return (
      <>
        <div>
          {pmDataVM().Mds_Dict_Dict.Pick_MDS_View}
          {pmDataVM().Mds_Dict_Dict.ModalView}
        </div>
        <div className='massdetail__table__wrapper'>
          {data.DetailTable.WrapperView}
        </div>
      </>
    );
  }
}

export default withRouter(observer(MassDetailView));
