import * as api from '../../../../../util/apiUtil'
import * as genUtil from '../../../../../util/generalUtil'
import Phase from './Phase';
import { url } from './Phase_Url'
import { PMData } from '../../../single/model/PMData';
import { message } from 'antd';

import { url as tls_url } from '../../setting/typicallevel/base/TypicalLevelSetting_Url'
import * as typlicalLevelSettingUtil from '../../setting/typicallevel/base/TypicalLevelSettingUtil'

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  if (!data) return undefined;

  var qI = get(dict);

  qI._id = data._id;
  qI.Name = data.name;
  qI.Description = data.description;

  return qI;
}

export const get = (dict) => {
  var qI = new Phase();
  qI.Dict = dict;
  return qI;
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

// Property
export const Handle_TypicalLevelSetting = (q) => {
  if (q.IsGet_TypicalLevelSetting) return undefined;
  q.IsGet_TypicalLevelSetting = true;

  const setObj = (data) => {
    q.TypicalLevelSetting = typlicalLevelSettingUtil.get(
      data, pmData().CurrentProject, q);
    q.IsGet_TypicalLevelSetting = false
  }

  var apiData = {
    projectId: pmData().CurrentProject._id,
    phaseId: q._id
  }

  api.post(`${tls_url}/get`, apiData)
    .then(response => response.data).then(data => {
      setObj(data)
    })
}

// Method
export const Run = (q, mainHandle) => {
  const finish = () => {
    if (mainHandle) {
      mainHandle();
    }
  }

  var incre = genUtil.IncreFunc(4, finish);

  const save_esversions = () => {
    var apiData = {
      phaseId: q._id,
      projectId: pmData().CurrentProject._id
    }

    api.post(`${url}/save_exportsetversion`, apiData)
      .then(response => response.data).then(data => {
        incre();
      })
  }

  const save_report = () => {
    var apiData = {
      phaseId: q._id,
      projectId: pmData().CurrentProject._id
    }

    api.post(`${url}/save_report`, apiData)
      .then(response => response.data).then(data => {
        incre();
      })
  }

  const save_levelsetting = () => {
    var apiData = {
      phaseId: q._id,
      projectId: pmData().CurrentProject._id
    }

    api.post(`${url}/save_levelsetting`, apiData)
      .then(response => response.data).then(data => {
        incre();
      })
  }

  const save_exportset_value = () => {
    var apiData = {
      phaseId: q._id,
      projectId: pmData().CurrentProject._id
    }

    api.post(`${url}/save_exportset_value`, apiData)
      .then(response => response.data).then(data => {
        incre();
      })
  }

  q.Save(() => {
    save_esversions();
    save_report();
    save_levelsetting();
    save_exportset_value();
  });
}