import { Item } from "./Item"
import * as mi_ValueItemUtil from '../../../../item/value/MI_ValueItemUtil'
import * as mif from '../../../../field/MassItemField'
import * as rowUtil from '../../../row/base/MassRowUtil'

// Instance
export const get = (dict) => {
  var qI = new Item();
  qI.Dict = dict;
  return qI;
}

// Method
export const Run = (q, cells, col, mainHandle) => {
  var table = q.Table;

  rowUtil.Handle_FromCellData(cells, col, undefined, table, (rows) => {
    // table.TBody.Rows = rows

    if (mainHandle){
      mainHandle();
    }
  });
}
