import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { LevelSettingDataVM } from '../data/LevelSettingDataVM';
import './Table.css'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import * as itemUtil from '../item/LS_ItemVMUtil'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new LevelSettingDataVM();
    } else {
      this.state = store;
    }
  }

  

  render() {
    var data = this.state;
    console.log(`mapping level count : ${data.Items?.length}`)

    return (
      <>
        <table className='table table-bordered'>
          <thead className='mt__thead'>
            <tr>
              <th className='mt__table__move'></th>
              <th className='mt__table__name'>Tn</th>
              <th>Tng ng vi</th>
            </tr>
          </thead>

          <DragDropContext onDragEnd={data.OnDragEnd}>
            <Droppable droppableId='mapping-level'>
              {(provided) =>
                <tbody {...provided.droppableProps} ref={provided.innerRef}>
                  {data.Items.map((x, i) => itemUtil.GetRowView(x, i))}
                  {provided.placeholder}
                </tbody>
              }
            </Droppable>
          </DragDropContext>
        </table>
      </>
    );
  }
}

export default observer(TableView);
