import { PageItemVM } from './PageItemVM'
import { EntDelete } from '../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import * as typeUtil from '../type/base/TypeVMUtil'
import { Button, message } from 'antd';

// Instance
export const get = (q, dict) => {
  var qI = new PageItemVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const GetTypes = (q) => {
  return q.ModelItem.Types.map(x => typeUtil.get(x, q));
}