import { Dict } from "./Dict"
import * as itemUtil from '../item/ItemUtil'
import { url } from '../item/Item_Url'
import * as api from '../../../../../util/apiUtil'
import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const get = (dict) => {
  var qI = new Dict();
  qI.Dict = dict;
  return qI;
}

// Api


// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => itemUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  var apiData = {
    
  }

  itemUtil.GetData(apiData, (data) => setObj(data));
}

export const Handle_Undefined_Item = (q) => {
  if (q.IsGet_Undefined_Item) return undefined;
  q.IsGet_Undefined_Item = true;

  const setObj = (data) => {
    q.Undefined_Item = genUtil.SingleFilter(data, x => x.Name ==='undefined');
    q.IsGet_Undefined_Item = false;
  }

  if (q.Items){
    setObj(q.Items)
  } else {
    genUtil.Add_2(q.OnSet_Items_Func,
      () => setObj(q.Items))
  }
}

// Method
export const GetItem = (q, name, handle) => {
  if (!name || name.length === 0) {
    const getObj = () => {
      handle(q.Undefined_Item)
    }

    if (q.Undefined_Item){
      getObj();
    } else {
      genUtil.Add_2(q.OnSet_Undefined_Item_Func,
        getObj);
    }

  } else {
    const getObj = () => {
      var list = q.Items;
      var item = genUtil.SingleFilter(list, x => x.Name === name);
      if (item) {
        handle(item)
      } else {
        var apiData = {
          name
        }

        const func = (data) => {
          item = itemUtil.GetFromDb(data, q);
          list.push(item);
          handle(item);
        }

        const apiFunc = () => api.post(`${url}/add`, apiData);

        apiFunc().then(response => response.data)
          .then(data => {
            func(data)
          })
      }
    }

    if (q.Items){
      getObj();
    } else {
      genUtil.Add_2(q.OnSet_Items_Func,
        getObj);
    }
  }
}