import * as genUtil from '../../../../../../../../../util/generalUtil'
import { PMData } from '../../../../../../../single/model/PMData'

const pmData = () => PMData.Instance;

export class ValueItem {
  Dict

  HandledItems = []

  AddItem = (v) => {
    this.HandledItems.push(v);
  }

  On_DeleteItem_Func = []

  On_Clear_Func = []

  DeleteItem = (v) => {
    var list = this.HandledItems;
    var index = list.findIndex(x => {
      return x == v
    })

    if (index === -1) return;

    list.splice(list.indexOf(v), 1);

    genUtil.Handle(this.On_DeleteItem_Func,
      (v) => this.On_DeleteItem_Func = v)

    if (list.length === 0) {
      genUtil.Handle(this.On_Clear_Func,
        (v) => this.On_Clear_Func = v)
    }
  }
}