import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { EntPopover } from './EntPopover';
import { Button, Popover } from 'antd';
import './EntPopover.css'
import * as tt from './EntPopover_TriggerType'

class EntPopoverView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new EntPopover()
    } else {
      this.state = store;
    }

    this.state.ClickRef = React.createRef();
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='popover__wrapper' style={data.WrapperStyle}
          onMouseEnter={data.Trigger === tt.hover ? data.ShowContent : undefined}
          onMouseLeave={data.Trigger === tt.hover ? data.HideContent : undefined}>
          <div className='popover__title__wrapper'
            onClick={data.Trigger === tt.click ? data.ToggleContent : undefined}>
            {data.Title}
          </div>
          {data.Is_Content_Visible ?
            <div className='popover__content__wrapper' ref={data.ClickRef}>
              <div className='popover__content__dec'>
                <div className='popover__content__arrow' />
                {data.Content}
              </div>
            </div>
            : ''}
        </div>
      </>
    );
  }
}

export default observer(EntPopoverView);
