import { SelectVM } from '../../../../../../../select/SelectVM';
import { ValueInfoVM } from './ValueInfoVM'
import * as vi_rt from './VI_RetrieveType'
import * as genUtil from '../../../../../../../../util/generalUtil'
import { MultiSelectVM } from '../../../../../../../select/MultiSelectVM';
import { EntDelete } from '../../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../../delete/EntDelete_ToggleType'
import { PMDataVM } from '../../../../../../single/form/PMDataVM';

const pmDataVM = () =>  PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new ValueInfoVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const GetRetrieveTypes_SelectVM = (q) => {
  var qI = new MultiSelectVM();

  qI.Items = vi_rt.GetList();
  qI.InitialItems = qI.SelectedItems = q.RetrieveTypes

  qI.OnSelectedItemChanged = (v) => q.RetrieveTypes = v;

  return qI;
}

export const GetParameter_SelectVM = (q) => {
  var qI = new SelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    var parameter = q.ModelItem.Parameter;
    if (parameter) {
      qI.InitialItem = qI.SelectedItem =
        genUtil.SingleFilter(list, (x) => x.Name === parameter);
    }
  }

  var host = pmDataVM().Parameter_Dict;
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }

  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.Parameter = v?.Name

  return qI;
}

export const GetParameterList_SelectVM = (q) => {
  var qI = new MultiSelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    var parameterList = q.ModelItem.ParameterList;
    if (parameterList.length !== 0) {
      qI.InitialItems = qI.SelectedItems =
        list.filter((x) => parameterList.includes(x.Name));
    }
  }

  var host = pmDataVM().Parameter_Dict;
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }

  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.ParameterList = v.map(x => x.Name)

  return qI;
}