import { SaveFilled } from '@ant-design/icons';
import { Button, message } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../util/generalUtil'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as util from './SaveVMUtil'
import * as mdsdd_ut from '../dict/dict/MDS_Dict_Dict_UseType'

const pmDataVM = () => PMDataVM.Instance

export class SaveVM {
  constructor() {
    makeObservable(this, {

    })
  }

  get Name(){
    return this.ModelItem.Name;
  } set Name(v){
    this.ModelItem.Name = v
  }

  Run = () => {
    this.ModelItem.Run();

    var q1 = pmDataVM().Mds_Dict_Dict;
    if (q1.UseType === mdsdd_ut.save){
      q1.Is_Modal_Visible = false;
    }
  }
}