import { message } from 'antd';
// import { url } from './PageItem_Url'
import * as genUtil from '../../../../../../../../util/generalUtil'
import * as util from './TypeUtil'
import * as massInfoUtil from '../../massinfo/base/MassInfoUtil'

export class Type {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name

  // Category
  Categories = []

  // Filter
  filter
  get Filter(){
    if (!this.filter){
      this.filter = util.GetFilter(this)
    } return this.filter
  } set Filter(v){
    this.filter = v;
  }

  // MassInfo
  MassInfos = []

  AddMassInfo = () => {
    var q = massInfoUtil.get(this)
    this.MassInfos.push(q);
    return q;
  }

  get ApiData() {
    return {
      name : this.Name,
      filter: this.Filter.ApiData,
      massInfos : this.MassInfos.map(x => x.ApiData)
    }
  }

  // Index
  get CurrentList() {
    return this.Dict.Types
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    // this.DeleteDatabase(true)
  }
}