import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './FilterVMUtil'

export class FilterVM {
  // Category
  get Categories() {
    return this.ModelItem.Categories;
  } set Categories(v) {
    this.ModelItem.Categories = v
  }

  category_SelectVM
  get Category_SelectVM() {
    if (!this.category_SelectVM) {
      this.category_SelectVM = util.GetCategory_SelectVM(this)
    } return this.category_SelectVM;
  }

  // View
  get View() {
    return <>
      <td className='title small'>B lc: </td>
      <td>
        <div>
          {this.Category_SelectVM.View}
        </div>
      </td>
    </>
  }
}