import { makeObservable, observable, action } from 'mobx';
import * as util from './Phase_DictVMUtil'
import TableView from '../table/TableView'
import * as itemUtil from '../item/PhaseVMUtil'

import * as genUtil from '../../../../../util/generalUtil'
import * as comUtil from '../../../../../util/commandUtil'
import { Button } from 'antd';

export class Phase_DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this);
    } return this.items;
  } set Items(v) {
    this.items = v;

    this.GetItemsDone = true;
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  set GetItemsDone(v) {
    if (this.OnGetItemsDone) {
      this.OnGetItemsDone(v);
    }
  }

  AddItem = () => {
    var item = this.ModelItem.AddItem();
    var itemVM = itemUtil.get(item, this);

    this.Items.push(itemVM);
    return itemVM;
  }

  // select
  selectVM
  get SelectVM() {
    if (!this.selectVM) {
      this.selectVM = util.GetSelectVM(this)
    } return this.selectVM;
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get View() {
    return <div className='f'>
      {comUtil.Validate('edit') ?
        <div className='es__add'>
          <Button type='primary' size='small'
            className='btn-success addproject'
            onClick={this.AddItem}>+</Button>
        </div>
        : ''}
      <div className='es__main f1'>
        <TableView store={this} />
      </div>
    </div>
  }
}