import { Button, message } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import InputView from '../../../../input/InputView';
import { TextVM } from '../../../../text/TextVM';
import ShowView from '../../svg/ShowView';
import * as util from './ForgeUtil'
import * as helperUtil from './Forge_HelperUtil'
import { url } from './Forge_Url'
import * as genUtil from '../../../../../util/generalUtil'
import * as comUtil from '../../../../../util/commandUtil'
import { PMData } from '../../../single/model/PMData';
import './Forge.css'
import './ForgeSpinner.css'
import * as pct from '../../command/ProjectCommand'
import { PMDataVM } from '../../../single/form/PMDataVM';

const pmData = () => PMData.Instance
const pmDataVM = () => PMDataVM.Instance

export class Forge {
  constructor() {
    makeObservable(this, {
      IsLoggedIn: observable,
      Can_Visible: observable,
      documentId: observable
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // DocumentId = 'dXJuOmFkc2sud2lwcHJvZDpmcy5maWxlOnZmLl9ERHpscjFsVG8ycFJ2UEk4ZjhjNUE_dmVyc2lvbj0x';
  documentId = undefined
  get DocumentId() {
    return this.documentId
  } set DocumentId(v) {
    this.documentId = v;
  }

  get DocumentIds() {
    return [this.DocumentId]
  }

  Initial = (handle) => {
    this.IsLoggedIn = true;
    // util.LaunchViewer(this, 'viewerDiv', this.DocumentId, handle)
    helperUtil.launchViewer(this, 'viewerDiv', this.DocumentIds, handle)
  }

  IsLoggedIn = undefined

  Can_Visible = false

  get Is_Visible() {
    return this.DocumentId && this.Can_Visible
  }

  Is_Initial

  ToggleVisible = () => {
    var v = this.Can_Visible = !this.Can_Visible;
    if (v && !this.Is_Initial) {
      this.Initial();
    }
  }

  get VisibleButton_Style() {
    return this.Can_Visible ?
      'forge__button close' : 'forge__button open'
  }

  get ToggleView_Button() {
    var size = 12;
    return <Button type='primary' size='small'
      className={this.VisibleButton_Style}
      onClick={this.ToggleVisible}>
      <ShowView className='forge__togglevisible__icon' width={size} height={size} />
    </Button>
  }

  get ModalViewerStyle() {
    return this.Can_Visible ? undefined : { display: 'none' }
  }

  inputVM
  get InputVM() {
    if (!this.inputVM) {
      var obj = this.inputVM = new TextVM();
      obj.Placeholder = 'urn ...'
      obj.Content = this.DocumentId
      obj.OnSetContent = (v) => this.TempDocumentId = v
    } return this.inputVM;
  }

  Input_Open = () => {
    this.DocumentId = this.TempDocumentId;
    this.Initial((status) => {
      if (!status) {
        message.error('Wrong urn!')
        this.DocumentId = undefined
      }
      this.SaveDatabase(false)
    });
  }

  get InputView() {
    return <>
      {comUtil.Validate(pct.edit) ?
        <div>
          <div className='forge__input__dec f-ac'>
            <div className='forge__title'>urn:</div>
            <InputView className='forge__input f1' store={this.InputVM} />
          </div>
          <Button type='primary' size='small'
            onClick={this.Input_Open}>OPEN</Button>
        </div>
        : pmDataVM().UnAuthorized_View}
    </>
  }

  Edit_Urn = () => {
    this.DocumentId = undefined;
  }

  get ModelView() {
    return <>
      <div className='f'>
        {comUtil.Validate(pct.edit) ?
          <Button type='primary' size='small'
            className='btn-warning'
            onClick={this.Edit_Urn}>Edit Urn</Button>
          : ''}
      </div>
      {
        this.IsLoggedIn ?
          <div className="forge-viewer" id='viewerDiv' />
          : ''
      }
    </>
  }

  get View() {
    return <div className='forge__wrapper' style={this.ModalViewerStyle}>
      {this.DocumentId ?
        this.ModelView
        : this.InputView
      }
    </div>
  }

  // Select
  SelectElements = (guids) => {
    // util.getElementWithExternalId(guids)
    helperUtil.getElementWithExternalId(guids);
  }

  // Database
  get ApiData() {
    return {
      _id: this._id,
      projectId: pmData().CurrentProject._id,
      documentId: this.DocumentId,
    }
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }
}