import * as genUtil from '../../../../../../util/generalUtil'
import { EntDelete } from '../../../../../delete/EntDelete';
import * as d_tt from '../../../../../delete/EntDelete_ToggleType'
import * as d_ut from '../../../../../delete/EntDelete_UseType'
import * as d_vt from '../../../../../delete/EntDelete_VisibleType'
import { Button, message } from 'antd';
import { CheckCircleOutlined, CloseCircleOutlined, SaveFilled } from '@ant-design/icons';
import { ItemVM } from './ItemVM';

// Instance
export const get = (q, dict) => {
  var qI = new ItemVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetNameForView = (q) => {
  return `${q.Name} (${q.MassType})`;
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent
  qI.UseType = d_ut.style
  qI.Retrieve_WrapperView = () => <div className='f'>
    <Button type='primary' size='small'
      onClick={q.SetValue}>ok
    </Button>
    {q.CanEdit ?
      <Button type='primary' size='small'
        className='btn-danger'
        onClick={q.Delete}>x
      </Button>
      : ''}
  </div>

  return qI;
}