import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Col, Row } from 'antd';
import { withRouter } from "react-router-dom";
import './ProjectComp.css'
import * as util from './ProjectCompVMUtil'
import { PMData } from '../../../single/model/PMData';
import * as dot from '../../design/DesignOptionType'

const pmData = () => PMData.Instance;

class ProjectCompView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.get()
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    if (pmData().DesignOption === dot.web) {
      var data = this.state;
      const id = this.props.match.params.id;
      data.id = id;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.ProjectVM ? data.ViewElement : ''}
      </>
    );
  }
}

// export default withRouter(observer(ProjectCompView));
export default observer(ProjectCompView)