import { ReplaceRow_Value } from "./ReplaceRow_Value"
import MassRow from '../../../../row/base/MassRow'
import { PMData } from "../../../../../../../single/model/PMData";

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  if (data[0] instanceof MassRow) {
    qI.RowValue = data
    var kv = qI.KeyValue
  } else {
    qI.KeyValue = data;
  }

  return qI;
}

export const get = (dict) => {
  var qI = new ReplaceRow_Value();
  qI.Dict = dict;
  return qI;
}

export const Clone = (q) => {
  var qI = new ReplaceRow_Value();

  qI.KeyValue = q.KeyValue;
  qI.Dict = q.Dict;

  return qI;
}

// Property
export const GetKeyValue = (q) => {
  return q.RowValue.map(x => x.RetrieveData);
}

export const GetRowValue = (q) => {
  return q.KeyValue.map(x => pmData().Current_MassTable.TBody.GetRow(x));
}