import * as mif from '../../../field/MassItemField'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as commandDictUtil from '../../command/dict/CommandDictUtil'
import * as saveValueUtil from '../../savevalue/base/SaveValueUtil'
import * as buildUtil from '../build/base/BulidUtil'

// Property
export const GetMassGroupName = (q) => {
  return q.MassGroupSetting ? q.MassGroupSetting.Name
    : '';
}

export const GetAllRows = (q) => {
  var list = [];
  q.MainRows.forEach(x => {
    list.push.apply(list, x.AllItems)
  });
  return list;
}

// columnfield
export const GetColumnField = (q) => {
  var qI = q.Dict.Dict.IsHavePhase ? mif.Phase : mif.Project
  return qI;
}

// backup
export const GetCommandDict = (q) => {
  return commandDictUtil.get(q)
}

// savevalue
export const GetSaveValue = (q) => {
  return saveValueUtil.get(q)
}

// build
export const GetBuildData = (q) => {
  return buildUtil.get(q);
}