import * as sn from './MCU_SectionName'
import * as u_dt from '../../../../update/Update_DirectionType'
import { message } from 'antd'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as hUtil from '../base/MassCellUtil'

export class MC_Update {
  Update_SubCells = (data, dirType, mainHandle) => {
    var q = this.Host;
    q.SubCells = hUtil.Get_SubCells(q);
    this.Update_TotalValueData(undefined, dirType, mainHandle)
  }

  Update_TotalValueData = (data, dirType, mainHandle) => {
    // hUtil.ReHandle_TotalValueData(this.Host);
    this.Host.ValueData.RefreshData(mainHandle);
  }

  Update_Unit = (data, dirType, mainHandle) => {
    var host = this.Host;
    this.Update_ValueCurrent(undefined, dirType, mainHandle);
  }

  Update_ValueCurrent = (data, dirType, mainHandle) => {
    var host = this.Host
    var valueData = host.ValueData
    var unit = host.Unit

    const setObj = () => {
      valueData.CurrentItem =
        genUtil.SingleFilter(valueData.Data,
          (x) => x.Unit === unit)

      if (mainHandle){
        mainHandle();
      }
    }

    if (valueData.Data) {
      setObj()
    } else {
      genUtil.Add_2(
        valueData.OnGet_Data_Func,
        setObj
      )
    }
  }

  Run = (sections, dirType = u_dt.none, isHostApplied = true, mainHandle) => {
    if (!Array.isArray(sections)) {
      sections = [sections]
    }

    if (!dirType){
      dirType = u_dt.none
    }

    if (isHostApplied === undefined){
      isHostApplied = true;
    }

    // handle
    const finish = () => {
      if (mainHandle){
        mainHandle();
      }
    }

    var segment_incre = genUtil.IncreFunc(2, finish)

    if (isHostApplied) {
      var s_incre = genUtil.IncreFunc(sections.length, segment_incre)

      sections.forEach(x => {
        this.RunItem(x, dirType, s_incre)
      })
    } else {
      segment_incre();
    }

    switch (dirType) {
      case u_dt.none:
        segment_incre();
        break;
      case u_dt.lower:
        var subItems = this.SubItems;
        var si_incre = genUtil.IncreFunc(subItems.length, segment_incre)

        subItems.forEach(x => {
          x.Update.Run(sections, dirType, si_incre)
        })
        break;
      case u_dt.upper:
        this.SupItem?.Update.Run(sections, dirType, segment_incre)
        break;
      case u_dt.both:
        var seg2_incre = genUtil.IncreFunc(2, segment_incre)

        var subItems = this.SubItems;
        var si_incre = genUtil.IncreFunc(subItems.length, seg2_incre)

        // Update Sub trc sau  n Sub
        subItems.forEach(x => {
          x.Update.Run(sections, u_dt.lower)
        })
        this.SupItem?.Update.Run(sections, u_dt.upper, seg2_incre)
        break;
    }
  }

  RunItem = (secData, dirType, mainHandle) => {
    var section = typeof secData === 'string' ?
      secData : secData.Section
    var data = secData.Data

    switch (section) {
      case sn.subcells:
        this.Update_SubCells(data, dirType, mainHandle);
        break;
      case sn.valuelist:
        this.Update_TotalValueData(data, dirType, mainHandle);
        break;
      case sn.unit:
        this.Update_Unit(data, dirType, mainHandle);
        break;
      case sn.valuecurrent:
        this.Update_ValueCurrent(data, dirType, mainHandle);
        break;
    }
  }
}