import * as genUtil from '../../../../../../../../util/generalUtil'
import { Bulid } from "./Build"
import * as bycellUtil from '../bycell/ItemUtil'
import * as byitemUtil from '../byitem/base/ItemUtil'
import * as mif from '../../../../field/MassItemField'
import { PMData } from '../../../../../../single/model/PMData'

import * as u_sn from '../../../row/update/MRU_SectionName'
import * as uc_sn from '../../../cell/update/MCU_SectionName'
import * as u_dt from '../../../../../update/Update_DirectionType'

import * as massItemUtil from '../../../../item/base/MassItemUtil'
import * as reportUtil from '../../../../report/ReportUtil'

import * as mc_ct from '../../../cell/base/MassCell_CreateType'

const pmData = () => PMData.Instance;

// Instance
export const get = (q) => {
  var qI = new Bulid();
  qI.Table = q;
  return qI;
}

// Property
export const GetByCell = (q) => {
  return bycellUtil.get(q)
}

export const GetByItem = (q) => {
  return byitemUtil.get(q)
}

// Method
export const Run = (q) => {
  var table = q.Table;

  // Initial
  q.Initial();

  var data = q.Data;

  var length = data.length;
  var i = 0;
  const incre_func = () => {
    i++;
    if (i === length) {
      UpdateData(q, () => Run_Command(q, q.Finish))
    }
  }

  // set columns
  data.forEach(x => {
    var col = table.GetColumn(x.key);
    col.Report = reportUtil.GetFromDb(x.report);
  })

  // set rows
  data.forEach(x => {
    var col = table.GetColumn(x.key);

    if (x.cells) {
      // cells
      q.ByCell.Run(x.cells, col, incre_func)
    } else {
      const handle_masses = () => {
        // masses
        var masses = x.masses.map(x => {
          var qI = massItemUtil.get(x)
          col.MassItem_Handle(qI);
          return qI;
        });
        q.ByItem.HandleItems(
          masses, col, undefined, [mif.MassType], incre_func)
      }

      switch (x.key.field) {
        case 'project':
          if (col.Project) {
            handle_masses();
          } else {
            genUtil.Add_2(col.OnSet_Project_Func, handle_masses);
          }
          break;
        case 'phase':
          if (col.Phase) {
            handle_masses()
          } else {
            genUtil.Add_2(col.OnSet_Phase_Func, handle_masses);
          }
          break;
      }
    }
  })
}

export const Initial = (q) => {
  var qI = q.Table;

  qI.Columns = [];

  // Reset cc dng to bi ngi dng
  qI.CreatedByUser_Rows = []
  qI.Handled_CreatedByUser_Rows = []
}

const UpdateData = (q, handle) => {
  var table = q.Table;

  var rows = table.MainRows

  // Update SaveValue - MassType, Category, Level ...
  table.SaveValue.Run();

  table.TBody.Rows = rows;

  if (handle) {
    handle();
  }
}

// Run pre command
const Run_Command = (q, handle) => {
  var table = q.Table;

  var mgs = pmData().Mgs_Dict_Dict.Current_MGS
  if (mgs) {
    const func1 = () => {
      var cd = table.CommandDict
      var list = cd.PreItems = mgs.CommandData.Items.map(x => x.Clone(cd));

      var count = list.length

      const c_fin = () => {
        if (handle) {
          handle();
        }
      }

      if (count !== 0) {
        var i = 0;

        const do_command = () => {
          var ci = list[i];
          ci.Do(() => {
            i++;
            if (i === count) {
              c_fin();
            } else {
              do_command();
            }
          });
        }

        do_command();
      } else {
        c_fin();
      }
    }

    if (mgs.CommandData) {
      func1();
    } else {
      genUtil.Add_2(mgs.OnSet_CommandData_Func, func1)
    }
  } else {
    if (handle) {
      handle();
    }
  }
}

export const Finish = (q) => {
  var qI = q.Table;

  qI.Is_Render_Done = true;
}