import { makeObservable, observable, action } from 'mobx';
import * as textUtil from '../../../../../text/TextVMUtil'
import * as util from './MassItem_DictUtil'
import * as genUtil from '../../../../../../util/generalUtil'

export default class MassItem_Dict {
  constructor() {
    makeObservable(this, {
      massTable: observable,
      isDataPending: observable,
      forge: observable
    })
  }

  data
  get Data() {
    return this.data
  } set Data(v) {
    this.data = v;

    this.IsDataPending = true;
    this.MassTable = util.GetMassTable(this)
  }

  // Table
  massTable = undefined
  get MassTable() {
    return this.massTable
  } set MassTable(v) {
    this.massTable = v
  }

  // Loading
  isDataPending = false;
  get IsDataPending(){
    return this.isDataPending
  } set IsDataPending(v){
    this.isDataPending = v;
  }

  get LoadingSpinTip() {
    return `loading data ... (${(this.HandledRatio * 100).toFixed(1)}%)`;
  }

  loadingSpinTipVM
  get LoadingSpinTipVM() {
    if (!this.loadingSpinTipVM) {
      this.loadingSpinTipVM = textUtil.get(this.LoadingSpinTip);
    }
    return this.loadingSpinTipVM;
  }

  handledRatio = 0
  get HandledRatio() {
    return this.handledRatio;
  }
  set HandledRatio(v) {
    this.handledRatio = v;
    this.LoadingSpinTipVM.Content = this.LoadingSpinTip;
  }

  // Forge
  OnSet_Forge_Func = []

  forge = undefined
  get Forge() {
    if (!this.forge){
      util.Handle_Forge(this);
    } return this.forge
  } set Forge(v) {
    this.forge = v;
    
    genUtil.Handle(this.OnSet_Forge_Func,
      (v) => this.OnSet_Forge_Func = v)
  }
}