import { makeObservable, observable, action } from 'mobx';
import * as util from './ProjectVMUtil'

import * as genUtil from '../../../../../util/generalUtil'
import * as inputUtil from '../../../../../util/inputUtil'

import EditableTextView from '../../../../text/EditableTextView';
import * as checkboxUtil from '../../../../checkbox/CheckboxVMUtil';

export default class ProjectVM {
  constructor() {
    makeObservable(this, {
      typicalLevelSetting: observable,
      typicalLevelSetting_ForEdit: observable,
      commands: observable
    })
  }

  get _id() {
    return this.ModelItem._id;
  }

  get id() {
    return this.ModelItem._id;
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      this.nameVM = inputUtil.GetInput(this, 'Name', () => this.Save());
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  // Code
  get Code() {
    return this.ModelItem.Code;
  } set Code(v) {
    this.ModelItem.Code = v;
  }

  codeVM
  get CodeVM() {
    if (!this.codeVM) {
      this.codeVM = inputUtil.GetInput(this, 'Code', () => this.Save());
    }
    return this.codeVM;
  }

  get CodeView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.CodeVM} />
  }

  // fortest
  get Fortest(){
    return this.ModelItem.Fortest
  } set Fortest(v){
    this.ModelItem.Fortest = v;
  }

  fortestVM
  get FortestVM(){
    if (!this.fortestVM){
      var obj = this.fortestVM = checkboxUtil.get('', this.Fortest);

      obj.onCheckChange = (v) => {
        this.Fortest = v;
        this.Save();
      };
    } return this.fortestVM;
  }

  // Phase
  phase_DictVM
  get Phase_DictVM() {
    if (!this.phase_DictVM) {
      this.phase_DictVM = util.GetPhase_DictVM(this);
    }
    return this.phase_DictVM;
  }

  exportSet_Dict
  get ExportSet_Dict() {
    if (!this.exportSet_Dict) {
      this.exportSet_Dict = util.GetExportSet_Dict(this)
    } return this.exportSet_Dict;
  }

  get DisplayContent() {
    return `${this.Code} - ${this.Name}`;
  }

  // Old Version
  get Level_Dict() {
    return this.ModelItem.Level_Dict;
  }

  // LevelSetting
  OnSet_LevelSetting_Func_Dict = []

  levelSetting
  get LevelSetting() {
    if (!this.levelSetting) {
      util.Handle_LevelSetting(this)
    }
    return this.levelSetting;
  } set LevelSetting(v) {
    this.levelSetting = v;

    genUtil.Handle(
      this.OnSet_LevelSetting_Func_Dict,
      (v) => this.OnSet_LevelSetting_Func_Dict = v)
  }

  // TypicalLevelSetting
  typicalLevelSetting = undefined
  get TypicalLevelSetting() {
    if (!this.typicalLevelSetting) {
      this.typicalLevelSetting = util.GetTypicalLevelSetting(this);
    }
    return this.typicalLevelSetting;
  } set TypicalLevelSetting(v) {
    this.typicalLevelSetting = v;
  }

  typicalLevelSetting_ForEdit = undefined
  get TypicalLevelSetting_ForEdit() {
    if (!this.typicalLevelSetting_ForEdit) {
      this.typicalLevelSetting_ForEdit = util.GetTypicalLevelSetting_ForEdit(this);
    }
    return this.typicalLevelSetting_ForEdit;
  } set TypicalLevelSetting_ForEdit(v) {
    this.typicalLevelSetting_ForEdit = v;
  }

  get Url() {
    return this.ModelItem.Url;
  }

  // Commands
  commands = undefined
  get Commands() {
    if (!this.commands) {
      util.Handle_Commands(this)
    } return this.commands;
  } set Commands(v) {
    this.commands = v
  }

  // Row
  get RowView() {
    return util.GetRowView(this)
  }

  // Save
  Save = (handle) => {
    this.ModelItem.Save(handle);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}