import { makeObservable, observable, action } from 'mobx';
import * as util from './ExportSet_DictVMUtil'
import TableView from '../../table/TableView';
import * as iUtil from '../../item/base/ExportSetVMUtil'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pct from '../../../command/ProjectCommand'
import * as erf from '../../item/base/ExportSet_RetrieveFrom'

const pmDataVM = () => PMDataVM.Instance

export class ExportSet_DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      // Is_Modal_Visible: observable,
      currentItem: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  get ModelItems(){
    return this.Items ? this.Items.filter(x => x.RetrieveFrom === erf.model) : undefined
  }

  get InputItems(){
    return this.Items ? this.Items.filter(x => x.RetrieveFrom === erf.input) : undefined
  }

  // AddItem = (v) => {
  //   var list = this.Items;
  //   if (list.length === this.ModelItem.Items.length) return

  //   var obj = iUtil.get(v, this);
  //   list.push(obj);
  // }

  AddItem = (retrieveFrom) => {
    var item = this.ModelItem.AddItem(retrieveFrom);
    var itemVM = iUtil.get(item, this);
    
    this.Items.push(itemVM);
    return item;
  }

  // addVM
  // get AddVM() {
  //   if (!this.addVM) {
  //     this.addVM = util.GetAddVM(this)
  //   } return this.addVM;
  // }

  //
  currentItem = undefined
  get CurrentItem() {
    return this.currentItem;
  } set CurrentItem(v) {
    this.currentItem = v;
    this.ModelItem.CurrentItem = v?.ModelItem
  }

  TempCurrentItem

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get MainView() {
    return <div className='f'>
      {this.Commands.includes(pct.edit) ?
        <div className='es__add'>
          {this.AddVM.View}
        </div>
        : ''}
      <div className='es__main f1'>
        <TableView store={this} />
      </div>
    </div>
  }

  // View
  // Is_Modal_Visible = false

  // get ModalView() {
  //   return util.GetModalView(this)
  // }

  // get PickView() {
  //   return util.GetPickView(this)
  // }

  modelView
  get ModelView(){
    if (!this.modelView){
      this.modelView = util.GetModelView(this);
    } return this.modelView
  }

  inputView
  get InputView(){
    if (!this.inputView){
      this.inputView = util.GetInputView(this);
    } return this.inputView;
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}