import { PMData } from "../../../../../single/model/PMData";
import { Color } from "./Color"

import * as filterUtil from '../../../../filter/EntFilterUtil'
import * as genUtil from '../../../../../../../util/generalUtil'

const pmData = () => PMData.Instance

// instance
export const get = (dict) => {
  var qI = new Color();
  qI.Dict = dict;
  return qI;
}

// property
export const Handle_BackgroundColor = (q) => {
  if (q.IsGet_BackgroundColor) return undefined
  q.IsGet_BackgroundColor = true;

  const setObj = (vcs_item) => {
    q.BackgroundColor = vcs_item ? vcs_item.Color : '#FFFFFF';
    q.IsGet_BackgroundColor = false;
  }

  var column = q.Column
  if (column.key.field === 'project' || column.Index === 0
    || pmData().Current_MassTable.Columns.length !== 2) {
    setObj();
  } else {

  }
}

export const GetBackgroundColor = (q) => {
  return q.VCS_Item ? q.VCS_Item.Color : '#FFFFFF'
}

export const GetColor = (q) => {
  return q.VCS_Item ? '#FFFFFF' : '#1890ff'
}

export const GetVCS_Item = (q) => {
  var valueItem = q.ValueItem;
  var compareValueItem = q.Cell.Row.Cells[0].ValueData.CurrentItem

  if (!valueItem || !compareValueItem) return undefined;

  var list = pmData().VersionCompareSetting.Items;
  var ratio = valueItem.Value / compareValueItem.Value
  var item = genUtil.SingleFilter(list, x => filterUtil.IsValid(x.Filter, ratio))

  return item;
}