import * as util from './PhaseUtil'
import * as genUtil from '../../../../../util/generalUtil'
import { url } from './Phase_Url'
import { message } from 'antd'
import { PMData } from '../../../single/model/PMData'

const pmData = () => PMData.Instance

export default class Phase {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Data
  name
  get Name() {
    if (!this.name) {
      this.name = 'tn version'
    } return this.name
  } set Name(v) {
    this.name = v;
  }

  description
  get Description() {
    if (!this.description) {
      this.description = 'm t'
    } return this.description
  } set Description(v) {
    this.description = v
  }

  Is_FirstCreated = false

  // typicalLevelSetting
  OnGet_TypicalLevelSetting_Func = []

  typicalLevelSetting
  get TypicalLevelSetting() {
    if (!this.typicalLevelSetting) {
      util.Handle_TypicalLevelSetting(this)
    } return this.typicalLevelSetting
  } set TypicalLevelSetting(v) {
    this.typicalLevelSetting = v;
    genUtil.Handle(this.OnGet_TypicalLevelSetting_Func,
      (v) => this.OnGet_TypicalLevelSetting_Func = v)
  }

  // ApiData
  get ApiData() {
    var obj = {
      _id: this._id,
      projectId: pmData().CurrentProject._id,
      name: this.Name,
      desciprtion: this.Description
    }

    return obj;
  }

  // Run
  Run = (handle) => {
    util.Run(this, handle);
  }

  // Save
  Save = (handle) => {
    this.SaveDatabase(false, handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  // Delete
  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(true)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage) {
        message.success('delete successflly!');
      }
      if (handle) {
        handle()
      }
    });
  }
}