import { PMData } from "../../../../../single/model/PMData"
import { CommandData } from "./CommandData";
import { url } from './CommandData_Url'
import * as api from '../../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as commandItemUtil from '../../../../mass/table/command/item/base/CommandItemUtil'

const pmData = () => PMData.Instance

// instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  if (!data) {
    return qI;
  }

  qI._id = data._id;
  if (data.items){
    qI.Items = data.items.map(x => commandItemUtil.get(x));
  }

  return qI;
}

export const get = (dict) => {
  var qI = new CommandData();

  qI.Dict = dict

  return qI;
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

