// Method
export const Check_Remove_Rename = (q, row) => {
  var removeitems = q.RemoveItems;
  var items = q.Items;

  var fv = row.FullValue;
  // remove
  var ri = removeitems.findIndex(x => x === fv)
  if (ri !== -1) {
    row.Invalid = true;
    return;
  }

  // rename
  var index = items.findIndex(x => x.key === fv);
  if (index !== -1) {
    row.Title = items[index].title;
    var sfs = row.SubFields = items[index].subfields
    if (sfs && sfs.length !== 0) {
      row.IsPending_HaveSub = true;
    }
  }
}

export const InitialForSave = (q) => {
  var tbody = pmData().Current_MassTable.TBody;

  var cellvalue_moredata = {
    mgsId: q.Dict._id
  }

  // X l Items
  var mainrows = tbody.Rows

  // Gi nh ly t View mc nh ra
  var items = []

  const getitem = (row) => {
    // Lu thng tin Row
    var item = genUtil.SingleFilter(items, x => x.key === row.FullValue);
    if (!item) {
      item = {
        key: row.FullValue
      }
      items.push(item);
    }

    item.title = row.Title;
    item.subfields = row.SubFields;

    // Lu thng tin CellValue
    var cell = row.Cells[0];
    if (cell._id) {
      var cellValue = row.Cells[0].ValueData
      cellValue.Save(cellvalue_moredata);
    }

    row.SubItems.forEach(x => getitem(x))
  }

  mainrows.forEach(x => getitem(x));
  q.Items = items;

  // X l RemoveItems
  var removerows = tbody.RemoveRows

  // Gi nh ly t View mc nh ra
  var removeItems = [];

  const getremoveitem = (row) => {
    var item = genUtil.SingleFilter(removeItems, x => x === row.FullValue);
    if (!item) {
      removeItems.push(row.FullValue)
    }
  }

  removerows.forEach(x => getremoveitem(x))
  q.RemoveItems = removeItems;
}