import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../util/generalUtil'
import ItemView from "./ItemView"

export class Item {
  constructor() {
    makeObservable(this, {
      Items: observable,
      Is_Sub_Visible: observable
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Dict

  Func

  // Sub
  Items = undefined

  get IsHaveSub() {
    return this.Items && this.Items.length !== 0
  }

  Is_Sub_Visible = false

  // Rank
  get Rank() {
    return this.SupItem ? this.SupItem.Rank + 1 : 0
  }

  OnClick = () => {
    if (this.IsHaveSub) {
      this.Is_Sub_Visible = !this.Is_Sub_Visible
    } else {
      this.Dict.CurrentItem = this;
      this.Func();
    }
  }

  Active = () => {
    this.Dict.CurrentItem = this;
  }

  get ActiveCss() {
    return this._id === this.Dict.CurrentItem?._id ? 'active' : ''
  }

  get Sub_WrapperCss() {
    var list = this.Dict.Refine_WrapperCss;
    var index = this.Rank + 1;

    return list.length > index ? list[index] : ''
  }

  get MainCss() {
    var list = this.Dict.Refine_ItemCss;
    var index = this.Rank

    return list.length > index ? list[index] : ''
  }

  get View() {
    return <ItemView key={this.id} store={this} />
  }

  get MainView() {
    return <div onClick={this.OnClick}
      className={`nav__item f ${this.MainCss} ${this.ActiveCss}`}>
      <span>
        {this.Title}
      </span>
      {this.IsHaveSub ?
        <div className='nav_item__togglesub'>
          {this.Is_Sub_Visible ?
            <CaretDownOutlined />
            :
            <CaretUpOutlined />
          }
        </div>
        : ''}
    </div>
  }
}