import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { ValueInfoVM } from './ValueInfoVM';
import * as mte_t from './VI_RetrieveType'

class MassTypeExportView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ValueInfoVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='b-s1 b-r6 p-3 mb-10'>
          <div className='f mes__cell__buttonwrapper'>
            <div className='f1' />
            {data.DeleteVM.View}
          </div>

          <table className='f1'>
            <tbody>
              <tr>
                <td className='title large'>Cch ly gi tr:</td>
                <td>{data.RetrieveTypes_SelectVM.View}</td>
              </tr>

              {data.RetrieveTypes && (data.RetrieveTypes.includes(mte_t.byparameter) ||
                data.RetrieveTypes.includes(mte_t.bymaterialparameter)) ?
                <tr>
                  <td className='title'>Parameter:</td>
                  <td>{data.Parameter_SelectVM.View}</td>
                </tr>
                : ''}

              {data.RetrieveTypes && data.RetrieveTypes.includes(mte_t.bysumparameters) ?
                <tr>
                  <td className='title'>Parameter List:</td>
                  <td>{data.ParameterList_SelectVM.View}</td>
                </tr>
                : ''}
            </tbody>
          </table>
        </div>
      </>
    );
  }
}

export default observer(MassTypeExportView);
