import { makeObservable, observable, action } from 'mobx';
import * as util from './Project_DictVMUtil'
import './Project_Dict.css'
import TableView from '../table/TableView'
import * as itemUtil from '../base/ProjectVMUtil'

import * as genUtil from '../../../../../util/generalUtil'
import * as comUtil from '../../../../../util/commandUtil'
import { Button } from 'antd';

export class Project_DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this);
    } return this.items;
  } set Items(v) {
    this.items = v;

    this.GetItemsDone = true;
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  set GetItemsDone(v) {
    if (this.OnGetItemsDone) {
      this.OnGetItemsDone(v);
    }
  }

  AddItem = () => {
    var item = this.ModelItem.AddItem();
    var itemVM = itemUtil.get(item, this);

    this.Items.push(itemVM);
    return itemVM;
  }

  // Select
  selectVM
  get SelectVM() {
    if (!this.selectVM) {
      this.selectVM = util.GetSelectVM(this)
    } return this.selectVM;
  }

  get SelectView() {
    return <div className='projectdict__select'>
      {this.SelectVM.View}
    </div>
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get View() {
    return <div className='f'>
      {comUtil.Validate('edit') ?
        <div className='es__add'>
          <Button type='primary' size='small'
            className='btn-success addproject'
            onClick={this.AddItem}>+</Button>
        </div>
        : ''}
      <div className='es__main f1'>
        <TableView store={this} />
      </div>
    </div>
  }

  // Move
  Move = (i1, i2) => {
    var list = this.Items;
    genUtil.MoveItem(list, i1, i2);

    this.ModelItem.Move(i1, i2);
  }

  Insert = (i1, i2) => {
    var list = this.Items;
    genUtil.InsertItem(list, i1, i2);

    this.ModelItem.Insert(i1, i2);
  }

  OnDragEnd = (res) => {
    if (!res.destination) return;

    var i1 = res.source.index;
    var i2 = res.destination.index;

    this.Insert(i1, i2);
  }
}