import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './ItemUtil'

export class Item {
  Dict

  get exportSetId(){
    return this.Dict.exportSetId
  }
  
  phaseId

  // Mass Multiply
  OnSet_Mass_Multiply_Func = []

  mass_Multiply
  get Mass_Multiply(){
    if (!this.mass_Multiply){
      util.Handle_Mass_Multiply(this)
    } return this.mass_Multiply
  } set Mass_Multiply(v){
    this.mass_Multiply = v;
    genUtil.Handle(this.OnSet_Mass_Multiply_Func,
      (v) => this.OnSet_Mass_Multiply_Func = v)
  }
}