import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Select } from "antd";
import TextView from "../../../../../../text/TextView";
import MassRow from "./MassRow";
import MassCellView from "../../cell/base/MassCellView";
import EditableTextView from "../../../../../../text/EditableTextView";
import './MassRow.css'
import MR_UnitView from "../unit/MR_UnitView";

const { Option } = Select;

class MassRowView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassRow();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;
    // debugger
    // var unitData = data.UnitData

    var toggleButton = data.IsHaveSub ?
      <Button size='small' style={{ marginRight: 5, minWidth: 25 }} type='primary'
        onClick={data.ToggleSub}>
        <TextView store={data.ToggleButtonContentVM} />
      </Button>
      : <div style={{ padding: '0 15.5px' }} />;

    return (
      <>
        <tr className={data.UnitData.MissingCss}>
          <th scope='row' >
            <div className='massrow__title__wrapper'
              onMouseEnter={data.ShowHover}
              onMouseLeave={data.HideHover}>
              <div className='f' style={data.Style}>
                {toggleButton}

                <div className='f1 f-ac'>
                  <span onDoubleClick={data.EditTitle}>
                    <EditableTextView store={data.Editable_TitleVM} />
                  </span>

                  {data.IsHave_Addition_Wrapper ?
                    <div className='massrow__addition__wrapper'>
                      {data.ReplaceInformation_Element}
                      {data.RawValue_Element}
                    </div>
                    : ''}
                </div>

                <div className='massrow__hover__wrapper'>
                  {data.HoverData.View}
                  {data.ModalElement}
                </div>
              </div>
            </div>
          </th>

          {/* Cells */}
          {data.Cells.map(x => {
            return <MassCellView key={x.id} store={x} />
          })}

          <MR_UnitView store={data.UnitData} />
        </tr>

        {/* Th hin trang thi x l d liu */}
        {data.IsDataPending_View}

        {data.IsShowSub ? data.SubItems.map((x, i) => {
          return <MassRowView store={x} key={x.id} />
        }) : undefined}
      </>
    )
  }
}

export default observer(MassRowView)