import { makeObservable, observable, action } from 'mobx';
import * as mif from '../../../field/MassItemField'
import * as colUtil from '../../column/base/MassColumnUtil'
import * as rowUtil from '../../row/base/MassRowUtil'
import * as cellUtil from '../../cell/base/MassCellUtil'
import * as util from './MassTableUtil'
import { PMData } from '../../../../../single/model/PMData';
import { PMDataVM } from '../../../../../single/form/PMDataVM';
import dayjs from 'dayjs';
import * as eeUtil from '../../../../../../export/excel/ExcelExportUtil';
import * as wbUtil from '../../export/MT_WorkbookUtil';
import * as genUtil from '../../../../../../../util/generalUtil'
import { MassTBody } from '../../tbody/MassTBody'
import MassTableView from './MassTableView'
import MassColumn from '../../column/base/MassColumn';
import MassRow from '../../row/base/MassRow';


export const pmData = () => PMData.Instance;
export const pmDataVM = () => PMDataVM.Instance;

export default class MassTable {
  constructor() {
    makeObservable(this, {
      is_Render_Done: observable,
      Columns: observable,
      // MainRows: observable,
      // Cells: observable,
      columnField: observable,
      tbody: observable
    })
  }

  // id
  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  //columnField = mif.Project
  columnField = undefined
  get ColumnField() {
    if (!this.columnField){
      this.columnField = util.GetColumnField(this)
    } return this.columnField;
  } set ColumnField(v) {
    this.columnField = v;
  }

  Columns = undefined

  get MainRows(){
    return this.TBody.Rows;
  } set MainRows(v){
    this.TBody.Rows = v
  }

  // Cells = undefined

  get AllRows() {
    return util.GetAllRows(this);
  }

  GetColumn = (key) => {
    var list = this.Columns;
    var item = genUtil.SingleFilter(list, x => x.key.value === key.value)
    if (!item){
      item = new MassColumn();
      item.Table = this;
      item.key = key;
      list.push(item);
    }
    return item;
  }

  GetRow = (rawValue, supRow) => {
    var list = supRow ? supRow.SubItems : this.MainRows;
    var fullValue = supRow ? `${supRow.FullValue}__${rawValue}` : rawValue;
    var item = genUtil.SingleFilter(list, x => x.FullValue === fullValue);
    if (!item){
      var item = new MassRow();
      item.Original_SupRow = item.SupItem = supRow
      item.RawValue = rawValue;

      if (!supRow){
        item.Dict = this;
      }

      list.push(item);
    }
    return item;
  }

  // GetColumn = (name, data) => {
  //   var list = this.Columns;
  //   var item = undefined;
  //   var index = list.findIndex(x => x.Name === name);
  //   if (index === -1) {
  //     item = colUtil.get(name, this, data);
  //     list.push(item);
  //     return item;
  //   }
  //   else {
  //     return list[index];
  //   }
  // }

  // Data
  get Data() {
    return this.Dict.Data;
  }

  // Build
  buildData
  get BuildData() {
    if (!this.buildData) {
      this.buildData = util.GetBuildData(this)
    } return this.buildData
  }

  OnSetHandledRatio_Funcs = []

  handledRatio
  get HandledRatio() {
    return this.handledRatio;
  } set HandledRatio(v) {
    this.handledRatio = v;
    this.OnSetHandledRatio_Funcs.forEach(x => x(v));
  }

  // MassGroupSetting
  // get MassGroupSetting_DictVM() {
  //   return pmDataVM().MassGroupSetting_Dict
  // }

  // get Mgs_Dict_Dict() {
  //   return pmData().Mgs_Dict_Dict
  // }

  AddMainRow = () => {
    return rowUtil.Add(undefined, this);
  }

  // Excel Export
  get ExportTitle() {
    var text = pmData().CurrentProject?.Name;
    text = text ? text + '_' : '';

    if (this.ColumnField === mif.Phase) {
      text += 'phase_'
    }

    const formatedDate = dayjs(Date.now()).format("YYMMDD");
    text += `masstable_${formatedDate}`
    return text;
  }

  get ExcelWorkbook() {
    return wbUtil.get(this).ResultData;
  }

  excelExport
  get ExcelExport() {
    if (!this.excelExport) {
      this.excelExport = eeUtil.get(
        undefined, undefined,
        () => this.ExportTitle,
        () => this.ExcelWorkbook);
    }
    return this.excelExport;
  }

  Export = () => {
    this.ExcelExport.Export();
  }

  // Lu tr cc Row to bi ngi dng
  // Trc mt dng  x l li khng update TotalValue
  // trong ln u tin truy xut bng khi lng
  CreatedByUser_Rows
  Handled_CreatedByUser_Rows

  Add_Handled_CreatedByUser_Row = (row) => {
    this.Handled_CreatedByUser_Rows.push(row);

    if (this.CreatedByUser_Rows.length === this.Handled_CreatedByUser_Rows.length) {
      // X l TotalValue thnh cng vi tt c cc CreatedByUser_Row
    }
  }

  // Body
  tbody = undefined
  get TBody() {
    if (!this.tbody) {
      var obj = this.tbody = new MassTBody();
      obj.Dict = this
    } return this.tbody
  }

  // Backup
  commandDict
  get CommandDict() {
    if (!this.commandDict) {
      this.commandDict = util.GetCommandDict(this)
    } return this.commandDict;
  }

  // SaveValue
  saveValue
  get SaveValue() {
    if (!this.saveValue) {
      this.saveValue = util.GetSaveValue(this)
    } return this.saveValue;
  }

  // Render
  is_Render_Done = undefined
  get Is_Render_Done(){
    return this.is_Render_Done
  } set Is_Render_Done(v){
    this.is_Render_Done = v;
    this.Dict.IsDataPending = false;
  }

  // View
  get View() {
    return <MassTableView key={this.id} store={this} />
  }
}