import { SelectVM } from '../../../../../../../select/SelectVM';
import { PMDataVM } from '../../../../../../single/form/PMDataVM';
import { MassTypeExportVM } from './MassTypeExportVM'
import * as mte_t from './MTE_Type'
import * as genUtil from '../../../../../../../../util/generalUtil'

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new MassTypeExportVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetExportType_SelectVM = (q) => {
  var qI = new SelectVM();

  qI.Items = mte_t.GetList();
  qI.InitialItem = qI.SelectedItem = q.ExportType

  qI.OnSelectedItemChanged = (v) => q.ExportType = v;

  return qI;
}

export const GetMassType_SelectVM = (q) => {
  var qI = new SelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    var massType = q.MassType;
    if (massType) {
      qI.InitialItem = qI.SelectedItem =
        genUtil.SingleFilter(list, (x) => x.Name === massType);
    }
  }

  var host = pmDataVM().MassType_Dict.ListData;
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }

  qI.CanClear = true;
  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.MassType = v?.Name;

  return qI;
}