import { message } from 'antd';
import * as mif from '../../../../field/MassItemField'
import { url } from './SubGroup_Url'
import * as genUtil from '../../../../../../../../util/generalUtil'
import * as util from './SubGroupUtil'
import { PMData } from '../../../../../../single/model/PMData';

const pmData = () => PMData.Instance;

export class SubGroup {
  Dict

  get Cell() {
    return this.Dict;
  }

  get Row() {
    return this.Cell.Row;
  }

  get Column() {
    return this.Cell.Column
  }

  get SubField() {
    return this.Row.SubFields[0]
  }

  item
  get Item() {
    if (!this.item) {
      this.item = util.GetItem(this)
    } return this.item;
  }

  get ApiData() {
    var obj = {
      cellId: this.Cell._id,
      // reportId : pmData().MassReport._id,
      reportId: this.Column.Report._id,
      subfield: this.SubField
    }

    return obj;
  }

  Save = (handle) => {
    var isValidSubGroup = true

    // have sub
    var row = this.Row;
    if (!row.IsHaveSub) {
      isValidSubGroup = false;
    }

    // valid field
    switch (this.SubField) {
      case mif.MassType:
      case mif.workItem:
      case mif.level:

        break;
      default:
        isValidSubGroup = false;
        break;
    }

    if (!isValidSubGroup) {
      if (handle) {
        handle();
      }
      return;
    }

    this._id = undefined
    const func = () => util.Save(this, handle)

    if (this.Cell.HaveValue_SubCells){
      func();
    } else {
      genUtil.Add_2(this.Cell.OnSet_HaveValue_SubCells_Func,
        func)
    }
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }

        if (handle) {
          handle();
        }
      });
  }
}