import * as util from './CommandDictUtil'
import { makeObservable, observable, action } from 'mobx';
import * as itemUtil from '../item/base/CommandItemUtil'
import { PMData } from '../../../../../single/model/PMData';
import * as tct from '../item/base/CommandType'

const pmData = () => PMData.Instance

export class CommandDict {
  constructor() {
    makeObservable(this, {
      ItemsForUndo: observable,
    })
  }

  Dict

  get Table() {
    return pmData().Current_MassTable
  }

  PreItems = []

  Items = []

  ItemsForUndo = []

  get AllItems() {
    return this.PreItems.concat(this.Items);
  }

  MaxLength = 15

  Do = (data, target) => {
    var qI = this.Add(data, target);
    qI.Do();
  }

  Add = (data, target) => {
    var qI = itemUtil.get(data, target, this);

    var list = this.Items;
    list.push(qI)

    var undoList = this.ItemsForUndo;
    if (undoList.length === this.MaxLength) {
      undoList.splice(0, 1);
    }
    undoList.push(qI);

    return qI;
  }

  Undo = () => {
    var list = this.Items;
    var undoList = this.ItemsForUndo;

    if (undoList.length === 0) return;
    var q = list.splice(list.length - 1, 1)[0];
    undoList.splice(undoList.length - 1, 1);
    q.Undo();
  }

  get ButtonView() {
    return util.GetButtonView(this)
  }

  get Disabled() {
    return this.ItemsForUndo.length === 0
  }

  // Clear = () => {
  //   this.Items = []
  // }

  get ApiData() {
    return this.Items.map(x => x.ApiData);
  }
}