import { Dict } from "./Dict"
import * as iUtil from '../item/ItemUtil'
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (dict) => {
  var qI = new Dict();

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    // u tin data c projectId
    q.Items = data.map(x => iUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  const getApi = () => {
    var apiData = {
      
    }

    iUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}

// Method
export const GetItemById = (q, id, handle) => {
  const getObj = () => {
    var list = q.Items;
    var item = genUtil.SingleFilter(list, x => x._id === id);
    handle(item);
  }

  if (q.Items){
    getObj();
  } else {
    genUtil.Add_2(q.OnSet_Items_Func,
      getObj);
  }
}