import { message } from 'antd';
import { url } from './ExportSet_Url'
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './ExportSetUtil'
import * as rf from './ExportSet_RetrieveFrom'
import * as report_statusUtil from '../../../mass/report/status/ReportStatusUtil'

export class ExportSet {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Data
  Name

  Description

  massTypeId

  RetrieveFrom

  mass_Multiply
  get Mass_Multiply(){
    if (!this.mass_Multiply){
      this.mass_Multiply = 1;
    } return this.mass_Multiply
  } set Mass_Multiply(v){
    this.mass_Multiply = v;
  }

  // Project
  get Project(){
    return this.Dict.Project;
  }

  phaseDict
  get PhaseDict(){
    if (!this.phaseDict){
      this.phaseDict = util.GetPhaseDict(this);
    } return this.phaseDict;
  }

  get ApiData() {
    var dict = this.Dict;

    var obj = {
      _id: this._id,
      projectId: dict.Project?._id,
      name: this.Name,
      description: this.Description,
      // massType : this.MassType,
      massTypeId : this.massTypeId,
      retrieveFrom : this.RetrieveFrom,
      mass_Multiply : this.Mass_Multiply
    }

    return obj;
  }

  Save = (handle) => {
    this.SaveDatabase(false, handle);

    if (this.Old_MassMultiply !== this.Mass_Multiply){
      this.Old_MassMultiply = this.Mass_Multiply
      report_statusUtil.Update();
    }
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(true)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage){
        message.success('delete successflly!');
      }
      if (handle){
        handle()
      }
    });
  }

  // Version
  NewVersion = (handle) => {
    return util.NewVersion(this, handle)
  }

  // Save MassItems
  SaveMassItems = () => {
    util.SaveMassItems(this)
  }
  
  // MassItem
  OnSet_MassItems_Func = []

  massItems
  get MassItems(){
    if (!this.massItems){
      util.Handle_MassItems(this)
    } return this.massItems
  } set MassItems(v){
    this.massItems = v;

    genUtil.Handle(this.OnSet_MassItems_Func,
      (v) => this.OnSet_MassItems_Func = v);
  }
}