import {Item} from './Item'
import {url} from './Item_Url'

import * as api from '../../../../../../../util/apiUtil'

// instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  qI._id = data._id
  qI.phaseId = data.phaseIds[0]
  qI.Mass_Multiply = data.mass_Multiply

  return qI
}

export const get = (dict) => {
  var qI = new Item();
  
  qI.Dict = dict;

  return qI;
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

// property
export const Handle_Mass_Multiply = (q) => {
  if (q.IsGet_Mass_Multiply) return undefined;
  q.IsGet_Mass_Multiply = true;

  const setObj = (data) => {
    q.Mass_Multiply = data.mass_Multiply;
    q.IsGet_Mass_Multiply = false
  }

  var apiData = {
    exportSetId : q.exportSetId,
    phaseId : q.phaseId
  }

  GetData(apiData, (data) => {
    setObj(data[0])
  })
}