import { message } from 'antd';
// import { url } from './PageItem_Url'
import * as util from './ValueInfoUtil'
import * as vi_rt from './VI_RetrieveType'
import * as genUtil from '../../../../../../../../util/generalUtil'

export class ValueInfo {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  RetrieveTypes = [vi_rt.byparameter]

  Parameter

  ParameterList = []

  get ApiData() {
    var obj = {
      valueRetrieveTypes: this.RetrieveTypes
    }

    if (this.RetrieveTypes.includes(vi_rt.bymaterialparameter) ||
      this.RetrieveTypes.includes(vi_rt.byparameter)) {
        obj.parameter = this.Parameter;
    }
    if (this.RetrieveTypes.includes(vi_rt.bysumparameters)){
      obj.parameterList = this.ParameterList;
    }

    return obj;
  }

  // Index
  get CurrentList() {
    return this.Dict.ValueInfos
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    // this.DeleteDatabase(true)
  }
}