import { PMData } from '../../../../../single/model/PMData'
import * as util from './SaveValueUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as mc_ct from '../../cell/base/MassCell_CreateType'

const pmData = () => PMData.Instance;

export class SaveValue {
  Table

  Run = () => {
    const func = () => {
      var columns = this.Table.Columns
      columns.forEach(col => {
        col.Report.Update();
      })
    }

    const incre_func = genUtil.IncreFunc(1, () => {
      func();
    })

    this.Save_MainRows(incre_func);
  }

  Save_MainRows = (mainHandle) => {
    var rows = this.Table.MainRows
    var row_incre = genUtil.IncreFunc(rows.length, mainHandle);

    // Foreach Row
    rows.forEach(row => {
      var cells = row.Cells;
      var cell_incre = genUtil.IncreFunc(cells.length, row_incre);

      // Foreach cells
      cells.forEach(cell => {
        const save = () => {
          cell.Save(cell_incre);
          // cell_incre();
        }

        if (cell.CreateType === mc_ct.bycell) {
          cell_incre();
        } else {
          var valueData = cell.ValueData;
          if (valueData.Data) {
            save();
          } else {
            genUtil.Add_2(valueData.OnSet_Data_Func,
              save);
          }
        }
      })
    })
  }
}