// MassType
export const Handle_MassType = (q) => {
  if (q.IsGet_MassType) return undefined;
  q.IsGet_MassType = true;


  const setObj = (data) => {
    var list = data;

    var massTypeId = q.massTypeId;
    if (massTypeId) {
      q.MassType = genUtil.SingleFilter(list, (x) => x.id === massTypeId);
    } else {
      q.MassType = list[0];
    }

    q.IsGet_MassType = false;
  }

  var host = pmDataVM().MassType_Dict.ListData;
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }
}

export const GetMassType_Select = (q) => {
  var qI = new SelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    const setObj2 = () => {
      qI.InitialItem = q.MassType;
    }

    if (q.MassType) {
      setObj2();
    } else {
      genUtil.Add_2(q.OnSet_MassType_Func,
        setObj2)
    }
  }

  var host = pmDataVM().MassType_Dict.ListData;
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }

  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.TempMassType = v;

  qI.OnSetOpen = (v) => {
    if (!v) {
      q.MassType_EditOK();
    }
  }

  return qI;
}

// View
export const GetRowView = (q) => {
  return <tr className={`es__row ${q.ActiveStyle}`}
    onMouseEnter={q.OnMouseEnter}
    onMouseLeave={q.OnMouseLeave}>

    <td
      onMouseEnter={
        () => inputUtil.Edit(q.NameVM, q.CheckCommand)}
      onMouseLeave={
        () => inputUtil.EditOK(q.NameVM)
      }
    >
      {q.NameWrapper_View}</td>

    {/* Input or Model */}
    {q.RetrieveFrom === erf.input ?
      <>
        <td
          onMouseEnter={q.MassType_Edit}
          onMouseLeave={q.MassType_EditOK}
        >{q.MassType_View}</td>
        <td
          onMouseEnter={
            () => inputUtil.Edit(q.DescriptionVM, q.CheckCommand)}
          onMouseLeave={
            () => inputUtil.EditOK(q.DescriptionVM)}
        >{q.DescriptionWrapper_View}</td>
      </>
      : <td>
        {q.InfoView}
      </td>
    }

    {/* Commands */}
    <td>
      <div className='f'>
        {q.RetrieveFrom === erf.input ?
          q.ActiveTool.View : ''}
        {comUtil.Validate(pct.edit) ? q.DeleteVM.View : ''}
      </div>
    </td>
  </tr>
}

export const GetInfoView = (q) => {
  return <table className='w-100p es__info__table'>
    <tbody>
      <tr>
        <th>M t:</th>
        <td
          onMouseEnter={
            () => inputUtil.Edit(q.DescriptionVM, q.CheckCommand)}
          onMouseLeave={
            () => inputUtil.EditOK(q.DescriptionVM)}
        >{q.DescriptionView}
        </td>

      </tr><tr>
        <th>Nhn h s:</th>
        <td
          onMouseEnter={
            () => inputUtil.Edit(q.Mass_MultiplyVM, q.CheckCommand)}
          onMouseLeave={
            () => inputUtil.EditOK(q.Mass_MultiplyVM)}
        >{q.Mass_MultiplyVM.View}
        </td>
      </tr>
    </tbody>
  </table>
}