import { makeObservable, observable, action } from 'mobx';
import { PMData } from '../model/PMData';
import * as util from './PMDataVMUtil'
import * as pvt from '../../entity/project/base/ProjectViewType'
import * as genUtil from '../../../../util/generalUtil'

export class PMDataVM {
  constructor() {
    makeObservable(this, {
      projectViewType: observable,
      currentProject: observable,
      main_Classname: observable,
      massGroupSetting_Dict: observable,
      commands: observable,
      adminCommands: observable
    })
  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = new PMDataVM();
    }
    return this.instance;
  }

  get pmData() {
    return PMData.Instance;
  }

  // Project
  project_DictVM
  get Project_DictVM() {
    if (!this.project_DictVM) {
      this.project_DictVM = util.GetProject_DictVM();
    }
    return this.project_DictVM;
  }

  OnSet_ProjectViewType_Func_Dict = []

  projectViewType = pvt.mass
  //projectViewType = pvt.document
  get ProjectViewType() {
    return this.projectViewType;
  } set ProjectViewType(v) {
    this.projectViewType = v;

    this.OnSet_ProjectViewType_Func_Dict.forEach(x => x.Value());
  }

  OnSet_CurrentProject_Func_Dict = []

  currentProject = undefined
  get CurrentProject() {
    return this.currentProject;
  } set CurrentProject(v) {
    this.currentProject = v;

    this.OnSet_CurrentProject_Func_Dict.forEach(x => x.Value());
    this.pmData.CurrentProject = v?.ModelItem

    util.Handle_SG_Dict_Dict(this)
  }

  projectComp
  get ProjectComp(){
    if (!this.projectComp){
      this.projectComp = util.GetProjectComp(this)
    } return this.projectComp
  }

  // MassResult
  massResultCompVM
  get MassResultCompVM() {
    if (!this.massResultCompVM) {
      this.massResultCompVM = util.GetMassResultCompVM();
    }
    return this.massResultCompVM;
  }
  set MassResultCompVM(v) {
    this.massResultCompVM = v;
    if (!v) {
      this.pmData.MassResultComp = v;
    }
    else {
      this.pmData.MassResultComp = v.ModelItem;
    }
  }

  // MassGroup
  mgs_Dict_Dict
  get Mgs_Dict_Dict() {
    if (!this.mgs_Dict_Dict) {
      this.mgs_Dict_Dict = util.GetMgs_Dict_Dict(this)
    } return this.mgs_Dict_Dict
  }

  // MassDetail
  mds_Dict_Dict
  get Mds_Dict_Dict() {
    if (!this.mds_Dict_Dict) {
      this.mds_Dict_Dict = util.GetMds_Dict_Dict(this)
    } return this.mds_Dict_Dict
  }

  // SiteSuggest
  sg_Dict_Dict
  get SG_Dict_Dict() {
    if (!this.sg_Dict_Dict) {
      util.Handle_SG_Dict_Dict(this)
    } return this.sg_Dict_Dict
  } set SG_Dict_Dict(v) {
    this.sg_Dict_Dict = v
  }

  // MassGroupSetting
  massGroupSetting_Dict = undefined
  get MassGroupSetting_Dict() {
    if (!this.massGroupSetting_Dict) {
      this.massGroupSetting_Dict = util.GetMassGroupSetting_Dict(this)
    } return this.massGroupSetting_Dict
  }

  get UpdatedDate() {
    return this.pmData.UpdatedDate;
  }

  // Classname scale b rng d n cho ph hp
  // View BoQ : 800px
  // View Detail : 100%
  main_Classname = 'projectcomp__main'
  get Main_Classname() {
    return this.main_Classname;
  } set Main_Classname(v) {
    this.main_Classname = v;
  }

  // UserDict
  userDict
  get UserDict() {
    if (!this.userDict) {
      this.userDict = util.GetUserDict(this)
    } return this.userDict;
  }

  // Admin
  admin
  get Admin() {
    if (!this.admin) {
      this.admin = util.GetAdmin(this)
    } return this.admin
  }

  // Commands
  commands = undefined
  get Commands() {
    if (!this.commands) {
      util.Handle_Commands(this)
    } return this.commands;
  } set Commands(v) {
    this.commands = v
  }

  adminCommands = undefined
  get AdminCommands() {
    if (!this.adminCommands) {
      util.Handle_AdminCommands(this)
    } return this.adminCommands;
  } set AdminCommands(v) {
    this.adminCommands = v
  }

  // MassType
  massType_Dict
  get MassType_Dict(){
    if (!this.massType_Dict){
      this.massType_Dict = util.GetMassType_Dict(this);
    }return this.massType_Dict;
  }

  // Category
  category_Dict
  get Category_Dict(){
    if (!this.category_Dict){
      this.category_Dict = util.GetCategory_Dict(this);
    }return this.category_Dict;
  }

  // Parameter
  parameter_Dict
  get Parameter_Dict(){
    if (!this.parameter_Dict){
      this.parameter_Dict = util.GetParameter_Dict(this);
    }return this.parameter_Dict;
  }

  // MassExport
  massExport_Dict
  get MassExport_Dict(){
    if (!this.massExport_Dict){
      this.massExport_Dict = util.GetMassExport_Dict(this);
    }return this.massExport_Dict;
  }

  // DesignOption
  get DesignOption(){
    return this.pmData.DesignOption
  }

  // View
  UnAuthorized_View =
    <span className='unauthorize'>Bn khng c quyn truy cp View hin ti!</span>

  get HomeView() {
    return util.GetHomeView(this)
  }

  get View(){
    return util.GetView(this)
  }
}