import { makeObservable, observable, action } from 'mobx';
import * as util from './PhaseVMUtil'

import * as genUtil from '../../../../../util/generalUtil'
import * as inputUtil from '../../../../../util/inputUtil'

import EditableTextView from '../../../../text/EditableTextView';
import { message } from 'antd';

export default class PhaseVM {
  constructor() {
    makeObservable(this, {
      is_FirstCreated : observable
    })
  }

  get _id() {
    return this.ModelItem._id;
  }

  get id() {
    return this.ModelItem.id;
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      this.nameVM = inputUtil.GetInput(this, 'Name', () => this.Save());
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  // Code
  get Description() {
    return this.ModelItem.Description;
  } set Description(v) {
    this.ModelItem.Description = v;
  }

  descriptionVM
  get DescriptionVM() {
    if (!this.descriptionVM) {
      this.descriptionVM = inputUtil.GetInput(this, 'Description', () => this.Save());
    }
    return this.descriptionVM;
  }

  get DescriptionView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.DescriptionVM} />
  }

  is_FirstCreated = undefined
  get Is_FirstCreated() {
    if (this.is_FirstCreated === undefined) {
      this.is_FirstCreated = this.ModelItem.Is_FirstCreated
    } return this.is_FirstCreated
  } set Is_FirstCreated(v) {
    this.is_FirstCreated = v;
    this.ModelItem.Is_FirstCreated = v
  }

  // Row
  get RowView() {
    return util.GetRowView(this)
  }

  // Run
  Run = () => {
    this.ModelItem.Run(() => {
      this.Is_FirstCreated = false;
      message.success('save successfully!');
    });
  }

  // Save
  Save = (handle) => {
    // to ln u
    if (this.Is_FirstCreated) {
      if (handle) {
        handle();
      }
      return;
    }
    this.ModelItem.Save(handle);
  }


  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}