import { EntDelete } from '../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import * as dt from './MR_DeleteType'
import * as u_dt from '../../../../update/Update_DirectionType'
import * as u_sn from '../update/MRU_SectionName'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import { PMData } from '../../../../../single/model/PMData';

export const pmData = () => PMData.Instance;

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.OnRun = () => {
    q.DeleteHost();
  }

  return qI;
}

// Method
// Hm x l theo lu vt Report (New)
export const Run = (q, data, handleUndo) => {
  // undo
  undoFuncs = undefined
  if (handleUndo) {
    var undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x())
    });
  }


  var deleteType = data.DeleteType
  var row = q.Host
  var table = row.Table

  var supItem = row.SupItem;
  var list = row.CurrentList;

  // remove row ch ko phi replace
  if (deleteType === dt.removedata) {
    // Add Table_TBody_RemoveRows
    var removeRows = table.TBody.RemoveRows;

    // Undo
    var index = removeRows.length;
    if (handleUndo) {
      undoFuncs.push(() => {
        removeRows.splice(index, 1)
      });
    }

    // Do
    removeRows.push(row);
  }

  if (supItem && list.length === 1) {
    data.IsContinue = true
  } else {
    data.IsContinue = false

    var index = row.Index

    // Undo
    if (handleUndo) {
      undoFuncs.push(() => {
        list.splice(index, 0, row)
      });
    }
    // Do
    list.splice(index, 1);

    if (supItem) {
      // Undo
      if (handleUndo) {
        undoFuncs.push(() => {
          supItem.Update.Run(
            [
              { Section: u_sn.celldata, Data: { Sections: uc_sn.subcells } },
              u_sn.unitlist
            ],
            u_dt.upper
          )
        })

        // Thc hin undo
        supItem.UnitData.SetUndo((undo) => {
          undoFuncs.push(undo);
        });
      }
      // Do
      supItem.Update.Run(
        [
          { Section: u_sn.celldata, Data: { Sections: uc_sn.subcells } },
          u_sn.unitlist, u_sn.unit],
        u_dt.upper
      )
    }
  }
}