import { message } from 'antd';
import { url } from './MassGroupSetting_Url'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './MassGroupSettingUtil'

export class MassGroupSetting {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name

  Description

  // // CellName
  // OnSet_CellData_Func = []

  // cellData
  // get CellData(){
  //   if (!this.cellData){
  //     util.Handle_CellData(this)
  //   } return this.cellData;
  // } set CellData(v){
  //   this.cellData = v;
  //   genUtil.Handle(this.OnSet_CellData_Func,
  //     (v) => this.OnSet_CellData_Func = v)
  // }

  // Command
  OnSet_CommandData_Func = []

  commandData
  get CommandData() {
    if (!this.commandData) {
      util.Handle_CommandData(this)
    } return this.commandData
  } set CommandData(v) {
    this.commandData = v;
    genUtil.Handle(this.OnSet_CommandData_Func,
      (v) => this.OnSet_CommandData_Func = v)
  }

  // AddTemplate
  addTemplate
  get AddTemplate() {
    if (!this.addTemplate) {
      this.addTemplate = util.GetAddTemplate(this)
    } return this.addTemplate
  }

  // UseType
  get UseType() {
    return this.Dict.UseType;
  }

  get ApiData() {
    var dict = this.Dict;

    var obj = {
      _id: this._id,
      projectId: dict.Project?._id,
      useType: dict.UseType,
      name: this.Name,
      description: this.Description
    }

    return obj;
  }

  SaveDatabase = (showMessage = true, handle, isSaveData = true) => {
    const handle_func = () => {
      if (handle) {
        handle();
      }
    }

    if (isSaveData) {
      const saveData = (handle) => {
        const save_command = (c_handle) => {
          this.CommandData.Save(c_handle);
        }

        if (this.CommandData) {
          save_command(handle);
        } else {
          genUtil.Add_2(this.OnSet_CommandData_Func,
            () => save_command(handle))
        }
      }

      if (this._id) {
        saveData(handle_func);
      } else {
        genUtil.Add_2(
          this.OnSet_id_Func_Dict,
          () => saveData(handle_func)
        )
      }
    }

    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (!isSaveData) {
          handle_func();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(true)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage) {
        message.success('delete successfully!');
      }
      if (handle) {
        handle()
      }
    });
  }
}