const Autodesk = window.Autodesk;

class SetGhostingExtension extends Autodesk.Viewing.Extension {
  constructor(viewer, options) {
    super(viewer, options);
    this._group = null;
    this._button = null;
  }

  load() {
    return true;
  }

  unload() {
    // Clean our UI elements if we added any
    if (this._group) {
      this._group.removeControl(this._button);
      if (this._group.getNumberOfControls() === 0) {
        this.viewer.toolbar.removeControl(this._group);
      }
    }
    return true;
  }

  onToolbarCreated() {
    this._group = this.viewer.toolbar.getControl(
      "nestedViewerExtensionToolbar"
    );
    if (!this._group) {
      this._group = new Autodesk.Viewing.UI.ControlGroup(
        "nestedViewerExtensionToolbar"
      );
      this.viewer.toolbar.addControl(this._group);
    }

    // Add a new button to the toolbar group
    this._button = new Autodesk.Viewing.UI.Button("setGhostingExtensionButton");
    this._button.onClick = (ev) => {
      if (!this._panel) {
        this._panel = false;
      }

      if (this._panel) {
        this._panel = false;
        this.viewer.setGhosting(false);
      } else {
        this._panel = true;
        this.viewer.setGhosting(true);
      }
    };

    this._button.setToolTip("Set Ghosting");
    this._button.addClass("setGhostingExtensionIcon");
    this._group.addControl(this._button);
  }

  static get ExtensionId() {
    return "SetGhostingExtension";
  }
}

Autodesk.Viewing.theExtensionManager.registerExtension(
  SetGhostingExtension.ExtensionId,
  SetGhostingExtension
);

export default "SetGhostingExtension";
