export const Handle_CommandData = (q) => {
  if (q.IsGet_CommandData) return undefined;
  q.IsGet_CommandData = true;

  const setObj = (data) => {
    q.CommandData = data ? 
      commandDataUtil.GetFromDb(data[0], q) :
      commandDataUtil.get(q);

    q.IsGet_CommandData = false;
  }

  if (q._id) {
    const getApi = () => {
      var apiData = {
        dictId: q._id
      }

      commandDataUtil.GetData(apiData, (data) => {
        setObj(data)
      })
    }

    getApi()
  } else {
    setObj();
  }
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}