import * as util from './Dict_DictUtil'
import * as dataItemUtil from '../../item/ItemUtil'
import { PMData } from '../../../../../single/model/PMData'
import * as genUtil from '../../../../../../../util/generalUtil'
import {url } from '../../item/Item_Url'
import { message } from 'antd'

const pmData = () => PMData.Instance

export class Dict_Dict {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  items
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this)
    } return this.items;
  }

  currentItem
  get CurrentItem(){
    return this.currentItem
  } set CurrentItem(v){
    this.currentItem = v;
  }

  CurrentField

  CurrentMassType

  data = []
  get Data(){
    return this.data;
  } set Data(v){
    this.data = v;
    if (this.OnSet_Data){
      this.OnSet_Data(v)
    }
  }

  AddData = () => {
    var qI = dataItemUtil.get(this);
    qI.MassType = this.CurrentMassType;
    qI.Field = this.CurrentField
    this.Data.push(qI)
    
    this.Items.forEach(x => {
      x.Items.push(qI)
    })

    if (this.OnAddData){
      this.OnAddData(qI)
    }
  }

  Initial = () => {
    util.Initial(this)
  }

  get ApiData() {
    var project = pmData().CurrentProject;

    var obj = {
      _id : this._id,
      projectId : project._id,
      data : this.Data.map(x => x.ApiData)
    }

    return obj;
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }
}