import { SettingVM } from "../../base/SettingVM";
import * as util from './TypicalLevelSettingVMUtil'
import './TypicalLevelSetting.css'

import LevelSettingDataView from '../../level/data/LevelSettingDataView'
import TypicalLevelSettingDataView from '../data/TypicalLevelSettingDataView';

export class TypicalLevelSettingVM extends SettingVM {
  get LevelStorageList() {
    return this.ModelItem.LevelStorageList;
  }

  get FieldFilterStorageList() {
    return this.ModelItem.FieldFilterStorageList;
  }

  Save = () => {
    this.ModelItem.Save();
  }

  // View
  // Mapping
  get MappingView() {
    return <div className='projectcomp__level'>
      <div className='projectcomp__title'>
        <h3>THIT LP MAPPING TNG</h3>
      </div>
      <LevelSettingDataView store={this.DataVM.Binding} />
    </div>
  }

  // Typical
  get TypicalView() {
    return <div className='projectcomp__typicallevel'>
      <div className='projectcomp__title'>
        <h3>THIT LP TNG IN HINH</h3>
      </div>
      <TypicalLevelSettingDataView store={this.DataVM.Typical} />
    </div>
  }
}