import * as api from '../../../../../../../util/apiUtil'
import { url } from './Item_Url'
import { Item } from './Item'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  qI._id = data._id
  qI.MassType = data.massType
  qI.Categories = data.categories

  return qI;
}

export const get = (dict) => {
  var qI = new Item();

  qI.Dict = dict;
  
  return qI;
}

// Property


// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}