import { Base } from "./Base"
import { Dict as ListData } from '../list/dict/Dict'
import { Dict as MaterialBindingData } from '../materialbinding/dict/Dict'
import { Dict as CategoryBindingData } from '../categorybinding/dict/Dict'
import { Dict as SystemBindingData } from '../systembinding/dict/Dict'

// Instance
export const get = (dict) => {
  var qI = new Base();

  return qI;
}

// Property
export const GetListData = (q) => {
  var qI = new ListData();
  qI.Dict = q;
  return qI;
}

export const GetCategoryBindingData = (q) => {
  var qI = new CategoryBindingData();
  qI.Dict = q;
  return qI;
}

export const GetMaterialBindingData = (q) => {
  var qI = new MaterialBindingData();
  qI.Dict = q;
  return qI;
}

export const GetSystemBindingData = (q) => {
  var qI = new SystemBindingData();
  qI.Dict = q;
  return qI;
}



// Method
