import * as mif from '../../../../field/MassItemField'

export const GetPropertyName_ForApi = (field) => {
  switch (field) {
    case mif.MassType:
      return 'massType'
    case mif.Project:
      return 'project'
    case mif.Phase:
      return 'phase'
    default:
      return field;
  }
}

export const GetValueFrom = (field) => {
  switch (field) {
    case mif.MassType:
      return 'MassType_Obj'
    case mif.workItem:
      return 'WorkItem_Obj'
    case mif.level:
      return 'Level_Obj'
    default:
      return undefined;
  }
}