import * as util from './FactoryUtil'

export class Factory {
  Dict

  get Row(){
    return this.Dict;
  }

  get Fields(){
    return this.Row.TempSubFields
  } set Fields(v){
    this.Row.TempSubFields = v;
  }

  Run = () => {
    util.Run(this);
  }

  MainRun = (value,  mainHandle, handleUndo) => {
    util.MainRun(this, value, mainHandle, handleUndo);
  }
}