import * as api from '../../../../../util/apiUtil'
import * as genUtil from '../../../../../util/generalUtil'
import Project from './Project';
import * as levelDictUtil from '../../level/dict/LevelDictUtil'
import * as levelTypicalUtil from '../../setting/typicallevel/base/TypicalLevelSettingUtil'
import * as levelUtil from '../../setting/level/base/LevelSettingUtil'
import * as exportSetDictUtil from '../../exportset/dict/base/ExportSet_DictUtil'
import { url } from './Project_Url'
import { url as pc_url } from './ProjectCommand_Url'
import { PMData } from '../../../single/model/PMData';

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  if (!data) return undefined;

  // var qI = new Project();
  var qI = get(dict);

  qI._id = data._id;
  qI.Name = data.name;
  qI.Code = data.code;
  qI.Fortest = data.fortest

  return qI;
}

export const get = (dict) => {
  var qI = new Project();
  qI.Dict = dict;
  return qI;
}

