export const getAll = (dict, handle) => {
  var url = "https://api.bimdev.vn/api/hbp/list";
  api.post(url, { }).then(response => response.data)
    .then(data => {
      var items = data.map(x => GetFromDb(x, dict))
      if (handle) {
        handle(items);
      }
    });
}

export const getById = (_id, handle) => {
  if (!_id) return undefined;
  var url = `https://api.bimdev.vn/api/hbp/get?id=${_id}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var item = GetFromDb(data);
      if (handle) {
        handle(item);
      }
    });
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

// Property
export const GetExportSet_Dict = (q) => {
  return exportSetDictUtil.get(q)
}

// Level
export const Handle_LevelDict = (q) => {
  if (q.IsGet_LevelDict) return undefined;
  q.IsGet_LevelDict = true;

  // const setLevelDict = (data) =>
  //   q.LevelDict = levelDictUtil.GetFromDb(data, q);

  // levelDictUtil.GetApi(q._id, setLevelDict);

  levelDictUtil.GetDefault(q, (data) => {
    q.LevelDict = data;
    q.IsGet_LevelDict = false;
  })
}

// LevelSetting
export const Handle_LevelSetting = (q) => {
  if (q.IsGet_LevelSetting) return undefined
  q.IsGet_LevelSetting = true;

  q.LevelSetting = levelUtil.GetDefault(q);
}

// TypicalLevelSetting
export const GetTypicalLevelSetting = (q) => {
  return levelTypicalUtil.GetTest(q);
}

// Commands
export const Handle_Commands = (q) => {
  if (q.IsGet_Commands) return undefined;
  q.IsGet_Commands = true;

  const setObj = (data, type) => {
    if (type === 'invalid' && q.Commands){
      return;
    }

    q.Commands = data && data.length !== 0 ? data[0].commands : []
    q.IsGet_Commands = false;
  }

  var host = pmData().UserDict.LoginUser;
  if (!host) {
    setObj(undefined, 'invalid')
  } else {
    var apiData = {
      userId: host._id,
      projectId: q._id
    }

    const apiFunc = () => api.post(`${pc_url}/list`, apiData);
    apiFunc().then(response => response.data)
      .then(data => {
        setObj(data)
      })
  }
}

