import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './ValueInfoVMUtil'
import MassTypeExportView from './ValueInfoView';

export class ValueInfoVM {
  constructor() {
    makeObservable(this, {
      retrieveTypes : observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  retrieveTypes = undefined
  get RetrieveTypes(){
    if (!this.retrieveTypes){
      this.retrieveTypes = this.ModelItem.RetrieveTypes
    }
    return this.retrieveTypes
  } set RetrieveTypes(v){
    this.retrieveTypes = v;
    this.ModelItem.RetrieveTypes = v;
  }

  retrieveTypes_SelectVM
  get RetrieveTypes_SelectVM(){
    if (!this.retrieveTypes_SelectVM){
      this.retrieveTypes_SelectVM = util.GetRetrieveTypes_SelectVM(this)
    } return this.retrieveTypes_SelectVM
  }

  get Parameter(){
    return this.ModelItem.Parameter
  } set Parameter(v){
    this.ModelItem.Parameter = v;
  }

  parameter_SelectVM
  get Parameter_SelectVM(){
    if (!this.parameter_SelectVM){
      this.parameter_SelectVM = util.GetParameter_SelectVM(this)
    } return this.parameter_SelectVM
  }

  get ParameterList(){
    return this.ModelItem.ParameterList
  } set ParameterList(v){
    this.ModelItem.ParameterList = v;
  }

  parameterList_SelectVM
  get ParameterList_SelectVM(){
    if (!this.parameterList_SelectVM){
      this.parameterList_SelectVM = util.GetParameterList_SelectVM(this)
    } return this.parameterList_SelectVM
  }

  // Index
  get CurrentList() {
    return this.Dict.ValueInfos
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // View
  get View(){
    return <MassTypeExportView key={this.id} store={this}/>
  }
}