import { System } from "./System"
import * as typeUtil from '../type/TypeUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  qI.Classification = data.classification
  qI.Types = data.types.map(x => typeUtil.GetFromDb(x, qI))
  return qI;
}

export const get = (dict) => {
  var qI = new System();
  qI.Dict = dict;

  return qI;
}

