import { message } from 'antd';
import { url } from './Item_Url'
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './ItemUtil'

export class Item {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name

  MassType

  Field

  get ApiData() {
    var obj = {
      name: this.Name,
      massType : this.MassType,
      field : this.Field
    }

    return obj;
  }

  SaveDatabase = (showMessage = true, handle) => {
    // genUtil.SaveDatabase(this, url,
    //   () => {
    //     if (showMessage) {
    //       message.success('save successfully!')
    //     }
    //     if (handle) {
    //       handle();
    //     }
    //   });
  }

  // Index
  get CurrentList() {
    return this.Dict.Data
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.Dict.Items.forEach(x => {
      x.Items.splice(x.Items.indexOf(this), 1)
    })

    
    this.DeleteDatabase(true)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    // genUtil.DeleteDatabase(this, url, () => {
    //   if (showMessage){
    //     message.success('delete successfully!');
    //   }
    //   if (handle){
    //     handle()
    //   }
    // });
  }
}