import { Value } from "./Value"
import { url } from './Value_Url'
import * as api from '../../../../../../../util/apiUtil'
import * as m_ut from '../../../unit/MassUnitType'
import { MI_ValueItem } from "../../../item/value/MI_ValueItem";
import * as genUtil from '../../../../../../../util/generalUtil'
import * as mif from '../../../field/MassItemField'

// Instance
export const get = (dict) => {
  var qI = new Value();
  qI.Dict = dict;
  return qI;
}

// Property
export const Handle_Data = (q, handle) => {
  if (q.IsGet_Data) return undefined;
  q.IsGet_Data = true;

  var host = q.Dict;

  // TotalValue Data;
  var totalVal_Data = m_ut.GetList().map(x => {
    var obj = new MI_ValueItem();
    obj.Unit = x;
    return obj;
  })

  var handledCount = 0;

  const setData = () => {
    // Lc cc Value = 0
    totalVal_Data = totalVal_Data
      .filter(x => x.Value)
    q.Data = totalVal_Data;
    q.IsGet_Data = false;

    if (handle){
      handle();
    }
  }

  var list = host.SubCells ? host.SubCells : host.Items
  if (!list) {
    if (host.Row.IsCreatedByUser) {
      // Nu Row to bi ngi dng v khng c Row con 
      setData();
      return
    }
  }

  var count = list.length
  if (count === 0) {
    setData();
    return;
  }

  const handle_Val = (valData, item) => {
    handledCount++;

    totalVal_Data.forEach((x, i) => {
      var unit = x.Unit;
      var item = genUtil.SingleFilter(valData,
        (a) => a.Unit === unit);

      if (item === undefined) {
        x.IsMissing = true;
      } else {
        x.Value += item.Value;
      }
    })

    if (handledCount === count) {
      setData();
    }
  }

  if (host.SubCells) {
    // Tn ti sub cells
    host.SubCells.forEach(x => {
      var hostValData = x.ValueData;
      if (hostValData.Data) {
        handle_Val(hostValData.Data, x)
      } else {
        genUtil.Add_2(hostValData.OnSet_Data_Func,
          () => {
            handle_Val(hostValData.Data, x)
          });
      }
    })
  } else {
    // Khng c sub cells
    host.Items.forEach(x => {
      var targetObj = x.ValueDict;

      // Kim tra value theo Phase hay theo Property khc (Project ...)
      var table = q.Dict;
      var colField = table.ColumnField;
      if (colField === mif.Phase) {
        // Khi lng theo Phase
        targetObj = x.GetPhaseItem(q.Column.Name);
      } else {
        // Khi lng theo cc thuc tnh khc
      }

      if (targetObj.FullData !== undefined) {
        handle_Val(targetObj.FullData, x)
      } else {
        genUtil.Add_2(targetObj.OnGet_FullData_Func_Dict,
          () => {
            handle_Val(targetObj.FullData, x)
          });
      }
    })
  }
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}