  IsCreatedByUser

  // Unit
  unit_SetType
  get Unit_SetType() {
    if (!this.unit_SetType) {
      this.unit_SetType = this.SubItems.length === 0
        ? u_st.upper : u_st.both
    }
    return this.unit_SetType
  } set Unit_SetType(v) {
    this.unit_SetType = v;
  }

