import { message } from 'antd';
// import { url } from './PageItem_Url'
import * as genUtil from '../../../../../../../../util/generalUtil'
import * as util from './MassInfoUtil'
import * as valueInfoUtil from '../valueinfo/ValueInfoUtil'
import * as mi_cvb from './MI_ClassifyValueBy'

export class MassInfo {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  get ApiData() {
    return {
      massType : this.MassTypeExport.ApiData,
      classifyValueBy : this.ClassifyValueBy,
      valueInfos : this.ValueInfos.map(x => x.ApiData)
    }
  }

  // Data
  massTypeExport
  get MassTypeExport(){
    if (!this.massTypeExport){
      this.massTypeExport = util.GetMassTypeExport(this)
    } return this.massTypeExport
  } set MassTypeExport(v){
    this.massTypeExport = v;
  }

  ClassifyValueBy = mi_cvb.element

  ValueInfos = []

  AddValueInfo = () => {
    var q = valueInfoUtil.get(this);
    this.ValueInfos.push(q)
    return q;
  }

  // Index
  get CurrentList() {
    return this.Dict.MassInfos
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    // this.DeleteDatabase(true)
  }
}