import { Data } from "./Data"
import * as tct from '../../base/CommandType'
import * as valueUtil from '../value/ReplaceRow_ValueUtil'
import { ReplaceRow_Value } from "../value/ReplaceRow_Value";

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  var action = qI.Action = data.action;
  qI.Target = data.target;
  qI.Result = data.result;

  if (action === tct.replace) {
    qI.Value = valueUtil.GetFromDb(data.value, qI);
  } else {
    qI.Value = data.value;
  }

  return qI;
}

export const get = (dict) => {
  var qI = new Data();

  qI.Dict = dict;

  return qI;
}

export const Clone = (q) => {
  var qI = new Data();

  qI.Action = q.Action;
  qI.Target = q.Target

  if (q.Value instanceof ReplaceRow_Value) {
    qI.Value = q.Value.Clone();
  } else {
    qI.Value = q.Value
  }

  qI.Result = q.Result
  qI.Dict = q.Dict;

  return qI;
}