import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './MassInfoVMUtil'
import * as rs from '../../../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../../../text/EditableTextView';
import * as valueInfoUtil from '../valueinfo/ValueInfoVMUtil'

export class MassInfoVM {
  constructor() {
    makeObservable(this, {
      valueInfos: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    // this.NameVM.RenderState = v;

    // if (v === rs._default) {
    //   this.SaveDatabase(false);
    // }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // Data
  massTypeExport
  get MassTypeExport() {
    if (!this.massTypeExport) {
      this.massTypeExport = util.GetMassTypeExport(this)
    } return this.massTypeExport
  }

  get ClassifyValueBy() {
    return this.ModelItem.ClassifyValueBy
  } set ClassifyValueBy(v) {
    this.ModelItem.ClassifyValueBy = v;
  }

  classifyValueBy_SelectVM
  get ClassifyValueBy_SelectVM() {
    if (!this.classifyValueBy_SelectVM) {
      this.classifyValueBy_SelectVM = util.GetClassifyValueBy_SelectVM(this)
    } return this.classifyValueBy_SelectVM
  }

  valueInfos = undefined
  get ValueInfos() {
    if (!this.valueInfos) {
      this.valueInfos = util.GetValueInfos(this)
    } return this.valueInfos
  }

  AddValueInfo = () => {
    var q = this.ModelItem.AddValueInfo();
    var qI = valueInfoUtil.get(q, this);
    this.ValueInfos.push(qI)
    return qI;
  }


  get Wrapper_View() {
    return <div>
      <div className='f mes__cell__buttonwrapper'>
        <div className='f1' />
        {this.DeleteVM.View}
      </div>

      <div className='mes__cell__innertable'>
        <table>
          <tbody>
            <tr>
              <td className='title medium'>Cng tc: </td>
              <td>{this.MassTypeExport.View}</td>
            </tr>
            <tr>
              <td colSpan={2}>
                <div className='title f-ac'>
                  <span>
                    Truy xut gi tr t :
                  </span>
                  <div className='ml-10'>
                    {this.ClassifyValueBy_SelectVM.View}
                  </div>
                </div>
              </td>
            </tr>
            <tr>
              <td colSpan={2}>
                <div >
                  <div className='title f-ac'>
                    <span>
                      Thng tin gi tr :
                    </span>
                    <Button type='primary' size='small'
                      className='btn-success ml-15'
                      onClick={this.AddValueInfo}>
                      +
                    </Button>
                  </div>

                  {/* ValueInfos */}
                  {this.ValueInfos ?
                    <div className='mt-15 ml-15'>
                      {this.ValueInfos.map(x => x.View)}
                    </div>
                    : ''
                  }
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return this.Index === 0 ?
      <>
        <td key={this.id} rowSpan={this.RowSpan}>
          {this.Wrapper_View}
        </td>
      </>
      :
      <>
        <tr key={this.id}>
          <td rowSpan={this.RowSpan}>
            {this.Wrapper_View}
          </td>
        </tr>
      </>
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.MassInfos
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}