import { PMData } from "../../../../single/model/PMData";
import * as forgeUtil from "../../../forge/View/ForgeUtil";
import MassTable from "../../table/base/instance/MassTable";

const pmData = () => PMData.Instance

// Property
export const GetMassTable = (q) => {
  // Lu Table vo pmData  c truy vt sau ny (phn undo)
  var qI = pmData().Current_MassTable = new MassTable();

  qI.OnSetHandledRatio_Funcs.push((v) => {
    // q1.HandledRatio = v;
    q.HandledRatio = v;
  })

  qI.Dict = q;
  qI.BuildData.Run();

  return qI;
}

export const Handle_Forge = (q) => {
  if (q.IsGet_Forge) return undefined;
  q.IsGet_Forge = true;

  const setObj = (data) => {
    q.Forge = data ?
      (data.length != 0 ?
        forgeUtil.GetFromDb(data[0], q) : forgeUtil.get(q))
      : undefined
    q.IsGet_Forge = false;
  }

  if (!q.MassTable || !pmData().CurrentProject) {
    setObj();
  } else {
    var apiData = {
      projectId: pmData().CurrentProject._id
    }

    forgeUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }
}