import * as util from './ReplaceRow_ValueUtil'

export class ReplaceRow_Value {
  rowValue
  get RowValue(){
    if (!this.rowValue){
      this.rowValue = util.GetRowValue(this)
    } return this.rowValue;
  } set RowValue(v){
    this.rowValue = v;
  }

  keyValue
  get KeyValue(){
    if (!this.keyValue){
      this.keyValue = util.GetKeyValue(this)
    } return this.keyValue
  } set KeyValue(v){
    this.keyValue = v;
  }

  get ApiData(){
    return this.KeyValue;
  }

  Clone = () => {
    return util.Clone(this)
  }
}