import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './SystemVMUtil'
import * as rs from '../../../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../../../text/EditableTextView';
import * as genUtil from '../../../../../../../../util/generalUtil'
import * as typeUtil from '../type/TypeVMUtil'

export class SystemVM {
  constructor() {
    makeObservable(this, {
      types: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.ClassificationVM.RenderState = v;

    // if (v === rs._default) {
    //   this.SaveDatabase(false);
    // }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // Name
  get Classification() {
    return this.ModelItem.Classification;
  } set Classification(v) {
    this.ModelItem.Classification = v;
  }

  classificationVM
  get ClassificationVM() {
    if (!this.classificationVM) {
      var obj = this.classificationVM
        = editTextUtil.get(this.Classification, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.Classification = obj.Content
            this.RenderState = rs._default
            break;
          case inputUtil.esc:
            obj.Content = this.Classification
            this.RenderState = rs._default
            break;
        }
      }
    }
    return this.classificationVM;
  }

  get ClassificationView() {
    return <EditableTextView className='mt__cell__main'
      store={this.ClassificationVM} />
  }

  get ClassificationWrapper_View() {
    return <div className='mt__subrow f-ac'
      onDoubleClick={this.Edit}
      onMouseEnter={this.OnMouseEnter}
      onMouseLeave={this.OnMouseLeave}>
      {this.ClassificationView}

      <Button type='primary' size='small'
        className='btn-success'
        onClick={this.AddType}>
        +
      </Button>

      {this.DeleteVM.View}
    </div>
  }

  get RowSpan() {
    return this.Types.length > 1 ? this.Types.length : 1
  }

  // Types
  types = undefined
  get Types() {
    if (!this.types) {
      this.types = util.GetTypes(this)
    } return this.types;
  }

  get NotFirst_Types() {
    var list = [...this.Types];
    list.splice(0, 1);
    return list;
  }

  AddType = () => {
    var q = this.ModelItem.AddType();
    var qI = typeUtil.get(q, this);
    qI.RenderState = rs.edit;

    this.Types.push(qI)
    return qI;
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return this.Index === 0 ?
      <>
        <td key={this.id} rowSpan={this.RowSpan}>
          {this.ClassificationWrapper_View}
        </td>
        {this.Types.length > 0 ?
          this.Types[0].RowView
          : ''}
      </>
      :
      <>
        <tr key={this.id}>
          <td rowSpan={this.RowSpan}>
            {this.ClassificationWrapper_View}
          </td>
          {this.Types.length > 0 ?
            this.Types[0].RowView
            : ''}
        </tr>

        {/* Type Index > 0 */}
        {this.NotFirst_Types?.map(x => x.RowView)}
      </>
  }

  // Save
  Save = () => {
    this.Types.forEach(x => {
      x.Name = x.NameVM.Content
    })
  }

  // Index
  get CurrentList() {
    return this.Dict.Systems
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}