import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './MGS_Dict_DictUtil'

export class MGS_Dict_Dict {
  items
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this)
    } return this.items;
  }

  current_MGS
  get Current_MGS(){
    return this.current_MGS
  } set Current_MGS(v){
    this.current_MGS = v;
  }
}