import { TypeVM } from './TypeVM'
import { EntDelete } from '../../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../../delete/EntDelete_ToggleType'
import { Button, message } from 'antd';
import * as massInfoUtil from '../../massinfo/base/MassInfoVMUtil'
import { PMDataVM } from '../../../../../../single/form/PMDataVM';
import { MultiSelectVM } from '../../../../../../../select/MultiSelectVM';
import * as genUtil from '../../../../../../../../util/generalUtil';
import * as filterUtil from '../filter/FilterVMUtil'

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new TypeVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const GetMassInfos = (q) => {
  return q.ModelItem.MassInfos.map(x => massInfoUtil.get(x, q))
}

export const GetFilter = (q) => {
  return filterUtil.get(q.ModelItem.Filter, q);
}