import { makeObservable, observable, action } from 'mobx';
import * as textVMUtil from '../../../../../../text/TextVMUtil'
import * as api from '../../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as mif from '../../../field/MassItemField'
import { PMData } from '../../../../../single/model/PMData';
import * as util from './MassCellUtil'
import { message } from 'antd';
import {url} from './MassCell_Url'

import * as mc_ct from './MassCell_CreateType'

export const pmData = () => {
  return PMData.Instance;
}

export default class MassCell {
  constructor() {
    makeObservable(this, {
      valueColor: observable,
      tdBgColor: observable,
      isHaveWarning: observable,
      tls_LevelGroup_Descriptions: observable,
      vcs_Item: observable,
      items : observable
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  get Table() {
    return this.Dict
  }

  get ColumnIndex() {
    return this.Column.Index;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items){
      this.items = util.GetItems(this);
    } return this.items;
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  GetItems_FromApi = () => {
    util.Handle_Items(this)
  }

  // Unit
  get Unit() {
    return this.Row.Unit
  }

  // ValueData
  valueData
  get ValueData(){
    if (!this.valueData){
      this.valueData = util.GetValueData(this)
    } return this.valueData
  }

  // Sup - Sub
  supItem
  get SupItem() {
    if (this.supItem === undefined) {
      this.supItem = util.Get_SupItem(this);
    }
    return this.supItem;
  } set SupItem(v) {
    this.supItem = v;
  }

  subCells
  get SubCells() {
    if (!this.subCells) {
      this.subCells = util.Get_SubCells(this);
    }
    return this.subCells;
  } set SubCells(v) {
    this.subCells = v;

    // reset haveValue subcells
    this.HaveValue_SubCells = undefined
  }

  OnSet_HaveValue_SubCells_Func = []

  haveValue_SubCells
  get HaveValue_SubCells(){
    if (!this.haveValue_SubCells){
      util.Handle_HaveValue_SubCells(this)
    } return this.haveValue_SubCells;
  } set HaveValue_SubCells(v){
    this.haveValue_SubCells = v;

    genUtil.Handle(this.OnSet_HaveValue_SubCells_Func,
      (v) => this.OnSet_HaveValue_SubCells_Func = v)
  }

  // AsString = () => {
  //   console.log(`${this.Column.Name} - ${this.Row.Name} - ${this.TotalValue}`)
  // }

  // Update
  update
  get Update() {
    if (!this.update) {
      this.update = util.GetUpdate(this)
    } return this.update
  }

  // ViewDetail
  viewDetail
  get ViewDetail(){
    if (!this.viewDetail){
      this.viewDetail = util.GetViewDetail(this)
    } return this.viewDetail;
  }

  get AllItems() {
    if (this.SubCells) {
      return this.SubCells.map(x => x.Items).reduce((a, b) => a.concat(b), []);
    }
    return this.Items;
  }

  // color data
  colorData
  get ColorData(){
    if (!this.colorData){
      this.colorData = util.GetColorData(this)
    } return this.colorData
  }

  isHaveVCS_Item
  get IsHaveVCS_Item() {
    if (!this.isHaveVCS_Item) {
      this.isHaveVCS_Item = util.GetIsHaveVCS_Item(this);
    }
    return this.isHaveVCS_Item;
  }
  set IsHaveVCS_Item(v) {
    this.isHaveVCS_Item = v;
  }

  vcs_Item = undefined
  get VCS_Item() {
    return this.vcs_Item;
  }
  set VCS_Item(v) {
    this.vcs_Item = v;

    this.ValueColor = util.GetValueColor(this);
    this.TDBgColor = util.GetTDBgColor(this)
  }

  valueColor = '#1890ff'
  get ValueColor() {
    return this.valueColor;
  }
  set ValueColor(v) {
    this.valueColor = v
  }

  tdBgColor = undefined
  get TDBgColor() {
    return this.tdBgColor;
  }
  set TDBgColor(v) {
    this.tdBgColor = v;
  }

  get AStyle() {
    return {
      color: this.ValueColor,
      fontWeight: 'bold'
    }
  }

  get TDStyle() {
    return {
      backgroundColor: this.TDBgColor
    }
  }

  OnGet_TLS_LevelGroups_Func_Dict = []
  set Get_TLS_LevelGroups(v) {
    this.OnGet_TLS_LevelGroups_Func_Dict.forEach(x => x.Value());
  }

  tls_LevelGroups
  get TLS_LevelGroups() {
    if (!this.tls_LevelGroups) {
      util.Handle_TLS_LevelGroups(this);
    }
    return this.tls_LevelGroups;
  } set TLS_LevelGroups(v) {
    this.tls_LevelGroups = v;
    this.Get_TLS_LevelGroups = true;
  }

  isHaveWarning = undefined
  get IsHaveWarning() {
    if (this.isHaveWarning === undefined) {
      this.isHaveWarning = util.GetIsHaveWarning(this);
    }
    return this.isHaveWarning;
  } set IsHaveWarning(v) {
    this.isHaveWarning = v;
  }

  tls_LevelGroup_Descriptions = undefined
  get TLS_LevelGroup_Descriptions() {
    if (!this.tls_LevelGroup_Descriptions) {
      util.Handle_TLS_LevelGroup_Descriptions(this);
    }
    return this.tls_LevelGroup_Descriptions;
  } set TLS_LevelGroup_Descriptions(v) {
    this.tls_LevelGroup_Descriptions = v;
  }

  // Query
  queryData
  get QueryData(){
    if (!this.queryData){
      this.queryData = util.GetQueryData(this)
    } return this.queryData;
  }

  // SubGroup
  subGroupData
  get SubGroupData(){
    if (!this.subGroupData){
      this.subGroupData = util.GetSubGroupData(this)
    } return this.subGroupData;
  }

  // CreateType
  // To bng cc lp qua MassItem (byitem) hay tnh ton sn (bycell)
  // mc nh l byitem
  createType
  get CreateType(){
    if (!this.createType){
      this.createType = mc_ct.byitem
    } return this.createType
  } set CreateType(v){
    this.createType = v;

    this.Items = util.GetItems(this);
  }

  // Api
  get ApiData(){
    var column = this.Column;

    var obj = {
      _id : this._id,
      // projectId : this.Column.Data._id,
      supId : this.SupItem?._id,
      key : this.Row.RawValue,
      fullkey : this.Row.FullValue
    }

    column.QueryData.AddQuery(obj);
    obj.reportId = column.Report._id

    return obj;
  }

  // Database
  // Lu thng tin Cell v ValueData
  Save = (handle) => {
    util.Save(this, handle)
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }

        if (handle) {
          handle();
        }
      });
  }
}