import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { MassTypeExportVM } from './MassTypeExportVM';
import * as mte_t from './MTE_Type'
import './MassTypeExport.css'

class MassTypeExportView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassTypeExportVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='f'>
          <div className='mes__row__mi__masstypeexport exporttype'>
            {data.ExportType_SelectVM.View}
          </div>

          <div className='mes__row__mi__masstypeexport masstype f'>
            <span className='title'>{data.MassType_Title}</span>
            {data.MassType_SelectVM.View}
          </div>
        </div>
      </>
    );
  }
}

export default observer(MassTypeExportView);
