import * as genUtil from '../../../../../../../util/generalUtil'
import * as itemUtil from '../item/ItemUtil'

export class Dict {
  Dict

  get ExportSet() {
    return this.Dict;
  }

  get exportSetId() {
    return this.ExportSet._id;
  }

  Items = []

  GetItem = (phaseId, handle) => {
    var list = this.Items
    var item = genUtil.SingleFilter(list, x => x.phaseId === phaseId);
    if (!item) {
      item = itemUtil.get(this);
      item.phaseId = phaseId;

      list.push(item);
    }

    handle(item);
  }
}