import { ItemVM } from './ItemVM'
import { EntDelete } from '../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import { Button, message } from 'antd';
import { SelectVM } from '../../../../../../select/SelectVM';
import * as genUtil from '../../../../../../../util/generalUtil'
import { PMDataVM } from '../../../../../single/form/PMDataVM';
import { MultiSelectVM } from '../../../../../../select/MultiSelectVM';

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new ItemVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const GetMassType_SelectVM = (q) => {
  var qI = new SelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    var massType = q.ModelItem.MassType;
    if (massType) {
      qI.InitialItem = qI.SelectedItem =
        genUtil.SingleFilter(list, (x) => x.Name === massType);
    }
  }

  var host = q.Dict.Dict.ListData;
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }

  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.MassType = v?.Name;

  return qI;
}

export const GetCategory_SelectVM = (q) => {
  var qI = new MultiSelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    var categories = q.ModelItem.Categories;
    if (categories && categories.length !== 0) {
      qI.InitialItems = qI.SelectedItems =
        list.filter(x => categories.includes(x.Name));
    }
  }

  var host = pmDataVM().Category_Dict
  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      () => setObj(host.Items)
    )
  }

  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.Categories = v.map(x => x.Name);

  return qI;
}