// Instance
export const Handle_FromCellData = (cellData, col, supItem, table, handle) => {
  const getRows = () => {
    var list = []

    cellData.forEach(x => {
      var rowValue = x.key

      var q = table.GetRow(rowValue, supItem);

      // // Query
      // if (x.queries.length !== 0) {
      //   q.QueryData.Set_FromApi(x.queries[0].items);
      // }

      var cell = q.GetCell(col);
      cell.SubGroups_Query = x.subgroups;

      // Query
      if (x.queries.length !== 0) {
        cell.QueryData.Set_FromApi(x.queries[0].items);
      }

      cell._id = x._id
      cell.CreateType = 'bycell'
      cell.ValueData.Data =
        x.valueData.map(x => mi_ValueItemUtil.GetFromDb(x));

      list.push(q);
    })

    if (handle) {
      handle(list);
    }
  }

  getRows();
}

export const get = (q, dict) => {
  var qI = new MassRow();
  qI.Name = q;
  qI.Dict = dict;
  return qI;
}

export const Clone = (q) => {
  var qI = new MassRow();
  qI._id = q._id;

}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

