// Method
// Add
export const Add = (supRow, table) => {
  // Command
  table.CommandDict.Do(
    {
      action: tct.add,
      target: {
        type: 'row',
        key: supRow?.RetrieveData
      }
    }, supRow
  )
}

export const Add_Main = (supRow, result, handle, handleUndo) => {
  var table = pmData().Current_MassTable

  var undoFuncs = []
  if (handleUndo) {
    handleUndo(() => {
      undoFuncs.forEach(x => x())
    });
  }

  var row = new MassRow();
  row.IsCreatedByUser = true;

  row.RawValue = result ? result.raw : genUtil.randomId();
  //row.Title = "Tn hng mc"

  if (supRow) {
    row.SupItem = supRow
    supRow.IsShowSub = true;
  } else {
    row.Dict = table;
  }

  // Undo
  undoFuncs.push(() => {
    row.CurrentList.splice(row.CurrentList.length - 1, 1);
  })
  // Do
  row.CurrentList.push(row);

  if (result) {
    row.Title = result.title;
  } else {
    row.Editable_TitleVM.RenderState = trs.edit;
  }

  if (handle) {
    handle();
  }

  return row;
}

// SupItem
export const GetFull_SubItems = (q) => {
  var supItem = q.SupItem;

  var list = []
  if (supItem) {
    list = [...supItem.Full_SupItems];
    list.push(supItem);
  }
  return list;
}

export const Get_Original_Full_SupRows = (q) => {
  var list = [];
  var supItem = q.Original_SupRow;

  while (supItem) {
    list.push(supItem);
    supItem = supItem.Original_SupRow;
  }

  list.reverse();
  return list;
}

