// Unit
export const Get_DefaultUnit = (q) => {
  var unit
  var isDone = false

  if (q.SubItems.length !== 0) {
    q.SubItems.forEach(x => {
      if (!isDone) {
        if (unit === undefined) {
          unit = x.Unit;
        }

        if (!unit || unit !== x.Unit) {
          unit = undefined
          isDone = true;
        }
      }
    })
  } else {
    unit = ut.each;
  }

  return unit;
}

