// Title
export const GetEditable_TitleVM = (q) => {
  var qI = editTextUtil.get(q.Title);

  qI.Is_Tool_Visible = false
  qI.Placeholder = 'tn u mc ...'

  qI.OnKeyDown = (e) => {
    var { key, value } = inputUtil.Get_Shortcut(e);
    switch (key) {
      case inputUtil.enter:
        if (!qI.Content || qI.Content.length === 0) {
          message.warn('Bn phi in tn u mc!');
          return
        }
        qI.RenderState = trs._default
        break
      case inputUtil.esc:
        qI.IsEscape = true;
        qI.RenderState = trs._default
        break;
    }
  }

  genUtil.Add(
    qI.OnSet_RenderState_Func_Dict,
    () => {
      if (qI.RenderState === trs._default) {
        if (qI.IsEscape) {
          qI.Content = q.Title
          qI.IsEscape = false
          return;
        }

        if (qI.Content === q.Title) return;

        // ang trong lnh Add
        if (q.BindingCommand) {
          q.ReTitle(qI.Content);

          var bc = q.BindingCommand;
          bc.Result(q);
          q.BindingCommand = undefined
        } else {
          // Command
          q.Table.CommandDict.Do(
            {
              action: tct.retitle,
              target: {
                type: 'row',
                key: q.RetrieveData
              },
              value: qI.Content
            }, q
          )
        }
      }
    },
    false
  )

  return qI;
}

export const ReTitle = (q, value, handle, handleUndo) => {
  // Undo
  var undoFuncs = []
  if (handleUndo) {
    undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x());
    });
  };

  var qI = q.Editable_TitleVM

  // Lu thng tin Title
  // Undo
  var oldTitle = q.Title;
  undoFuncs.push(() => q.Title = oldTitle);
  // Do
  q.Title = value;

  q.ToolElement.editable_TitleVM_RenderState = qI.RenderState

  if (handle) {
    handle();
  }
}

// FilterData
export const GetFilterData = (q) => {
  const getFilter = (x) => {
    return {
      field: x.Field,
      value: x.RawValue
    }
  }

  var filters = q.Original_Full_SupRows
    .map(x => getFilter(x));
  filters.push(getFilter(q));

  return filters;
}

export const GetRetrieveData = (q) => {
  var f_s = q.Full_SupItems;
  var list = f_s.map(x => x.FullValue);
  list.push(q.FullValue);
  return list;
}

// Tp hp tt c filter trong Row
// Row to bi ngi dng: tp hp cc filter ca cc Row con
// Row t sinh: 1 filter tng ng vi Origin_Full_SupRows
export const GetFilterDict = (q) => {
  if (!q.IsCreatedByUser) {
    return q.FilterData
  } else {
    if (q.SubItems.length === 0) {
      return []
    } else {
      return q.SubItems.map(x => x.FilterDict).filter(x => x.length !== 0)
    }
  }
}

