// ReplaceInfomation
export const GetReplaceInformation = (q) => {
  if (q.SupItem === q.Original_SupRow) return ''
  var info = 'Replace';

  var list = q.Original_Full_SupRows;
  var length = list.length;

  list.forEach((x, i) => {
    if (i == 0) {
      info += ': '
    }

    info += x.Title;

    if (length > 1 && i !== length - 1) {
      info += ' > '
    }
  })

  return info;
}

// SubItems
export const Get_SubItems_FromApi = (q, subGroupId, col, handle) => {
  var apiData = {
    hostId: subGroupId,
    reportId: col.Report._id
  }

  GetData(apiData, (data) => {
    Handle_FromCellData(data, col, q, q.Table, (rows) => {
      if (handle) {
        handle();
      }
    });
  })
}

// ToggleSub
export const ToggleSub = (q) => {
  // Nu ang Pending_HaveSub, truy vn thng tin Sub
  if (q.IsPending_HaveSub) {
    var tempFields = q.SubFields;

    const finish = () => {
      q.SubFields = tempFields;
      q.IsPending_HaveSub = false;
    }

    const sub_pending = (handle) => {
      // Tip tc Pending_HaveSub  mc con
      if (tempFields.length > 1) {
        var sfsFields = tempFields.filter((x, i) => i !== 0);
        q.SubItems.forEach(x => {
          x.SubFields = sfsFields;
          x.IsPending_HaveSub = true;
        })
      }

      if (handle) {
        handle();
      }
    }

    var gf = q.GroupFactory
    // Ch ly SubFields u tin  tnh ton
    gf.Fields = [tempFields[0]];
    q.SubFields = [];
    gf.Run(() => sub_pending(finish));

  } else {
    q.IsShowSub = !q.IsShowSub;
  }
}

// Move
export const Move = (row1, row2) => {
  var table = pmData().Current_MassTable;
  table.CommandDict.Do(
    {
      action: tct.move,
      target: {
        type: 'rowlist',
        key: [
          row1.RetrieveData,
          row2.RetrieveData
        ]
      }
    }, [row1, row2]
  )
}

export const Move_Main = (row1, row2, handle, handleUndo) => {
  // Undo
  var undoFuncs
  if (handleUndo) {
    undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x());
    });
  }

  var list = row1.CurrentList;

  // Undo
  if (handleUndo) {
    undoFuncs.push(() => {
      genUtil.MoveItem(list, row1.Index, row2.Index);
    })
  }
  // Do
  genUtil.MoveItem(list, row1.Index, row2.Index);

  if (handle) {
    handle();
  }
}