import { TLS_PageItem } from "./TLS_PageItem"
import * as levelGroupUtil from '../levelGroup/LevelGroupUtil'
import * as filterUtil from '../filter/TLS_FilterUtil'

// Instance
export const get = (data, dict) => {
  var qI = new TLS_PageItem();
  qI.Name = data.name;
  qI.Filters = data.filters.map(x => filterUtil.get(x, qI));
  qI.LevelGroups = data.levelGroups ? data.levelGroups.map(x => levelGroupUtil.get(x, qI))
     : data.levels.map(x => levelGroupUtil.get(x, qI))
  qI.Dict = dict;
  return qI;
}

export const GetDefault = (dict) => {
  var qI = new TLS_PageItem();
  qI.Name = 'setting 1';
  qI.Dict = dict;
  return qI;
}

export const Clone = (q, dict) => {
  var qI = new TLS_PageItem();

  qI.Name = q.Name;
  qI.Filters = q.Filters.map(x => filterUtil.Clone(x, qI));
  qI.LevelGroups = q.LevelGroups.map(x => levelGroupUtil.Clone(x, qI));
  qI.Dict = dict ? dict : q.Dict;

  return qI;
}

// Property
export const GetLevelGroups = (q) => {
  return [ levelGroupUtil.GetDefault(q) ]
}

export const GetFilters = (q) => {
  return [ filterUtil.GetDefault(q) ]
}

// Method
export const IsEqual = (q1, q2) => {
  // Filter
  var f1 = q1.Filters;
  var f2 = q2.Filters;

  if (f1.length !== f2.length) return false;

  var c_f2 = [...f2];
  var isValid = true;

  f1.forEach(x => {
    if (isValid) {
      var index = c_f2.findIndex(y => x.IsEqual(y));
      if (index !== -1) {
        c_f2.splice(index, 1);
      } else {
        isValid = false;
      }
    }
  });

  if (!isValid) return false;

  // Level
  var l1 = q1.LevelGroups;
  var l2 = q2.LevelGroups;

  if (l1.length !== l2.length) return false;

  // Level phi ng th t v bng nhau
  l1.forEach((x, i) => {
    if (isValid){
      if (x.IsEqual(l2[i])){

      }else {
        isValid = false;
      }
    }
  })

  return isValid;
}