import { Button, Popover } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import * as rs from '../../../../../text/TextRenderState';

import * as genUtil from '../../../../../../util/generalUtil';
import * as inputUtil from '../../../../../../util/inputUtil';
import * as comUtil from '../../../../../../util/commandUtil';

import * as util from './LS_ItemVMUtil'
import * as mulSelectUtil from '../../../../../select/MultiSelectVMUtil';
import MultiSelectView from '../../../../../select/MultiSelectView';
import * as pct from '../../../command/ProjectCommand'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import EditableTextView from '../../../../../text/EditableTextView';

const pmDataVM = () => PMDataVM.Instance

export class LS_ItemVM {
  constructor() {
    makeObservable(this, {
      Is_ShowValue_Delete: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Data
  get Name() {
    return this.ModelItem.Name;
  }
  set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      this.nameVM = inputUtil.GetInput(this, 'Name');
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  get Values() {
    return this.ModelItem.Values;
  }
  set Values(v) {
    this.ModelItem.Values = v;
  }

  value_StorageList
  get Value_StorageList() {
    if (!this.value_StorageList) {
      util.Handle_Value_StorageList(this);
    }
    return this.value_StorageList;
  } set Value_StorageList(v) {
    this.value_StorageList = v;

    var obj = this.ValueVM;
    obj.Items = v;
    obj.InitialItems = this.Values;
  }

  valueVM
  get ValueVM() {
    if (!this.valueVM) {
      var list = this.Value_StorageList ? this.Value_StorageList : []
      var obj = this.valueVM = mulSelectUtil.get(list, this.Values);
      obj.Placeholder = 'Chon d liu'
      obj.OnSelectedItemChanged = (v) => this.Values = v;
      obj.GetDisabled_Func = () => !this.Commands.includes(pct.edit)
    }
    return this.valueVM;
  }

  Is_ShowValue_Delete = undefined

  ShowValue_Delete = () => {
    if (this.Commands.includes(pct.edit)) {
      this.Is_ShowValue_Delete = true;
    }
  }

  HideValue_Delete = () => {
    this.Is_ShowValue_Delete = false;
  }

  get Value_Element() {
    return <>
      <div className='ls_item__value__wrapper'
        onMouseEnter={this.ShowValue_Delete}
        onMouseLeave={this.HideValue_Delete}>
        <MultiSelectView store={this.ValueVM} />

        <div className='ls_item__value__delete'>
          {comUtil.Validate(pct.edit) ? this.DeleteVM.View : <div />}
        </div>
      </div>
    </>
  }

  // Index
  get Index() {
    return this.Dict.Items.indexOf(this);
  }

  get IsLast() {
    return this.Dict.Items.length - 1 === this.Index;
  }

  // Delete
  Delete = () => {
    var dict = this.Dict;
    dict.Items.splice(this.Index, 1);
    
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }


  get Project() {
    return this.Dict.Project
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }

  // Row
  get RowView() {
    return util.GetRowView(this)
  }
}