import { PMData } from "../../../../../single/model/PMData";
import * as genUtil from '../../../../../../../util/generalUtil'
import * as mgsd_ut from '../../dict/base/MGS_Dict_UseType'

export const pmData = () => PMData.Instance

export const Run = (q) => {
  var mgs = q.Dict;

  var mgsd = genUtil.SingleFilter(pmData().Mgs_Dict_Dict.Items, 
    (x) => x.UseType === mgsd_ut.template)
  var save = mgsd.Save;
  save.Name = q.InputName

  const run = () => save.Run();
  
  // CommandItem
  const setCommandItems = (data) => {
    save.Command_Items = 
      data.Items.filter(x => x.CanUser_ForTemplate);
    run();
  }

  if (mgs.CommandData){
    setCommandItems(mgs.CommandData)
  } else {
    genUtil.Add_2(mgs.OnSet_CommandData_Func,
      () => setCommandItems(mgs.CommandData))
  }
}