import { LevelDict } from "./LevelDict"
import * as itemUtil from '../base/LevelItemUtil'
import { baseurl, url } from './LevelDict_Url'
import { url as itemUrl } from '../base/Level_Url'
import * as api from '../../../../../util/apiUtil'
import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const GetDefault = (project, handle) => {
  // ly t LevelDict database
  // GetApi(project._id, (data) => {
  //   var qI = GetFromDb(data, project);
  //   handle(qI);
  // });

  // Ly t Level database
  var qI = get(project);
  handle(qI);
}

export const GetFromDb = (data, project) => {
  var qI = get(project);

  var items = data.data.items;
  qI.Items = items ? items.map(x => itemUtil.GetFromDb(x, qI)) : []

  return qI;
}

export const get = (project) => {
  var qI = new LevelDict();
  qI.Project = project;
  return qI;
}

// Api
export const GetApi = (projectId, handle) => {
  var data = {
    projectId
  }

  var url = `${baseurl()}/get`;
  api.post(url, data).then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data);
      }
    });
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => itemUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  var apiData = {
    projectId: q.Project._id
  }

  itemUtil.GetData(apiData, (data) => setObj(data));
}

export const Handle_Undefined_Item = (q) => {
  if (q.IsGet_Undefined_Item) return undefined;
  q.IsGet_Undefined_Item = true;

  const setObj = (data) => {
    q.Undefined_Item = itemUtil.GetFromDb(data, q);
    q.IsGet_Undefined_Item = false;
  }

  var apiData = {
    type: 'undefined'
  }

  const apiFunc = () => api.post(`${itemUrl}/get`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      setObj(data)
    })
}

// Method
export const GetLevel = (q, name, handle) => {
  if (!name || name.length === 0) {
    const getObj = () => {
      handle(q.Undefined_Item)
    }

    if (q.Undefined_Item){
      getObj();
    } else {
      genUtil.Add_2(q.OnSet_Undefined_Item_Func,
        getObj);
    }

  } else {
    const getObj = () => {
      var list = q.Items;
      var item = genUtil.SingleFilter(list, x => x.Name === name);
      if (item) {
        handle(item)
      } else {
        var apiData = {
          projectId: q.Project._id,
          name
        }

        const func = (data) => {
          item = itemUtil.GetFromDb(data, q);
          list.push(item);
          handle(item);
        }

        const apiFunc = () => api.post(`${itemUrl}/add`, apiData);

        apiFunc().then(response => response.data)
          .then(data => {
            func(data)
          })
      }
    }

    if (q.Items){
      getObj();
    } else {
      genUtil.Add_2(q.OnSet_Items_Func,
        getObj);
    }
  }
}