import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './MassTypeExportVMUtil'
import MassTypeExportView from './MassTypeExportView';
import * as mte_t from './MTE_Type'

export class MassTypeExportVM {
  constructor() {
    makeObservable(this, {
      exportType: observable
    })
  }

  exportType = undefined
  get ExportType() {
    if (!this.exportType) {
      this.exportType = this.ModelItem.ExportType
    }
    return this.exportType
  } set ExportType(v) {
    this.exportType = v;
    this.ModelItem.ExportType = v;
  }

  get MassType_Title() {
    switch (this.ExportType) {
      case mte_t.byname:
        return 'Tn:';
      default:
        return 'Mc nh:';
    }
  }

  exportType_SelectVM
  get ExportType_SelectVM() {
    if (!this.exportType_SelectVM) {
      this.exportType_SelectVM = util.GetExportType_SelectVM(this)
    } return this.exportType_SelectVM
  }

  get MassType() {
    return this.ModelItem.MassType
  } set MassType(v) {
    this.ModelItem.MassType = v
  }

  massType_SelectVM
  get MassType_SelectVM() {
    if (!this.massType_SelectVM) {
      this.massType_SelectVM = util.GetMassType_SelectVM(this)
    } return this.massType_SelectVM;
  }

  get View() {
    return <MassTypeExportView store={this} />
  }
}