import * as itemUtil from '../base/ProjectUtil'
import * as util from './Project_DictUtil'

import * as genUtil from '../../../../../util/generalUtil'

export class Project_Dict {
  // items
  // get Items(){
  //   if (!this.items){
  //     this.items = []
  //   }
  //   return this.items;
  // } set Items(v){
  //   this.items = v;

  //   this.GetItemsDone = true;
  // }

  // set GetItemsDone (v){
  //   if (this.OnGetItemsDone){
  //     this.OnGetItemsDone(v);
  //   }
  // }

  // Item
  OnSet_Items_Func = []

  items
  get Items(){
    if (!this.items){
      util.Handle_Items(this)
    }return this.items;
  } set Items(v){
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var item = itemUtil.get(this);
    item.Name = 'Tn d n'

    this.Items.push(item);
    return item;
  }

  Get_Project = (data) => {
    var list = this.Items;
    var index = list.findIndex(x => x._id === data._id);
    if (index === -1){
      var item = itemUtil.GetFromDb(data, this);
      list.push(item);
      return item;
    }
    return list[index];
  }

  // Move
  Move = (i1, i2) => {
    var list = this.Items;
    genUtil.MoveItem(list, i1, i2);

    list[i1].Save();
    list[i2].Save();
  }

  Insert = (i1, i2) => {
    var list = this.Items;
    genUtil.InsertItem(list, i1, i2);

    var max = i1 > i2 ? i1 : i2
    for (let i = 0; i < max + 1; i++) {
      list[i].Save();
    }
  }
}