import * as util from './DataUtil'
import { ReplaceRow_Value } from "../value/ReplaceRow_Value"

export class Data {
  Action

  Target

  Value

  Result

  get ApiData(){
    var obj = {
      action : this.Action,
      target: this.Target,
      result: this.Result
    }

    if (this.Value instanceof ReplaceRow_Value){
      obj['value']  = this.Value.ApiData
    } else {
      obj['value']  = this.Value
    }

    return obj;
  }

  Clone = () => {
    return util.Clone(this)
  }
}