  HandleItems = (items, col, initialRow, fields, mainhandle) => {
    // this.RunType = rt.partial

    var length = items.length;
    var i = 0;
    const incre_func = () => {
      i++;
      if (i === length){
        if (mainhandle){
          mainhandle();
        }
      }
    }

    items.forEach(x => this.HandleItem(x, col, initialRow, fields, incre_func))
  }

  HandleItem = (item, col, initialRow, fields, handle) => {
    // this.RunType = rt.partial

    util.HandleItem(this, item, col, fields, initialRow);

    // Lng function  ngoi vo  x l async
    if (handle){
      var vid = this.ValueItem_Data
      if (vid.HandledItems.length === 0){
        handle()
      } else {
        genUtil.Add_2(
          vid.On_Clear_Func,
          () => {
            handle()
          }
        )
      }
    }
  }
