import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Table.css'
import { Project_DictVM } from '../dict/Project_DictVM';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import * as itemUtil from '../base/ProjectVMUtil'

import * as comUtil from '../../../../../util/commandUtil'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Project_DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;
    console.log(`project count : ${data.Items?.length}`)

    return (
      <>
        <table className='table table-bordered'>
          <thead className='es__thead'>
            <tr>
              <th className='mt__table__move'></th>
              <th className='project__table__name text-center'>Tn</th>
              <th className='text-center'>M d n</th>

              {comUtil.Validate('admin') ?
                <th className='project__table__fortest text-center'>For test</th>
                : undefined}

              <th className='mt__table__command'></th>
            </tr>
          </thead>

          <DragDropContext onDragEnd={data.OnDragEnd}>
            <Droppable droppableId='project-dict'>
              {(provided) =>
                <tbody {...provided.droppableProps} ref={provided.innerRef}>
                  {data.Items.map((x, i) => itemUtil.GetRowView(x, i))}
                  {provided.placeholder}
                </tbody>
              }
            </Droppable>
          </DragDropContext>

          {/* <tbody>
            {data.Items?.map(x => x.RowView)}
          </tbody> */}
        </table>
      </>
    );
  }
}

export default observer(TableView);
