import { PlusCircleOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as util from './Dict_DictVMUtil'
import './Dict_Dict.css'
import * as dataItemUtil from '../../item/ItemVMUtil'

export class Dict_DictVM {
  constructor() {
    makeObservable(this, {
      Is_Modal_Visible : observable,
      SetValue : observable
    })
  }

  items
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items;
  }

  get Data(){
    return this.ModelItem.Data
  }

  queryTab
  get QueryTab() {
    if (!this.queryTab) {
      this.queryTab = util.GetQueryTab(this)
    } return this.queryTab
  }

  get View() {
    return util.GetView(this)
  }

  Is_Modal_Visible = false

  get ModalView() {
    return util.GetModalView(this)
  }

  currentItem = undefined
  get CurrentItem() {
    return this.currentItem
  } set CurrentItem(v) {
    this.currentItem = v;
    this.ModelItem.Current_MDS = v?.ModelItem ? v.ModelItem : v
  }

  popover_Render
  get Popover_Render(){
    if (!this.popover_Render){
      this.popover_Render = util.GetDefaultPopover_Render(this)
    } return this.popover_Render
  } set Popover_Render(v){
    this.popover_Render = v;
  }

  // Field
  currentField = undefined
  get CurrentField(){
    return this.currentField
  } set CurrentField(v){
    this.currentField = v;
    this.ModelItem.CurrentField = v;
  }

  // MassType
  currentMassType = undefined
  get CurrentMassType(){
    return this.currentMassType
  } set CurrentMassType(v){
    this.currentMassType = v;
    this.ModelItem.CurrentMassType = v;
  }

  // Data
  data
  get Data(){
    if (!this.data){
      this.data = util.GetData(this)
    } return this.data
  } set Data(v){
    this.data = v;
  }

  AddData = (v) => {
    var qI = dataItemUtil.get(v, this)
    qI.Edit();
    this.Data.push(qI)
    
    this.Items.forEach(x => {
      var list = x.Items;
      if (list.length !== this.Data.length){
        x.Items.push(qI)
      }
    })
  }

  // Initial
  Initial = () => {
    this.QueryTab.CurrentItem = this.Items[0]
    this.ModelItem.Initial();
  }

  // Refine
  Refine = () => {
    util.Refine(this);
  }

  // SetValue
  SetValue = undefined

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle);
  }
}