import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './MassColumnUtil'

export default class MassColumn {
  constructor() {
    makeObservable(this, {
      name : observable
    })
  }

  _id
  get id() {
    if (!this._id){
      this._id = genUtil.randomId();
    }
    return this._id;
  }

  get Index(){
    return this.Table.Columns.indexOf(this);
  }

  Cells = []

  key

  field
  get Field(){
    if (!this.field){
      this.field = util.GetField(this)
    } return this.field;
  }

  // project
  OnSet_Project_Func = []

  project
  get Project() {
    if (!this.project){
      util.Handle_Project(this)
    } return this.project;
  } set Project(v) {
    this.project = v;
    genUtil.Handle(this.OnSet_Project_Func,
      (v) => this.OnSet_Project_Func = v)
  }

  // phase
  OnSet_Phase_Func = []

  phase
  get Phase(){
    if (!this.phase){
      util.Handle_Phase(this)
    } return this.phase;
  } set Phase(v){
    this.phase = v;
    genUtil.Handle(this.OnSet_Phase_Func,
      (v) => this.OnSet_Phase_Func = v)
  }

  // name
  OnSet_Name_Func = []

  name = undefined
  get Name(){
    if (!this.name){
      util.Handle_Name(this)
    } return this.name;
  } set Name(v){
    this.name = v;
    genUtil.Handle(this.OnSet_Name_Func,
      (v) => this.OnSet_Name_Func = v)
  }

  queryData
  get QueryData(){
    if (!this.queryData){
      this.queryData = util.GetQueryData(this)
    } return this.queryData
  }

  // massitem handle
  massItem_Handle
  get MassItem_Handle(){
    if (!this.massItem_Handle){
      this.massItem_Handle = util.GetMassItem_Handle(this);
    } return this.massItem_Handle
  }
}