import { SelectVM } from "../../../../../../select/SelectVM";
import * as textUtil from "../../../../../../text/TextVMUtil";
import * as genUtil from '../../../../../../../util/generalUtil';
import * as rs from './MR_Unit_RenderState'
import * as rUtil from '../base/MassRowUtil'
import * as u_sn from '../update/MRU_SectionName'
import * as u_dt from '../../../../update/Update_DirectionType'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import { InfoCircleOutlined } from "@ant-design/icons";
import { Popover } from "antd";

// Command
import * as tct from '../../command/item/base/CommandType'

// Instance


// Property
export const GetUpdate_DirectionType = (q) => {
  return q.Dict.SubItems.length === 0 ? u_dt.upper
    : u_dt.none
}

export const GetUnit_SelectVM = (q) => {
  var qI = new SelectVM();
  qI.Placeholder = '...'

  const setObj = () => {
    var data = q.UnitDict
    qI.Items = data
    qI.IdRender = (v) => v.Unit
    qI.TitleRender = (v) => q.ItemRender(v)

    // Ch lm vic ny khi khi to SelectVM
    if (!q.Is_Unit_Select_Created) {
      var host = q.Dict;
      q.Is_Unit_Select_Created = true;

      if (q.Update_DirectionType === u_dt.upper) {
        q.SetUnit_FromHost(rUtil.Get_DefaultUnit(host));
      }
    }
  }

  if (q.UnitDict) {
    setObj()
  }

  // Khi cp nhp Units, t ng cp nhp List  trn
  genUtil.Add_2(
    q.OnSet_UnitDict_Func,
    setObj, false)

  qI.OnSelectedItemChanged = (v) => {
    if (!q.IsUndo) {
      q.IsUndo = true;
      q.CurrentUnitItem = v;
    } else {
      SelectUnit(q, v);
      // var table = q.Table
      // SetUndo(q, (undo) => {
      //   table.CommandDict.Add(
      //     {
      //       Key: tct.changeunitrow,
      //       Undo: undo
      //     })
      // });
    }
  }

  qI.OnSetOpen = (v) => {
    if (!v) {
      q.RenderState = rs.text
    }
  }

  return qI;
}

export const GetUnit_RenderVM = (q) => {
  var qI = textUtil.get(q.CurrentUnit)
  return qI;
}

export const GetUnit_RenderView = (q) => {
  return q.ItemRender(q.CurrentUnitItem)
}

// Units
export const Handle_UnitDict = (q) => {
  if (q.IsGet_UnitDict) return undefined;
  q.IsGet_UnitDict = true;

  var host = q.Dict;

  const setObj = () => {
    // if (q.Row.Title === '30 Degree'){
    //   q.IsDebug = true;
    // }

    var list = host.Cells.map(x => x.ValueData.Data);
    var unitDict

    list.forEach((x, i) => {
      // if (q.IsDebug){
      //   debugger
      // }

      if (i === 0) {
        unitDict = x.map(y => {
          return {
            Unit: y.Unit,
            IsMissing: y.IsMissing
          }
        });
      } else {
        // unitDict.forEach((y, j) => {
        //   var item = genUtil.SingleFilter(x, (a) => y === a.Unit);
        //   if (!item) {
        //     // Unit trong UnitDict khng c trong phn t (x,i)
        //     y.IsMissing = true;
        //   }
        // })

        var mapIndexs = []
        // Kim tra Unit khng c trong UnitDict
        x.forEach(x_unit => {
          var index = unitDict.findIndex(u => u.Unit === x_unit.Unit);
          if (index === -1){
            // khng c x_unit trong unitDict
            unitDict.push({
              Unit : x_unit.Unit,
              IsMissing: x_unit.IsMissing
            })
            mapIndexs.push(unitDict.length-1);
          } else {
            mapIndexs.push(index);
          }
        })

        // Kim tra Unit cha c map
        for (let u_i = 0; u_i < unitDict.length; u_i++) {
          if (!mapIndexs.includes(u_i)){
            // mapIndexs khng cha u_i ang xt
            unitDict[u_i].IsMissing = true;
          }
        }
      }
    })

    q.UnitDict = unitDict;
    q.IsGet_UnitDict = false
  }

  if (host.Is_HandleTotalData_Cells) {
    setObj()
  } else {
    genUtil.Add_2(
      host.On_HandleTotalData_Cells_Func,
      setObj
    )
  }
}

export const Set_CurrentUnit = (q, v) => {
  var dirType = q.Update_DirectionType
  var qI = q.Dict;

  // Update Data hin ti
  qI.Update.Run({
    Section: u_sn.celldata,
    Data: { Sections: uc_sn.unit }
  }, u_dt.none)

  if (dirType !== u_dt.none) {
    qI.Update.Run(u_sn.unit, dirType, false)
  }

  q.Update_DirectionType = u_dt.both
}

// Method
export const SelectUnit = (q, v) => {
  var row = q.Dict;

  // Command
  q.Table.CommandDict.Do(
    {
      action: tct.changeunit,
      target: {
        type: 'row',
        key: row.RetrieveData
      },
      value: v
    }, row
  )
}

export const SelectUnit_Main = (q, value, handle, handleUndo) => {
  SetUndo(q, handleUndo);

  q.CurrentUnitItem = value;

  if (handle){
    handle();
  }
}

export const SetUndo = (q, handleUndo) => {
  var oldUnit = q.CurrentUnit;

  var undo
  if (oldUnit) {
    undo = () => {
      q.SetUnit_FromHost(oldUnit);
    }
  } else {
    // Lu cc  c Unit bn trong
    var dict = []
    var row = q.Dict;
    const getRows = (r) => {
      var unit = r.UnitData.CurrentUnit;
      if (r !== row && r.UnitData.CurrentUnit) {
        dict.push({
          Row: r,
          Unit: unit,
          DirectionType: u_dt.lower
        })
      } else {
        dict.push({
          Row: r,
          Unit: undefined,
          DirectionType: r === row ? u_dt.upper : u_dt.none
        })
        r.SubItems.forEach(x => getRows(x));
      }
    }

    getRows(row);

    undo = () => {
      dict.forEach(x => {
        x.Row.Update.Run({
          Section: u_sn.unit,
          Data: { Unit: x.Unit }
        }, x.DirectionType)
      })
    }
  }

  handleUndo(undo);
}

// ItemRender
export const ItemRender = (q, v) => {
  return <>
    {v ? <div className='f-ac'>
      <div className='f1'>{v.Unit}</div>
      {v.IsMissing ?
        <Popover trigger='hover'
          content={<span className='warning'>
            Mt s u mc con khng c n v ny!
          </span>}>
          <InfoCircleOutlined className='warning' />
        </Popover>
        : ''}
    </div>
      : ''}
  </>
}