import { Query } from "./Query"
import * as itemUtil from '../../row/query/item/ItemUtil'
import * as mif from '../../../field/MassItemField'
import { PMData } from '../../../../../single/model/PMData'

const pmData = () => PMData.Instance;

// Instance
export const get = (dict) => {
  var qI = new Query();
  qI.Dict = dict;
  return qI;
}

// Property
export const GetItems = (q) => {
  var {key, Field }= q.Column;

  switch (Field) {
    case mif.Project:
      var qI = itemUtil.get(q);
      qI.field = mif.Project;
      qI.valueId = key.value;

      return [qI]
    case mif.Phase:
      var projectItem = itemUtil.get(q);
      projectItem.field = mif.Project;
      projectItem.valueId = pmData().CurrentProject._id;

      var phaseItem = itemUtil.get(q);
      phaseItem.field = mif.Phase;
      phaseItem.valueId = key.value;

      return [projectItem, phaseItem]
  }
}