import { makeObservable, observable, action } from 'mobx';
import * as util from './ColorUtil'

import * as genUtil from '../../../../../../../util/generalUtil'

export class Color {
  constructor() {
    makeObservable(this, {
      // backgroundColor: observable,
      // color: observable
    })
  }

  Dict

  get Cell() {
    return this.Dict
  }

  get Column() {
    return this.Cell.Column
  }

  get ValueItem() {
    return this.Cell.ValueData.CurrentItem
  }

  // background
  // OnSet_BackgroundColor_Func = []

  // backgroundColor = undefined
  // get BackgroundColor() {
  //   if (!this.backgroundColor) {
  //     util.Handle_BackgroundColor(this)
  //   } return this.backgroundColor
  // } set BackgroundColor(v) {
  //   this.backgroundColor = v;

  //   genUtil.Handle(this.OnSet_BackgroundColor_Func,
  //     (v) => this.OnSet_BackgroundColor_Func = v)
  // }

  // background
  get BackgroundColor() {
    return util.GetBackgroundColor(this)
  }

  get Td_Style() {
    return this.BackgroundColor ?
      {
        backgroundColor: this.BackgroundColor
      }
      : undefined
  }

  // color
  // color = '#1890ff'
  // get Color() {
  //   return this.color
  // } set Color(v) {
  //   this.color = v;
  // }

  get Color(){
    return util.GetColor(this)
  }

  get ValueStyle() {
    return this.Color ?
      {
        color: this.Color,
        fontWeight: 'bold'
      }
      :
      {
        fontWeight: 'bold'
      }
  }

  get VCS_Item() {
    return util.GetVCS_Item(this)
  }
}