import axios from 'axios';

function getaccesstoken() {

	var qs = require('qs');
	var data = qs.stringify({
		'client_id': process.env.REACT_APP_CLIENT_ID || 'jyHlwagmmY0Zx8XGvmt0EU2S9nUAwGKb',
		'client_secret': process.env.REACT_APP_CLIENT_SECRET || 'i0TzpGwtAOLATGU5',

		'grant_type': 'client_credentials',
		'scope': 'code:all data:write data:read bucket:create bucket:delete bucket:read'
	});
	var config = {
		method: 'post',
		url: 'https://developer.api.autodesk.com/authentication/v1/authenticate',
		headers: {
			'Content-Type': 'application/x-www-form-urlencoded',
			'Cookie': 'PF=Smw68bwi3kpiuv1Q4fnRcs'
		},
		data: data
	};

	return axios(config)
		.then(function (response) {
			//   console.log(JSON.stringify(response.data));
			return response.data
		})
		.catch(function (error) {
			console.log(error);
		});

}
const Client = { getaccesstoken };
export default Client;