import { CommandDict } from "./CommandDict"
import { Button } from 'antd';
import UndoView from "../../../../svg/UndoView";
import './CommandDict.css'

// Instance
export const get = (dict) => {
  var qI = new CommandDict();
  qI.Dict = dict;
  return qI;
}

// Property
export const GetButtonView = (q) => {
  var size = 12
  return <Button type='primary' size='small'
    className='commanddict__button btn-warning' onClick={q.Undo}
    disabled={q.Disabled}>
    <UndoView width={size} height={size} />
  </Button>
}