import * as genUtil from '../../../../../../../util/generalUtil'
import * as comUtil from '../../../../../../../util/commandUtil'
import { EntDelete } from '../../../../../../delete/EntDelete';
import { Tab } from '../../../../tab/Tab';
import * as iUtil from '../base/DictVMUtil'
import { Dict_DictVM } from './Dict_DictVM';
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import { PlusCircleOutlined, SaveFilled } from '@ant-design/icons';
import { Button, message, Popover } from 'antd';
import { TextVM } from '../../../../../../text/TextVM';
import InputView from '../../../../../../input/InputView';
import Modal from 'antd/lib/modal/Modal';
import * as pct from '../../../../command/ProjectCommand'
import * as i_ut from '../base/Dict_UseType'
import * as popoverUtil from '../../../../../../popover/EntPopoverUtil';
import * as dataItemUtil from '../../item/ItemVMUtil'

// Instance
export const get = (q) => {
  var qI = new Dict_DictVM();

  qI.ModelItem = q;
  q.OnAddData = (v) => qI.AddData(v)
  q.OnSet_Data = (v) => qI.Data = GetData(qI)

  return qI;
}

// Property
export const GetItems = (q) => {
  return q.ModelItem.Items.map(x => iUtil.get(x, q));
}

export const GetQueryTab = (q) => {
  var qI = new Tab();

  qI.Items = q.Items;
  qI.TitleRender = (v) => {
    switch (v.UseType) {
      case i_ut.partial:
        return q.CurrentMassType
      case i_ut.all:
        return 'All'
    }
  }
  qI.ContentRender = (v) => v.TableWrapper_View;

  return qI;
}

export const GetDefaultPopover_Render = (q) => {
  return (button) => {
    var qI = popoverUtil.get();

    qI.Trigger = 'click'
    qI.TitleRender = () => button;
    qI.ContentRender = () => q.View;

    qI.PreSet_Is_Content_Visible = (v) => {
      if (v) {
        q.Initial();
      }
    }
    qI.OnSet_Is_Content_Visible = (v) => {
      if (!v) {
        q.Refine();
        q.SaveDatabase(false);
      }
    }

    return qI;
  }
}

export const GetView = (q) => {
  return <div className='sg__view__wrapper'>
    {q.QueryTab.View}
  </div>
}

export const GetModalView = (q) => {
  var title = 'THIT LP'

  const hideVisible = () => {
    q.Is_Modal_Visible = false
  }

  const modalOK = () => {
    q.Is_Modal_Visible = false
    q.CurrentItem = q.TempCurrentItem
  }

  return <Modal title={title} visible={q.Is_Modal_Visible}
    width={1000}
    className='msg__modal'
    onCancel={hideVisible}
    footer={
      <>
        <Button type='primary' size='small'
          onClick={modalOK}>ok</Button>
        <Button type='text' size='small'
          onClick={hideVisible}>cancel</Button>
      </>
    }>
    <div>
      {q.View}
    </div>
  </Modal>
}

export const GetData = (q) => {
  return q.ModelItem.Data.map(x => dataItemUtil.get(x, q));
}

// Method
export const Refine = (q) => {
  // Ly list trong cng tc v field hin ti
  var list = q.Items[0].Items;
  var removes = []

  list.forEach((x,i,a) => {
    var name = x.Name;
    if (!name || name.length === 0){
      removes.push(x);
    } else {
      if (i !== a.findIndex(y => y.Name === name)){
        removes.push(x)
      }
    }
  })

  removes.forEach(x => x.Delete());
}

