import { message } from "antd";
import { PMDataVM } from "../../../../single/form/PMDataVM";
import { PMData } from "../../../../single/model/PMData";
import MassResultCompVM from "./MassResultCompVM"
import * as genUtil from '../../../../../../util/generalUtil'
import { MultiSelectVM } from "../../../../../select/MultiSelectVM";

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export const pmData = () => {
  return PMData.Instance;
}

export const get = (q, isfetchProjects = true) => {
  var qI = new MassResultCompVM();
  qI.ModelItem = q;

  q.ViewItem = qI;

  if (isfetchProjects) {
    pmDataVM().Project_DictVM.OnGetItemsDone = (v) => {
      qI.ProjectVMStorageList = GetProjectVMStorageList(qI);
    }
  }

  q.OnGetItemsDone_Funcs.push(() => {
    message.success('get data successfully');
  })

  return qI;
}

export const GetProjectVMStorageList = (q) => {
  return pmDataVM().Project_DictVM.Items;
}

export const GetProjectVMs = (q) => {
  var storageList = q.ProjectVMStorageList;
  if (!storageList) return [];

  var list = [];

  var projects = q.Projects;
  projects.forEach(x => {
    var index = storageList.findIndex(y => y.ModelItem === x);
    if (index !== -1) {
      list.push(storageList[index]);
    }
  })

  return list;
}

export const Get_PhaseSelectVM = (q) => {
  var qI = new MultiSelectVM();
  qI.Placeholder = 'chn version'
  qI.OnSelectedItemChanged = (v) => q.PhaseVMs = v;
  qI.TitleRender = (v) => v.Name

  const setList = (data) => {
    qI.Items = data
  }

  var host = pmDataVM().CurrentProject.Phase_DictVM;
  if (host.Items) {
    setList(host.Items)
  } else {
    genUtil.Add_2(host.OnSet_Items_Func,
      () => setList(host.Items));
  }

return qI;
}

