import { Button, Popover } from 'antd'
import { EntPopover } from './EntPopover'

// Instance
export const get = (dict) => {
  var qI = new EntPopover();

  qI.Dict = dict;

  return qI;
}

// Property
export const GetDefault_TitleRender = (q) => {
  return () => 'Title'
}

export const GetDefault_ContentRender = (q) => {
  return () => 'Content'
}