import { Component } from "react";
import { observer } from 'mobx-react';
import { Item } from "./Item";
import './Item.css'

class ItemView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Item();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.Items ?
          <div>
            {data.MainView}

            {data.Is_Sub_Visible ?
              <div className={data.Sub_WrapperCss}>
                {data.Items.map(x => x.View)}
              </div>
              : ''}
          </div>
          : data.MainView}
      </>
    )
  }
}

export default observer(ItemView)