import * as itemUtil from '../item/PhaseUtil'
import * as util from './Phase_DictUtil'
import Project from '../../project/base/Project'

import * as genUtil from '../../../../../util/generalUtil'

export class Phase_Dict {
  // Dict
  Dict

  get Project() {
    return this.Dict instanceof Project ? 
      this.Dict : undefined
  }

  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items;
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var item = itemUtil.get(this);
    item.Is_FirstCreated = true;

    this.Items.push(item);
    return item;
  }

  GetItem = (phaseId, handle) => {
    const setObj = (data) => {
      var list = data;
      var item = genUtil.SingleFilter(list, x => x._id === phaseId);
      handle(item);
    }

    var host = this;
    if (host.Items){
      setObj(host.Items)
    } else {
      genUtil.Add_2(host.OnSet_Items_Func, () => setObj(host.Items));
    }
  }
}