import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pvt from '../../../project/base/ProjectViewType'

export const pmDataVM = () => PMDataVM.Instance;

// Property
export const GetTitle = (viewType) => {
  var title;

  switch (viewType) {
    case pvt.mass:
      title = 'MASS';
      break;

    // MassType
    case pvt.masstype:
      title = 'U MC CNG VIC';
      break;
    case pvt.mt_list:
      title = 'DANH SCH';
      break;
    case pvt.mt_category_binding:
      title = 'THEO CATEGORY';
      break;
    case pvt.mt_material_binding:
      title = 'THEO VT LIU';
      break;
    case pvt.mt_system_binding:
      title = 'THEO H THNG';
      break;

    // Level
    case pvt.level:
      title = 'LEVEL';
      break;
    case pvt.mappinglevel:
      title = 'MAPPING';
      break;
    case pvt.typicallevel:
      title = 'TYPICAL';
      break;

    // ExportSet
    case pvt.exportset:
      title = 'EXPORT SET'
      break;
    case pvt.phase:
      title = 'VERSION';
      break;
    case pvt.inputdata:
      title = 'INPUT DATA';
      break;

    case pvt.category:
      title = 'CATEGORY'
      break;

    case pvt.parameter:
      title = 'PARAMETER'
      break;

    case pvt.massexport:
      title = 'MASS EXPORT'
      break;

    case pvt.project:
      title = 'PROJECT'
      break;
  }

  return title;
}

export const GetValues = (viewType) => {
  var obj

  if (viewType == null) {
    obj = pmDataVM().CurrentProject ?
      [
        pvt.mass,
        pvt.level,
        pvt.exportset,
        pvt.phase,
        pvt.inputdata
      ] :
      [
        pvt.mass,
        pvt.masstype, 
        pvt.category, 
        pvt.parameter, 
        pvt.massexport,
        pvt.project
      ]
  } else {
    switch (viewType) {
      case pvt.masstype:
        obj = [
          pvt.mt_list,
          pvt.mt_material_binding,
          pvt.mt_category_binding,
          pvt.mt_system_binding
        ]
        break;
      case pvt.level:
        obj = [
          pvt.mappinglevel,
          pvt.typicallevel
        ]
    }
  }

  return obj;
}