import { makeObservable, observable, action } from 'mobx';
import * as util from './LevelSettingDataVMUtil'
import { Button, message } from 'antd';
import * as itemUtil from '../item/LS_ItemVMUtil'
import * as rs from '../../../../../text/TextRenderState';
import { PMDataVM } from '../../../../single/form/PMDataVM';
import TableView from '../table/TableView';
import * as genUtil from '../../../../../../util/generalUtil'

const pmDataVM = () => PMDataVM.Instance

export class LevelSettingDataVM {
  constructor() {
    makeObservable(this, {
      items: observable,
    })
  }

  items = undefined
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this);
    }
    return this.items;
  } set Items(v) {
    this.items = v;
  }

  get Project() {
    return this.Dict.Project
  }

  AddItem = () => {
    var item = itemUtil.get(this.ModelItem.AddItem(), this);

    // item.NameVM.RenderState = rs.edit

    this.Items.push(item);
    // this.Table.AddItem(item);
    return item;
  }

  get Project() {
    return this.Dict.Project
  }

  Update_LevelData = () => {
    this.ModelItem.Update_LevelData();
    message.success('save successfully!');
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  // Move
  Move = (i1, i2) => {
    var list = this.Items;
    genUtil.MoveItem(list, i1, i2);

    this.ModelItem.Move(i1, i2);
  }

  Insert = (i1, i2) => {
    var list = this.Items;
    genUtil.InsertItem(list, i1, i2);

    this.ModelItem.Insert(i1, i2);
  }

  OnDragEnd = (res) => {
    if (!res.destination) return;

    var i1 = res.source.index;
    var i2 = res.destination.index;

    this.Insert(i1,i2);
  }
}