import { TLS_Filter } from "./TLS_Filter";
import { TLS_FilterValue } from "./TLS_FilterValue";
import * as valueUtil from './TLS_FilterValueUtil'

export const get = (data, dict) => {
  var qI = new TLS_Filter();
  qI.Field = data.field;

  if (data.items) {
    qI.Items = data.items.map(x => {
      var obj = new TLS_FilterValue();
      obj.Value = x.value;
      obj.InputType = x.inputType
      return obj;
    })
  } else {
    qI.Items = data.values.map(x => {
      var obj = new TLS_FilterValue();
      obj.Value = x;
      return obj;
    })
  }

  qI.Dict = dict;
  return qI;
}

export const GetDefault = (dict) => {
  var qI = new TLS_Filter();
  qI.Dict = dict;
  return qI;
}

export const Clone = (q, dict) => {
  var qI = new TLS_Filter();

  qI.Field = q.Field;
  qI.Items = q.Items.map(x => valueUtil.Clone(x));
  qI.Dict = dict ? dict : q.Dict;

  return qI;
}

// Method
export const IsEqual = (q1, q2) => {
  if (q1.Field !== q2.Field) return false;

  var i1 = q1.Items;
  var i2 = q2.Items;
  if (i1.length !== i2.length) return false;

  var c_i2 = [...i2];
  var isValid = true;

  i1.forEach(x => {
    if (isValid) {
      var index = c_i2.findIndex(y => x.IsEqual(y));
      if (index !== -1) {
        c_i2.splice(index, 1);
      } else {
        isValid = false;
      }
    }
  });

  return isValid;
}