import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './ExportSet_DictUtil'
import * as iUtil from '../../item/base/ExportSetUtil'
import * as erf from '../../item/base/ExportSet_RetrieveFrom'

export class ExportSet_Dict {
  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Project
  Project

  UseType

  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  // AddItem = (v) => {
  //   this.Items.push(v);
  //   if (this.OnAddItem){
  //     this.OnAddItem(v)
  //   }
  // }

  AddItem = (retrieveFrom) => {
    var item = iUtil.get(this);
    item.Name = 'Tn bng'
    item.RetrieveFrom = retrieveFrom
    item.SaveDatabase();
    
    this.Items.push(item);
    return item;
  }

  GetItem = (exportSetId, handle) => {
    const setObj = (data) => {
      var list = data;
      var item = genUtil.SingleFilter(list, x => x._id === exportSetId);
      handle(item);
    }

    var host = this;
    if (host.Items){
      setObj(host.Items)
    } else {
      genUtil.Add_2(host.OnSet_Items_Func, () => setObj(host.Items));
    }
  }

  Get_AddItem = (data, handle) => {
    const setObj = (list) => {
      var item = genUtil.SingleFilter(list, x => x._id === data._id);

      if (!item){
        item = iUtil.GetFromDb(data, this);
        list.push(item);
      }
      handle(item);
    }

    var host = this;
    if (host.Items){
      setObj(host.Items)
    } else {
      genUtil.Add_2(host.OnSet_Items_Func, () => setObj(host.Items));
    }
  }

  CurrentItem
}