import { PMData } from "../../../single/model/PMData"
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './LevelDictUtil'

export const pmData = () => PMData.Instance;

export class LevelDict {
  OnSet_Items_Func = []

  items
  get Items(){
    if (!this.items){
      util.Handle_Items(this)
    } return this.items;
  } set Items(v){
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  OnSet_Undefined_Item_Func = []

  undefined_Item;
  get Undefined_Item(){
    if (!this.undefined_Item){
      util.Handle_Undefined_Item(this)
    } return this.undefined_Item
  } set Undefined_Item(v){
    this.undefined_Item = v;

    genUtil.Handle(this.OnSet_Undefined_Item_Func,
      (v) => this.OnSet_Undefined_Item_Func = v)
  }

  GetLevel = (name, handle) => {
    util.GetLevel(this, name, handle)
  }
}