import { PMDataVM } from '../../../../single/form/PMDataVM';
import { TypicalLevelSettingDataVM } from './TypicalLevelSettingDataVM';
import * as pageItemUtil from '../pageItem/TLS_PageItemVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'

export const pmDataVM = () => PMDataVM.Instance;

// Instance
export const get = (q, dict) => {
  var qI = new TypicalLevelSettingDataVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  genUtil.Add_2(
    dict.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );

  return qI;
}

// Property
export const GetPageItems = (q) => {
  return q.ModelItem.PageItems.map(x => pageItemUtil.get(x, q));
}

export const GetLevelStorageList = (q) => {
  return q.Dict.LevelStorageList
}

export const GetCurrentPageKey = (q) => {
  return q.PageItems.length !== 0 ? q.PageItems[0].id : undefined
}