import { SelectVM } from '../../../../select/SelectVM';
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as itemUtil from '../item/PhaseVMUtil'
import { Phase_DictVM } from "./Phase_DictVM";
import * as genUtil from '../../../../../util/generalUtil'
import { MultiSelectVM } from '../../../../select/MultiSelectVM';

const pmDataVM = () => PMDataVM.Instance

export const get = (q) => {
  var qI = new Phase_DictVM();

  qI.ModelItem = q;

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (v) => {
    q.Items = v.map(x => itemUtil.get(x, q));
    q.IsGet_Items = false;
  }

  var qI = q.ModelItem;
  if (qI.Items && qI.Items.length !== 0) {
    setObj(qI.Items);
  }

  genUtil.Add_2(qI.OnSet_Items_Func,
    () => setObj(qI.Items), false);
}

export const GetSelectVM = (q) => {
  var qI = new MultiSelectVM();
  q.Placeholder = "chn version";

  const setObj = () => {
    var list = qI.Items = q.Items;
    qI.TitleRender = (item) => item.Name;

    qI.OnSelectedItemChanged = (v) => {
      pmDataVM().ProjectComp.ProjectVM = v;
    }

    qI.SetValue(list[0]);
  }

  if (q.Items) {
    setObj();
  } else {
    genUtil.Add_2(q.OnSet_Items_Func,
      setObj)
  }

  return qI;
}