import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import MassItem_Dict from "./MassItem_Dict";
import { Spin } from "antd";
import TextView from "../../../../../text/TextView";
import './MassItem_Dict.css'

class MassItem_DictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassItem_Dict();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.IsDataPending ?
          <div className='f-jc'>
            <div>
              <Spin />
              <TextView store={data.LoadingSpinTipVM} />
            </div>
          </div> 
          :
          <div className='f'>
            {data.MassTable?.View}
            <div className='massdict__forgewrapper f1'>
            {data.Forge?.View}
            </div>
          </div>
        }
      </>
    )
  }
}

export default observer(MassItem_DictView)