import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Divider, Input, Select, Spin } from "antd";
import MultiSelectView from '../../../../../select/MultiSelectView'
import CheckboxView from "../../../../../checkbox/CheckboxView";
import { PMDataVM } from "../../../../single/form/PMDataVM";
import MassItem_DictView from "../../item/base/MassItem_DictView";
import * as comUtil from '../../../../../../util/commandUtil'
import * as pct from '../../../command/ProjectCommand'

const { Option } = Select;

export const pmDataVM = () => PMDataVM.Instance;

class MassResultCompView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = pmDataVM().MassResultCompVM
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.IsShowProjectSelect ?
          <MultiSelectView store={data.ProjectSelectVM} /> : ''}

        {comUtil.Validate([pct.view, pct.edit]) ?
          <>
            {data.CanHavePhase ?
              <>
                <CheckboxView store={data.IsShowPhaseSelectVM} />
                {data.IsHavePhase ?
                  <MultiSelectView store={data.PhaseSelectVM} />
                  : ''}
              </>
              : ''}

            <div style={{ marginTop: 8 }} >
              {pmDataVM().Mgs_Dict_Dict.Pick_MGS_View}
              {pmDataVM().Mgs_Dict_Dict.ModalView}
            </div>

            <Button style={{ marginTop: 8 }} onClick={data.GetMassData}
              size='middle' type='primary'>GET DATA</Button>
            <div style={{ marginTop: 15 }}>
              <MassItem_DictView store={data.MassItem_Dict} />
            </div>
          </>
          : ''}
      </>
    )
  }
}

export default observer(MassResultCompView)