import { makeObservable, observable, action } from 'mobx';
import * as util from './BaseVMUtil'

export class BaseVM {
  listData
  get ListData(){
    if (!this.listData){
      this.listData = util.GetListData(this)
    } return this.listData
  }

  categoryBindingData
  get CategoryBindingData(){
    if (!this.categoryBindingData){
      this.categoryBindingData = util.GetCategoryBindingData(this)
    } return this.categoryBindingData
  }

  materialBindingData
  get MaterialBindingData(){
    if (!this.materialBindingData){
      this.materialBindingData = util.GetMaterialBindingData(this)
    } return this.materialBindingData
  }

  systemBindingData
  get SystemBindingData(){
    if (!this.systemBindingData){
      this.systemBindingData = util.GetSystemBindingData(this)
    } return this.systemBindingData
  }
}