import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as util from './ItemVMUtil'
import './Item.css'
import * as rs from '../../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../../text/EditableTextView';
import * as genUtil from '../../../../../../../util/generalUtil'
import * as materialUtil from '../material/MaterialVMUtil'

export class ItemVM {
  constructor() {
    makeObservable(this, {
      materials: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    // this.NameVM.RenderState = v;

    if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // Name
  get MassType() {
    return this.ModelItem.MassType
  } set MassType(v) {
    this.ModelItem.MassType = v
  }

  massType_SelectVM
  get MassType_SelectVM() {
    if (!this.massType_SelectVM) {
      this.massType_SelectVM = util.GetMassType_SelectVM(this)
    } return this.massType_SelectVM;
  }

  get MassTypeWrapper_View() {
    return <div className='mt__cell__wrapper'>
      {this.MassType_SelectVM.View}

      <Button type='primary' size='small'
        className='btn-success mt__row__add'
        onClick={this.AddMaterial}>
        +
      </Button>

      {this.DeleteVM.View}
    </div>
  }

  get RowSpan() {
    return this.Materials.length > 1 ? this.Materials.length : 1
  }

  // Materials
  materials = undefined
  get Materials() {
    if (!this.materials) {
      this.materials = util.GetMaterials(this)
    } return this.materials;
  }

  get NotFirst_Materials() {
    var list = [...this.Materials];
    list.splice(0, 1);
    return list;
  }

  AddMaterial = () => {
    var q = this.ModelItem.AddMaterial();
    var qI = materialUtil.get(q, this);
    qI.RenderState = rs.edit;

    this.Materials.push(qI)
    return qI;
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return <>
      <tr className='mt__row'>
        <td rowSpan={this.RowSpan}>{this.MassTypeWrapper_View}</td>
        {/* First Item */}
        {this.Materials.length > 0 ?
          <td>
            {this.Materials[0].RowView}
          </td>
          : ''}
      </tr>
      {this.Materials.length > 1 ?
        this.NotFirst_Materials.map(x => {
          return <tr key={x.id} className='mt__row'>
            <td>{x.RowView}</td>
          </tr>
        })
        : ''}
    </>
  }

  // Save
  Save = (showMessage = true, handle) => {
    this.Materials.forEach(x => {
      x.Name = x.NameVM.Content
    })

    this.SaveDatabase(showMessage, handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}