import { LinkOutlined } from '@ant-design/icons';
import { makeObservable, observable, action } from 'mobx';
import * as mif from '../../../field/MassItemField'
import * as util from './ViewDetailUtil'
import * as api from '../../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../../util/generalUtil'

export class ViewDetail {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  Dict

  get ApiData() {
    var host = this.Dict;
    var phaseId
    if (host.Table.ColumnField === mif.Phase) {
      phaseId = host.Column.Data._id;
    }

    var massIds = host.AllItems.map(x => x._id);
    return {
      massIds,
      phaseId
    };
  }

  Open = () => {
    const func = () => {
      var isHash = true;
      var url = `${process.env.PUBLIC_URL}${isHash ? '/#' : ''}/massdetail`;
      var getUrl = (v) => `${url}/${v}`;
      var openTabFunc = (v) =>
        window.open(getUrl(v), "_blank");

      var func = () => {
        openTabFunc(this._id);
      }

      if (this._id) {
        func()
      } else {
        var apiData = this.ApiData;
        util.GetData(apiData, (data) => {
          this._id = data._id;
          func();
        })
      }
    }

    var cell = this.Dict;
    if (cell.Items){
      func();
    } else {
      cell.GetItems_FromApi();
      genUtil.Add_2(cell.OnSet_Items_Func,
        func);
    }
  }

  // View
  get View() {
    return <a className='ml-10' onClick={this.Open}>
      <LinkOutlined />
    </a>
  }
}