import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import TableView from '../../table/TableView';
import * as iUtil from '../../item/ItemVMUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as comUtil from '../../../../../../../util/commandUtil'
import * as pct from '../../../../command/ProjectCommand'
import * as ut from './Dict_UseType'
import { Button } from 'antd';

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  addData
  get AddData() {
    if (!this.addData) {
      this.addData = util.GetAddData(this)
    } return this.addData;
  }

  get UseType() {
    return this.ModelItem.UseType
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get TableWrapper_View() {
    return <div className='sg__table__wrapper f'>
      {this.UseType === ut.partial && comUtil.Validate(pct.edit)
        ? this.AddData.View : ''}
      <TableView store={this} />
    </div>
  }
}