export const pmData = () => {
  return PMData.Instance;
}

export const get = (col, row, dict) => {
  var qI = genUtil.SingleFilter(row.Cells, (x) => x.Column === col)
  col.Cells.push(qI)

  return qI;
}

// Property
// p dng cho CreateType = byitem
export const GetItems = (q) => {
  if (q.CreateType === mc_ct.bycell) {
    return undefined
  }

  return []
}

// p dng cho CreateType = bycell
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  var column = q.Column;

  const setObj = (data) => {
    q.Items = data[0].masses.map(x => {
      var qI = massItemUtil.get(x)
      column.MassItem_Handle(qI);
      return qI;
    });
    q.IsGet_Items = false;
  }

  const getApi = () => {
    var queryData = {}
    var apiData = {
      queryData
    }

    // q.Row.QueryData.AddQuery(queryData);
    q.QueryData.AddQuery(queryData)
    column.QueryData.AddQuery(apiData, 'list');

    dataUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}

// color data
