// Method
// Save
export const Save = (q, mainHandle) => {
  // valuedata
  var vd = q.ValueData;

  // Save thng tin Info (Key, FullKey) - Query (Field-Value) - ValueData 
  const saveQuery = () => {
    q.QueryData.SaveDatabase(false)
  }

  const saveValue = () => {
    // var vd = q.ValueData;
    const s_vd = () => {
      vd.Save();
    }

    s_vd();

    // if (vd.Data) {
    //   s_vd();
    // } else {
    //   genUtil.Add_2(vd.OnSet_Data_Func,
    //     s_vd);
    // }
  }

  const saveInfo = (handle) => {
    q.SaveDatabase(false, () => {
      if (handle) {
        handle();
      }

      // Sau khi ly Id -> lu query v value
      saveQuery();
      saveValue();
    });
  }

  // find fullkey database, kim tra thng tin c tn ti hay khng
  const find = (handle) => {
    var apiData = {
      fullkey: q.Row.FullValue
    }

    var column =q.Column;
    column.QueryData.AddQuery(apiData);
    apiData.reportId = column.Report._id

    const apiFunc = () => api.post(`${url}/find`, apiData);
    apiFunc().then(res => res.data).then(data => {
      data = data.length === 1 ? data[0] : undefined
      handle(data)
    })
  }

  // Hm x l chnh
  const main = () => {
    // Khng c gi tr, ta b qua lu vt ct ny
    if (vd.Data.length === 0){
      mainHandle();
      return;
    }

    find(data => {
      if (!data) {
        saveInfo(mainHandle);
      } else {
        q._id = data._id;

        if (mainHandle) {
          mainHandle();
        }

        saveValue();
      }
    })
  }

  // process
  if (vd.Data) {
    main();
  } else {
    genUtil.Add_2(vd.OnSet_Data_Func, main);
  }
}