import { makeObservable, observable, action } from 'mobx';
import * as util from './ViewUtil'
import TableView from '../../table/TableView';
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pct from '../../../command/ProjectCommand'
import * as erf from '../../item/base/ExportSet_RetrieveFrom'

const pmDataVM = () => PMDataVM.Instance

export class View {
  constructor() {
    makeObservable(this, {
      currentItem: observable,
      Is_Modal_Visible: observable,
    })
  }

  RetrieveFrom

  // Item
  get Items() {
    return util.GetItems(this);
  }

  TempCurrentItem

  currentItem = undefined
  get CurrentItem() {
    return this.currentItem;
  } set CurrentItem(v) {
    this.currentItem = v;
  }

  addVM
  get AddVM() {
    if (!this.addVM) {
      this.addVM = util.GetAddVM(this)
    } return this.addVM;
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }

  // View
  get Table_WrapperView() {
    return <div className='f'>
      {this.Commands.includes(pct.edit) ?
        <div className='es__add'>
          {this.AddVM.View}
        </div>
        : ''}
      <div className='es__main f1'>
        <TableView store={this} />
      </div>
    </div>
  }

  // Modal
  Is_Modal_Visible = false

  get ModalView() {
    return util.GetModalView(this)
  }
  
  // Pick
  get PickView() {
    return util.GetPickView(this)
  }

  get View() {
    switch (this.RetrieveFrom) {
      case erf.model:
        return this.Table_WrapperView
      case erf.input:
        return <>
          <div className='es__wrapper'>
            <div className='es__pick__wrapper'>
              <div className='f'>
                {this.PickView}
              </div>
              {this.ModalView}
            </div>
            <div className='es__render__wrapper'>
              {this.CurrentItem &&
                this.CurrentItem.MassItems_Table ?
                this.CurrentItem.MassItems_Table.WrapperView
                : ''}
            </div>
          </div>
        </>
    }
  }
}