import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../util/generalUtil'
import * as util from './EntPopoverUtil'
import EntPopoverView from './EntPopoverView';
import * as tt from './EntPopover_TriggerType'

export class EntPopover {
  constructor() {
    makeObservable(this, {
      is_Content_Visible: observable,
      Trigger: observable
    })
  }

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.newGUID();
    } return this._id;
  } set id(v) {
    this._id = v;
  }

  titleRender
  get TitleRender() {
    if (!this.titleRender) {
      this.titleRender = util.GetDefault_TitleRender(this)
    } return this.titleRender
  } set TitleRender(v) {
    this.titleRender = v;
  }

  get Title() {
    return this.TitleRender();
  }

  contentRender
  get ContentRender() {
    if (!this.contentRender) {
      this.contentRender = util.GetDefault_ContentRender(this)
    } return this.contentRender
  } set ContentRender(v) {
    this.contentRender = v;
  }

  get Content() {
    return this.ContentRender();
  }

  get WrapperStyle() {
    return this.Is_Content_Visible ?
      { zIndex: 100 } : undefined
  }

  // Trigger
  Trigger = tt.hover

  is_Content_Visible = false
  get Is_Content_Visible() {
    return this.is_Content_Visible
  } set Is_Content_Visible(v) {
    if (this.PreSet_Is_Content_Visible) {
      this.PreSet_Is_Content_Visible(v)
    }

    this.is_Content_Visible = v;
    if (v) {
      this.Init_ClickOutside = true;
      document.addEventListener('click', this.OnClickOutside);
    } else {
      document.removeEventListener('click', this.OnClickOutside);
    }

    if (this.OnSet_Is_Content_Visible) {
      this.OnSet_Is_Content_Visible(v)
    }
  }

  ShowContent = () => {
    this.Is_Content_Visible = true
  }

  HideContent = () => {
    this.Is_Content_Visible = false
  }

  ToggleContent = () => {
    this.Is_Content_Visible = !this.Is_Content_Visible
  }

  OnClickOutside = (e) => {
    if (this.Trigger === tt.click) {
      if (this.IsCancel){
        this.IsCancel = false
        return;
      }
      if (this.Init_ClickOutside) {
        this.Init_ClickOutside = false;
        return;
      }

      if (this.ClickRef) {
        var current = this.ClickRef.current;
        if (!current) return;

        var target = e.target;
        var isInside = false
        const func = (p, c) => {
          if (p.contains(c)) {
            isInside = true;
          } else {
            Array.from(p.children).forEach(x => func(x, c))
          }
        }

        func(current, target)

        if (!isInside) {
          this.Is_Content_Visible = false
        }
      }
    }
  }

  // View
  get View() {
    return <EntPopoverView key={this.id} store={this} />
  }
}