import { MassTypeExport } from '../masstypeexport/MassTypeExport';
import { MassInfo } from './MassInfo'
import * as massTypeExportUtil from '../masstypeexport/MassTypeExportUtil'
import * as valueInfoUtil from '../valueinfo/ValueInfoUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  
  qI.MassTypeExport = massTypeExportUtil.GetFromDb(data.massType, qI);
  qI.ClassifyValueBy = data.classifyValueBy
  qI.ValueInfos = data.valueInfos.map(x => valueInfoUtil.GetFromDb(x, qI))

  return qI;
}

export const get = (dict) => {
  var qI = new MassInfo();

  qI.Dict = dict;
  
  return qI;
}

// Property
export const GetMassTypeExport = (q) => {
  var qI = new MassTypeExport();
  qI.Dict = q;
  return qI;
}

// api
