import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { ExportSet_DictVM } from '../dict/base/ExportSet_DictVM';
import './Table.css'
import TableItemView from './TableItemView';
import * as erf from '../item/base/ExportSet_RetrieveFrom'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ExportSet_DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered'>
          <thead className='es__thead'>
            <tr>
              <th className='es__table__name'>Tn</th>

              {data.RetrieveFrom === erf.input ?
                <th className='es__table__masstype'>Cng tc</th>
                : ''}

              <th className='es__table__description'>
                {data.RetrieveFrom === erf.input ?
                  "M t" : "Thng tin"}
              </th>

              <th className='es__table__command'></th>
            </tr>
          </thead>
          <tbody>
            {data.Items?.map(x => {
              return <TableItemView key={x.id} store={x} />
            })}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
