import { message } from 'antd';
import * as mif from '../../../../field/MassItemField'
import {url} from './Item_Url'
import * as genUtil from '../../../../../../../../util/generalUtil'

export class Item {
  Dict

  get Cell() {
    return this.Dict.Cell;
  }

  get Row(){
    return this.Cell.Row;
  }

  get ApiData(){
    var obj = {
      hostId : this.Dict._id,
      cellIds : this.Cell.HaveValue_SubCells.map(x => x._id)
    }

    return obj;
  }

  Save = (handle) => {
    this._id = undefined

    this.SaveDatabase(false, handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }

        if (handle) {
          handle();
        }
      });
  }
}