import Client from '../Client';
import NestedViewerExtension from "./extensions/NestedViewerExtension";
import SetGhostingExtension from './extensions/SetGhostingExtension';

const Autodesk = window.Autodesk;
var viewer;
var getToken = { accessToken: Client.getaccesstoken() };

export const launchViewer = (q, div, urns, handle) => {
  getToken.accessToken.then((token) => {
    var options = {
      accessToken: token.access_token,
    };
    Autodesk.Viewing.Initializer(options, () => {
      viewer = new Autodesk.Viewing.GuiViewer3D(document.getElementById(div), {
        extensions: [
          "Autodesk.AEC.LevelsExtension",
          "Autodesk.AEC.Minimap3DExtension",
          "Autodesk.DocumentBrowser",
          "Autodesk.MemoryLimited"
        ],
      });

      // Start View
      viewer.start();

      // Spinner chy trc khi start View
      replaceSpinner();

      // debugger
      urns.map((urn) => {
        Autodesk.Viewing.Document.load(`urn:${urn}`, (doc) => {
          var viewables = doc.getRoot().getDefaultGeometry();
          Autodesk.Viewing.Document.getAecModelData(doc.getRoot())
            .then(aec => console.log('AEC metadata', aec));

          viewer
            .loadDocumentNode(doc, viewables, {
              keepCurrentModels: true,
              globalOffset: { x: 0, y: 0, z: 0 },
              applyRefPoint: true,
              isAEC: true,
            })
            .then(() => {
              onDocumentLoadSuccess(doc, handle)
            });
        });
      });
      loadExtension();
    });
  });
}

const onDocumentLoadSuccess = (doc, handle) => {
  viewer.setGroundReflection(false);
  viewer.setGroundShadow(false);
  viewer.setOptimizeNavigation(true);
  loadExtension();

  // Handle
  if (handle) {
    handle(true);
  }
}

const onDocumentLoadFailure = (viewerErrorCode, viewerErrorMsg) => {
  console.error('onDocumentLoadFailure() - errorCode:' + viewerErrorCode + '\n- errorMessage:' + viewerErrorMsg);
}

// Load Extension
const replaceSpinner = () => {
  var spinners = document.getElementsByClassName("forge-spinner");
  if (spinners.length === 0) return;
  var spinner = spinners[0];
  spinner.classList.remove("forge-spinner");
  spinner.classList.add('lds-ring');
  spinner.innerHTML = '<div></div>';
}

const loadExtension = () => {
  viewer.loadExtension(NestedViewerExtension.ExtensionId, {
    filter: ["2d", "3d"],
    crossSelection: true,
  });
  viewer.loadExtension(SetGhostingExtension, {
    filter: ["2d", "3d"],
    crossSelection: true,
  });
  viewer.loadExtension("Autodesk.VisualClusters", {
    attribName: "Category",
    searchAncestors: false,
  });
}

export const showAllElement = () => {
  // viewer.isolate();// Dng cho model n
  viewer.impl.visibilityManager.aggregateIsolate([]); //Dng cho multiple models
}

export const getElementWithExternalId = (mapIds) => {
  var allModels = viewer.impl.modelQueue().getModels();
  var DBids = allModels.map((a) => {
    return getAllDbIdsMultipleModel(a);
  });

  for (let i = 0; i < allModels.length; i++) {
    getSubsetModel(allModels[i], DBids[i], mapIds, (dbId) => {
      if(dbId.length===0){
        viewer.impl.visibilityManager.isolate([0], allModels[i]);
      }else{
        viewer.impl.visibilityManager.isolate(dbId, allModels[i]);
      }
    });
  }
}

const getSubsetModel = (model, dbIds, values, callback) => {
  var newDbIds = [];
  model.getBulkProperties(
    dbIds,
    {
      propFilter: ["externalId"],
      ignoreHidden: true,
    },

    function (data) {
      for (var key1 in values) {
        var value = values[key1];
        for (var key in data) {
          var item = data[key];
          if (item.externalId === value) {
            newDbIds.push(item.dbId);
          }
        }
      }
      callback(newDbIds);
    },
    function (error) {}
  );
}

// Mi ln load model, load tp hp dbIds mi
const getAllDbIdsMultipleModel = (model) => {
  var instanceTree = model.getInstanceTree();

  var allDbIdsStr = Object.keys(instanceTree.nodeAccess.dbIdToIndex);

  return allDbIdsStr.map(function (id) {
    return parseInt(id);
  });
}