import { Filter } from './Filter'
import * as massInfoUtil from '../../massinfo/base/MassInfoUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  qI.IsByCategory = data.isByCategory !== undefined ? data.isByCategory : true
  qI.Categories = data.categories

  return qI;
}

export const get = (dict) => {
  var qI = new Filter();

  qI.Dict = dict;

  return qI;
}

