import { PMData } from "../../../../single/model/PMData";
import MassItem_Dict from "../../item/base/MassItem_Dict"

const pmData = () => PMData.Instance

// Property
export const GetMassItem_Dict = (q) => {
  var qI = new MassItem_Dict();
  qI.Dict = q;
  return qI;
}

export const GetMassGroupField_Dict = (q) => {
  return q.Projects.length === 0 && !q.IsHavePhase ?
    pmData().MIF_Dict_ForMassGroup : pmData().MIF_Dict_ForInput
}

// Method
export const GetApiData = (q) => {
  var skip;
  var take;

  var obj = {
    projectIds :  pmData().CurrentProject ? 
      [ pmData().CurrentProject._id ] : q.Projects.map(x => x._id),
    phaseIds : q.IsHavePhase ? q.Phases.map(x => x._id) : undefined,
    otherData : {
      skip, take,
      massGroupId : pmData().Mgs_Dict_Dict.Current_MGS?._id
    }
  }
  
  return obj;
}