import { MassTypeExport } from './MassTypeExport'
import * as mte_t from './MTE_Type'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  qI.ExportType = data.retrieveType;

  switch (data.retrieveType) {
    case mte_t.byname:
      qI.MassType = data.name
      break
    default:
      qI.MassType = data.defaultValue
      break
  }

  return qI;
}

export const get = (dict) => {
  var qI = new MassTypeExport();

  qI.Dict = dict;

  return qI;
}

