import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './DictUtil'
import * as iUtil from '../item/page/PageItemUtil'
import {url} from './Dict_Url'
import { message } from 'antd'

export class Dict {
  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = iUtil.get(this)
    this.Items.push(q)
    return q;
  }

  // Api
  get ApiData() {
    return {
      _id : this._id,
      pageItems : this.Items.map(x => x.ApiData)
    }
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }
}