import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { DictVM } from '../dict/DictVM';
import './Table.css'
import TableItemView from './TableItemView';
import TabView from '../../../tab/TabView';

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered'>
          <thead className='mt__thead'>
            <tr>
              <th className='mt__table__masstype'>u mc cng vic</th>
              <th className='mt__table__classification'>System Classification</th>
              <th className='mt__table__type'>System Type</th>
            </tr>
          </thead>
          <tbody>
            {data.Items ? data.Items.map(x => {
              return <TableItemView key={x.id} store={x} />
            }) : <tr />}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
