import { Save } from "./Save"
import * as genUtil from '../../../../../../util/generalUtil'
import * as mdsUtil from "../item/MassDetailSettingUtil";
import { PMData } from "../../../../single/model/PMData";
import { PMDataVM } from "../../../../single/form/PMDataVM";

export const pmData = () => PMData.Instance;
export const pmDataVM = () => PMDataVM.Instance;

// Instance
export const get = (dict) => {
  var qI = new Save();
  qI.Dict = dict;
  return qI;
}

// Property


// Method
export const Run = (q) => {
  var host = q.Dict;

  var setObj = () => {
    var items = host.Items
    var name = q.Name

    var item = genUtil.SingleFilter(items, (x) => x.Name === name);
    if (!item) {
      item = mdsUtil.get(host);
      item.Name = name;
      // items.push(item);
      host.AddItem(item);
    }

    item.Data = item.Dict.Dict.Table.ApiData
    item.SaveDatabase(true);
  }

  if (host.Items) {
    setObj()
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      setObj
    )
  }
}