import { LevelGroup } from "./LevelGroup"

// Instance
export const get = (data, dict) => {
  var qI = new LevelGroup();

  if (Array.isArray(data)) {
    qI.Levels = data;
  } else {
    qI.Levels = data.levels;
    qI.Primary = data.primary
  }

  qI.Dict = dict;
  return qI;
}

export const GetDefault = (dict) => {
  var qI = new LevelGroup();
  qI.Dict = dict;
  return qI;
}

export const Clone = (q, dict) => {
  var qI = new LevelGroup();

  qI.Levels = q.Levels;
  qI.Primary = q.Primary;
  qI.Dict = dict ? dict : q.Dict;

  return qI;
}

// Property
export const GetDefaultPrimary = (q) => {
  if (q.Levels && q.Levels.length !== 0) {
    return q.Levels[0];
  }
}

export const GetPrimary_Values = (q) => {
  var primary = q.Primary;
  var list = q.Setting.Binding.Items;

  var index = list.findIndex(x => x.Name === primary);
  return index !== -1 ? list[index].Values : []
}

// Method
export const IsEqual = (q1, q2) => {
  if (q1.Primary !== q2.Primary) return false;

  var l1 = q1.Levels;
  var l2 = q2.Levels;
  if (l1.length !== l2.length) return false;

  var c_l2 = [...l2];
  var isValid = true;

  l1.forEach(x => {
    if (isValid) {
      var index = c_l2.findIndex(y => x === y);
      if (index !== -1) {
        c_l2.splice(index, 1);
      } else {
        isValid = false;
      }
    }
  });

  return isValid;
}