import { Dict_Dict } from './Dict_Dict'
import * as iUtil from '../base/DictUtil'
import * as i_ut from '../base/Dict_UseType'
import * as dataUtil from '../../item/ItemUtil'
import * as api from '../../../../../../../util/apiUtil'
import {url} from '../../item/Item_Url'

// Instance
export const GetFromDb = (data) => {
  var qI = get();
  
  qI._id = data._id
  qI.Data = data.data.map(x => dataUtil.GetFromDb(x, qI))

  return qI;
}

export const get = () => {
  var qI = new Dict_Dict();
  return qI;
}

// Property
export const GetItems = (q) => {
  return [
    iUtil.get(i_ut.partial, q), iUtil.get(i_ut.all, q)
  ]
}

// Method
export const Initial = (q) => {
  q.Items.forEach(x => {
    x.Refresh_Items()
  })
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}