import { Query } from "./Query"
import * as itemUtil from '../../row/query/item/ItemUtil'

// Instance
export const get = (dict) => {
  var qI = new Query();
  qI.Dict = dict;
  return qI;
}

// property
export const GetItems = (q) => {
  var qI = itemUtil.get(q);
  qI.field = q.Field;
  qI.valueId = q.valueId;

  return [qI];
}

export const Get_AllItems = (q) => {
  var list = [q.Items]
  if (q.Is_Inherit_FromSup) {
    var cell = q.Cell
    if (cell.SupItem) {
      list.push(cell.SupItem.QueryData.AllItems);
    }
  }

  list.reverse();
  return list.reduce((a, b) => {
    a.push.apply(a, b)
    return a
  }, []);
}