import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './DictUtil'
import * as iUtil from '../item/ItemUtil'

export class Dict {
  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = iUtil.get(this)
    this.Items.push(q)
    return q;
  }

  GetItemById = (id, handle) => {
    util.GetItemById(this, id, handle);
  }
}