import { Factory } from "./Factory"
import * as genUtil from '../../../../../../../util/generalUtil';
import * as cellUtil from '../../cell/base/MassCellUtil'

import * as mif from '../../../field/MassItemField'

import * as ut from '../../../unit/MassUnitType'
import * as u_dt from '../../../../update/Update_DirectionType'
import * as u_sn from '../update/MRU_SectionName'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import * as r_st from '../save/SaveType'
import { PMData } from "../../../../../single/model/PMData";

// Command
import * as tct from '../../command/item/base/CommandType'

const pmData = () => PMData.Instance;

// Instance
export const get = (dict) => {
  var qI = new Factory();
  qI.Dict = dict;
  return qI;
}

// Method
export const Run = (q) => {
  if (!Validate(q)) return

  var row = q.Row;
  var table = row.Table
  // Command
  table.CommandDict.Do(
    {
      action: tct.subgroup,
      target: {
        type: 'row',
        key: row.RetrieveData
      },
      value: q.Fields
    }, row
  )
}

const Validate = (q) => {
  if (!q.Fields) {
    q.Fields = []
  }

  var subFields = q.Fields;
  var row = q.Row;

  var isSame = true;
  if (row.SubFields.length !== subFields.length) {
    isSame = false;
  } else {
    row.SubFields.forEach((x, i) => {
      if (isSame && x !== subFields[i]) {
        isSame = false;
      }
    })
  }
  if (isSame) return false;

  return true;
}

export const MainRun = (q, value, mainHandle, handleUndo) => {
  q.Fields = value;

  // Undo
  var undoFuncs
  if (handleUndo) {
    undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x());
    });
  };

  const addUndo = (undo) => {
    if (handleUndo) {
      undoFuncs.push(undo);
    }
  }

  var row = q.Row;

  // set subcells
  const finish = () => {
    row.IsDataPending = false
    if (mainHandle) {
      mainHandle();
    }
  }

  const updateSubFields = () => {
    Update_SubFields(q, finish);
  }

  const updateUnit = () => {
    Update_Unit(q, updateSubFields, addUndo)
  }

  var updateSubCells_Funcs = []
  const updateSubCells = () => {
    var usc_incre = genUtil.IncreFunc(updateSubCells_Funcs.length,
      updateUnit)

    updateSubCells_Funcs.forEach(x => x(usc_incre));
  }

  // Thit lp SubComponent
  Setup_GroupComponent(q, addUndo)

  // OpenSub
  // Undo
  var oldShowSub = row.IsShowSub;
  if (handleUndo) {
    undoFuncs.push(() => {
      row.IsShowSub = oldShowSub
    })
  }

  // Do
  row.IsShowSub = true;

  // Th hin trang thi x l d liu
  row.IsDataPending = true;

  var subFields = q.Fields;

  // Tnh ton cell byitem hoc bycell
  var cells = row.Cells;

  var cell_length = cells.length;
  var cell_i = 0;
  const cell_incre = () => {
    cell_i++;
    if (cell_i === cell_length) {
      // setSubCells();
      //updateUnit();
      updateSubCells();
    }
  }

  cells.forEach(cell => {
    Cell_Group(cell, subFields, cell_incre, undefined,
      (pf) => updateSubCells_Funcs.push(pf))
  })
}

// Thit lp cc thng tin SubFields trc khi chy lnh
const Setup_GroupComponent = (q, handleUndo) => {
  // Undo
  var undoFuncs
  if (handleUndo) {
    undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x());
    });
  };

  var row = q.Row;
  var subFields = q.Fields;

  // Undo
  if (handleUndo) {
    var oldSubItems = row.SubItems;
    undoFuncs.push(() => {
      row.SubItems = oldSubItems
    })
  }
  // Do
  row.SubItems = []

  // Undo
  var oldSubFields = row.SubFields;
  if (handleUndo) {
    undoFuncs.push(() => {
      row.SubFields = oldSubFields
    });
  }
  // Do
  row.SubFields = subFields;
}

const Cell_Group = (cell, fields, mainHandle, handleUndo, pendingHandle) => {
  // Pending Func
  var pendingFuncs = []

  pendingHandle((p_handle) => {
    var p_incre = genUtil.IncreFunc(pendingFuncs.length, p_handle)
    pendingFuncs.forEach(x => x(p_incre));
  });

  var subGroup_Query
  if (cell.SubGroups_Query) {
    subGroup_Query = genUtil.SingleFilter(cell.SubGroups_Query,
      (x) => x.subfield === fields[0]);
  }

  if (subGroup_Query) {
    const nextFields_Func = () => {
      // Next SubFields
      var nextFields = fields.filter((x, i) => i !== 0);
      if (nextFields.length !== 0) {
        var subCells = cell.SubCells

        var sc_length = subCells.length;
        var sc_i = 0;
        const sc_incre = () => {
          sc_i++;
          if (sc_i === sc_length) {
            mainHandle();
          }
        }

        cell.SubCells.forEach(sc => {
          Cell_Group(sc, nextFields, sc_incre, undefined, (pf) => pendingFuncs.push(pf))
        })
      } else {
        mainHandle();
      }
    }

    Cell_GroupByCell(cell, subGroup_Query, nextFields_Func, undefined,
      (pf) => pendingFuncs.push(pf));
  } else {
    const groupByItem = () =>
      Cell_GroupByItem(cell, fields, mainHandle, undefined,
        (pf) => pendingFuncs.push(pf));

    if (cell.Items) {
      groupByItem();
    } else {
      genUtil.Add_2(
        cell.OnSet_Items_Func,
        groupByItem
      )

      cell.GetItems_FromApi();
    }
  }
}

const Cell_GroupByCell = (cell, subGroup_Query, mainHandle, handleUndo, pendingHandle) => {
  var row = cell.Row
  var col = cell.Column

  const finish = () => {
    mainHandle();
  }

  const set_subcells = () => {
    var subcells = cell.SubCells = cellUtil.Get_SubCells(cell)

    // thit lp gi tr rng cho cc cells cha tnh ton Data
    subcells.map(sc => sc.ValueData)
      .filter(vd => !vd.Data).forEach(vd => {
        vd.Data = []
      })
  }

  pendingHandle((handle) => {
    set_subcells();

    handle();
  })

  row.Get_SubItems_FromApi(subGroup_Query._id, col, finish);
}

const Cell_GroupByItem = (cell, fields, mainHandle, handleUndo, pendingHandle) => {
  const finish = () => {
    mainHandle();
  }

  // items
  var items = cell.Items;

  pendingHandle((handle) => {
    const pending_finish = () => {
      handle();
    }

    if (items.length !== 0) {
      // save subgroup
      const subgroup_save = () => {
        cell.SubGroupData.Save(pending_finish);
      }

      // Refresh value
      // ( bao gm setup SubCells)
      cell.Update.Run(uc_sn.subcells, undefined, undefined,
        subgroup_save);
    } else {
      // set data rng khi cell khng c items
      var subcells = cell.SubCells = cellUtil.Get_SubCells(cell)

      // thit lp gi tr rng cho cc cells cha tnh ton Data
      subcells.map(sc => sc.ValueData).forEach(vd => {
        vd.Data = []
      })
      pending_finish();
    }
  })

  if (items.length === 0) {
    finish();
    return;
  }

  var row = cell.Row
  var table = row.Table;
  const build = table.BuildData.ByItem
  build.HandleItems(cell.Items, cell.Column, row, fields, finish)
}

// Phng thc  refresh SubCells ca Row.Cells
const Set_SubCells = (q, mainHandle, handleUndo) => {
  var row = q.Row;

  // Undo Func
  var undoFuncs
  if (handleUndo) {
    undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x());
    });
  }

  row.Cells.forEach(x => {
    // Undo
    var oldSubCells = x.SubCells
    undoFuncs.push(() => {
      x.SubCells = oldSubCells
    })

    // Do
    x.SubCells = cellUtil.Get_SubCells(x);
  })

  if (mainHandle) {
    mainHandle();
  }
}

// Phng thc update cc gi tr v unit sau khi gom nhm
const Update_Unit = (q, mainHandle, handleUndo) => {
  // Undo Func
  var undoFuncs
  if (handleUndo) {
    undoFuncs = []
    handleUndo(() => {
      undoFuncs.forEach(x => x());
    });
  }

  var row = q.Row;

  // Update
  // UpdateCell vi Unit_SetType l Both cho cc SubCell
  // row.Update.Run(
  //   {
  //     Section: u_sn.celldata,
  //     Data: { Sections: uc_sn.subcells }
  //   },
  //   u_dt.lower, false
  // )

  // Update Unit vi SubCell
  // Undo
  if (handleUndo) {
    var oldUnit = row.Unit
    undoFuncs.push(() => {
      row.Update.Run(
        {
          Section: u_sn.unit,
          Data: { Unit: oldUnit }
        },
        u_dt.none
      )
    })
  }
  // Do
  row.Update.Run(
    {
      Section: u_sn.unit,
      Data: { Unit: row.Unit ? row.Unit : ut.each }
    },
    u_dt.lower
  )

  if (mainHandle) {
    mainHandle();
  }
}

const Update_SubFields = (q, mainHandle) => {
  const func = (fields, rows) => {
    var newfields = fields.filter((x, i) => i !== 0);
    var newrows = []

    if (newfields.length !== 0) {
      rows.forEach(row => {
        var subItems = row.SubItems;
        subItems.forEach(sub => {
          sub.SubFields = newfields;
        })

        //newrows.push(subItems);
        newrows.push.apply(newrows, subItems)
      })

      if (newfields.length > 1) {
        func(newfields, newrows);
      } else {
        mainHandle();
      }
    } else {
      mainHandle();
    }
  }

  func(q.Fields, [q.Row]);
}