import PhaseVM from "./PhaseVM";

import * as genUtil from '../../../../../util/generalUtil'
import * as inputUtil from '../../../../../util/inputUtil'
import * as comUtil from '../../../../../util/commandUtil'
import { EntDelete } from "../../../../delete/EntDelete";
import { Button } from "antd";
import { CheckCircleOutlined } from "@ant-design/icons";

export const get = (q, dict) => {
  if (!q) return undefined;
  var qI = new PhaseVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Delete
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

// View
export const GetRowView = (q, i) => {
  return <tr key={q.id}>
    {/* Name */}
    <td
      onMouseEnter={
        () => inputUtil.Edit(q.NameVM, () => comUtil.Validate('edit'))}
      onMouseLeave={
        () => inputUtil.EditOK(q.NameVM)
      }
    >{q.NameView}</td>

    {/* Code */}
    <td
      onMouseEnter={
        () => inputUtil.Edit(q.DescriptionVM, () => comUtil.Validate('edit'))}
      onMouseLeave={
        () => inputUtil.EditOK(q.DescriptionVM)
      }
    >{q.DescriptionView}</td>

    {/* Command */}
    <td>
      <div className='f-jc'>
        <div>
          {q.Is_FirstCreated ?
            <Button type='primary' size='small' onClick={q.Run}>Run</Button>
            : <CheckCircleOutlined />}
        </div>
      </div>
    </td>
  </tr>
}