import { Dict } from "./Dict"
import * as iUtil from '../item/page/PageItemUtil'
import * as api from '../../../../../../util/apiUtil'
import {url} from './Dict_Url'

// Instance
export const get = (dict) => {
  var qI = new Dict();

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => iUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  const getApi = () => {
    var apiData = {
      
    }

    // iUtil.GetData(apiData, (data) => {
    //   setObj(data)
    // })

    GetData(apiData, (data) => {
      var dict = data.length !== 0 ? data[0] : undefined;
      q._id = dict ? dict._id : undefined;
      var items = dict ? dict.pageItems : []
      setObj(items);
    })
  }

  getApi()
}

// Method

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}