// Property
export const Handle_Name = (q) => {
  if (q.IsGet_Name) return undefined;
  q.IsGet_Name = true;

  var key = q.key

  const setObj = (data) => {
    q.Name = genUtil.SingleFilter(data, x => x._id === key.value).Name;
    q.IsGet_Name = false;
  }

  var host
  switch (key.field) {
    case 'project':
      host = pmData().Project_Dict;
      break;
    case 'phase':
      host = pmData().CurrentProject.Phase_Dict
      break;
  }

  if (host.Items) {
    setObj(host.Items)
  } else {
    genUtil.Add_2(host.OnSet_Items_Func,
      () => setObj(host.Items))
  }
}

export const GetQueryData = (q) => {
  return queryUtil.get(q);
}

export const GetField = (q) => {
  return q.key.field === 'project' ? mif.Project : mif.Phase;
}

export const Handle_Project = (q) => {
  if (q.IsGet_Project) return undefined
  q.IsGet_Project = true

  const setObj = (data) => {
    q.Project = data
    q.IsGet_Project = false
  }

  switch (q.Field) {
    case mif.Project:
      const setObj2 = (list) => {
        var item = genUtil.SingleFilter(list, x => x._id === q.key.value)
        setObj(item);
      }

      var host = pmData().Project_Dict
      if (host.Items) {
        setObj2(host.Items)
      } else {
        genUtil.Add_2(host.OnSet_Items_Func, () => setObj2(host.Items))
      }
      break;
    case mif.Phase:
      setObj(pmData().CurrentProject)
      break;
  }
}

export const Handle_Phase = (q) => {
  if (q.IsGet_Phase) return undefined
  q.IsGet_Phase = true

  const setObj = (data) => {
    q.Phase = data
    q.IsGet_Phase = false
  }

  switch (q.Field) {
    case mif.Phase:
      pmData().CurrentProject.Phase_Dict.GetItem(q.key.value, setObj)
      break;
  }
}

export const GetMassItem_Handle = (q) => {
  switch (q.Field) {
    case mif.Project:
      return (massitem) => massitem.Project = q.Project
    case mif.Phase:
      return (massitem) => massitem.Phase = q.Phase
  }
}