import { LeftNavVM } from "./LeftNavVM"
import * as modelUtil from './LeftNavUtil'
import { PMDataVM } from "../../../../single/form/PMDataVM";
import * as genUtil from '../../../../../../util/generalUtil'
import * as pvt from '../../../project/base/ProjectViewType'
import { Dict as MenuDict } from '../../../../../nav/dict/Dict'
import * as menuItemUtil from '../../../../../nav/item/ItemUtil'
import * as itemUtil from '../item/ItemUtil'

export const pmDataVM = () => PMDataVM.Instance;

export const get = (q) => {
  var qI = new LeftNavVM();

  qI.ModelItem = q;
  genUtil.Add(
    pmDataVM().OnSet_CurrentProject_Func_Dict,
    () => qI.CurrentProject = pmDataVM().CurrentProject,
    false);

  return qI;
}

export const GetTest = () => {
  return get(modelUtil.GetTest());
}

// Property
export const GetPreTitle = (q) => {
  return q.CurrentProject ? 'D N'
    : 'CC D N';
}

export const GetTitle = (q) => {
  return q.CurrentProject ? q.CurrentProject.Name
    : '';
}

export const GetItemDict = (q) => {
  var qI = new MenuDict();

  var wc = 'leftnav__items f'
  var ic = 'leftnav__item'
  qI.WrapperCss = [wc, wc]
  qI.ItemCss = [ic, ic]

  return qI;
}

export const GetItems = (q) => {
  var list = itemUtil.GetValues();
  var menuDict = q.ItemDict

  const getItem = (value, sup) => {
    var qI = menuItemUtil.get(menuDict);
    qI.SupItem = sup;

    qI._id = value;
    qI.Title = itemUtil.GetTitle(value);
    qI.Func = () => q.ProjectViewType = value;

    // SubItems
    qI.Items = itemUtil.GetValues(value)
      ?.map(x => getItem(x, qI))

    return qI;
  }

  return list.map(x => {
    return getItem(x);
  })
}

export const SetItems = (q) => {
  var items = GetItems(q);
  var qI = q.ItemDict;
  qI.Items = items;

  items[0].Active();
}