import * as mif from '../../../field/MassItemField'
import * as baseUtil from '../../row/query/base/QueryUtil'
import * as util from './QueryUtil'

export class Query {
  Dict

  get Column() {
    return this.Dict;
  }

  get Value(){
    return this.Column.Data;
  }

  get valueId(){
    return this.Value._id;
  }

  get Field() {
    return this.Column.Field
  }

  items
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items;
  }

  AddQuery = (obj, type='object') => {
    baseUtil.AddQuery_Base(this,obj, type);
  }

  get ApiData(){
    var obj = {};
    this.AddQuery(obj);
    return obj;
  }
}