import { message } from 'antd';
// import { url } from './PageItem_Url'
import * as util from './MassTypeExportUtil'
import * as mte_t from './MTE_Type'

export class MassTypeExport {
  ExportType = mte_t.byname

  MassType

  get ApiData() {
    var obj = {
      retrieveType: this.ExportType,
    }

    switch (this.ExportType) {
      case mte_t.byname:
        obj.name = this.MassType
        break;
      default:
        obj.defaultValue = this.MassType
        break;
    }

    return obj;
  }
}