import * as u_dt from '../../../../update/Update_DirectionType'
import * as u_sn from '../update/MRU_SectionName'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import * as dt from './MR_DeleteType'
import * as util from './MR_DeleteUtil'

// Command
import * as tct from '../../command/item/base/CommandType'

export class MR_Delete {
  Run = (data, handleUndo) => {
    util.Run(this, data, handleUndo)
  }

  DeleteHost = () => {
    var row = this.Host;
    var table = row.Table

    // Command
    table.CommandDict.Do(
      {
        action: tct._delete,
        target: {
          type: 'row',
          key: row.RetrieveData
        }
      }, row
    )
  }

  DeleteHost_Main = (handle, handleUndo) => {
    var row = this.Host;

    row.Update.Run({
      Section: u_sn._delete,
      Data: { DeleteType: dt.removedata }
    }, u_dt.upper, true,
      handleUndo)

    if (handle){
      handle();
    }
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}