import { MassInfoVM } from './MassInfoVM'
import { EntDelete } from '../../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../../delete/EntDelete_ToggleType'
import * as massTypeExportUtil from '../masstypeexport/MassTypeExportVMUtil';
import * as valueInfoUtil from '../valueinfo/ValueInfoVMUtil'
import { Button, message } from 'antd';
import { SelectVM } from '../../../../../../../select/SelectVM';
import * as mi_cvb from './MI_ClassifyValueBy'

// Instance
export const get = (q, dict) => {
  var qI = new MassInfoVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const GetMassTypeExport = (q) => {
  return massTypeExportUtil.get(q.ModelItem.MassTypeExport, q)
}

export const GetValueInfos = (q) => {
  return q.ModelItem.ValueInfos.map(x => valueInfoUtil.get(x, q))
}

export const GetClassifyValueBy_SelectVM = (q) => {
  var qI = new SelectVM();

  qI.Items = mi_cvb.GetList();
  qI.InitialItem = qI.SelectedItem = q.ClassifyValueBy

  qI.OnSelectedItemChanged = (v) => q.ClassifyValueBy = v;

  return qI;
}