import { message } from 'antd';
import * as mif from '../../../field/MassItemField'
import { url } from './Query_Url'
import * as util from './QueryUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as qt from '../../row/query/base/QueryTypeUtil'
import * as baseUtil from '../../row/query/base/QueryUtil'
import * as itemUtil from '../../row/query/item/ItemUtil'

export class Query {
  Dict

  get Cell() {
    return this.Dict;
  }

  get Row() {
    return this.Cell.Row;
  }

  // value
  value
  get Value() {
    return this.value
  } set Value(v) {
    this.value = v;
    this.valueId = v?._id;
  }

  valueId

  // field
  get Field() {
    return this.Row.Field
  } set Field(v) {
    this.Row.Field = v;
  }

  // items
  Is_Inherit_FromSup = true;

  items
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items;
  } set Items(v) {
    this.items = v;
  }

  get AllItems() {
    return util.Get_AllItems(this)
  }

  // set value
  Set_FromMassItem = (item) => {
    var valField = qt.GetValueFrom(this.Field);
    if (valField) {
      this.Value = item[qt.GetValueFrom(this.Field)]
    }
  }

  Set_FromApi = (data) => {
    this.Is_Inherit_FromSup = false;
    this.Items = data.map(x => itemUtil.GetFromDb(x, this));
  }

  // AddQuery
  AddQuery = (obj, type = 'object') => {
    baseUtil.AddQuery_Base(this, obj, type);

    if (this.Is_Inherit_FromSup) {
      var cell = this.Cell
      if (cell.SupItem) {
        cell.SupItem.QueryData.AddQuery(obj, type);
      }
    }
  }

  // apidata
  get ApiData() {
    var obj = {
      cellId: this.Cell._id,
      // items : this.Row.QueryData.ApiData
      items: this.AllItems.map(x => x.ApiData)
    }

    // thm trng thng tin ly t Query Row
    // this.Row.QueryData.AddQuery(obj);


    return obj;
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }

        if (handle) {
          handle();
        }
      });
  }
}