import { PlusCircleOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './MDS_Dict_DictVMUtil'
import * as ut from './MDS_Dict_Dict_UseType'
import './MDS_Dict_Dict.css'
import { PMDataVM } from '../../../../../single/form/PMDataVM';

const pmDataVM = () => PMDataVM.Instance

export class MDS_Dict_DictVM {
  constructor() {
    makeObservable(this, {
      useType: observable,
      current_MDS : observable,
      Is_Modal_Visible : observable
    })
  }

  items
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items;
  }

  useType = ut.query
  get UseType(){
    return this.useType
  } set UseType(v){
    this.useType = v;

    util.SetUseType(this, v)
  }

  Toggle_UseType = () => {
    this.UseType = this.UseType === ut.query ? ut.save : ut.query
  }

  queryTab
  get QueryTab() {
    if (!this.queryTab) {
      this.queryTab = util.GetQueryTab(this)
    } return this.queryTab
  }

  saveTab
  get SaveTab() {
    if (!this.saveTab) {
      this.saveTab = util.GetSaveTab(this)
    } return this.saveTab
  }

  get View() {
    return util.GetView(this)
  }

  Is_Modal_Visible = false

  get ModalView() {
    return util.GetModalView(this)
  }

  mainItem
  get MainItem(){
    if (!this.mainItem){
      this.mainItem = util.GetMainItem(this)
    } return this.mainItem
  }

  add_MDS
  get Add_MDS(){
    if (!this.add_MDS){
      this.add_MDS = util.GetAdd_MDS(this)
    } return this.add_MDS
  }

  current_MDS = undefined
  get Current_MDS() {
    return this.current_MDS
  } set Current_MDS(v) {
    this.current_MDS = v;
    this.ModelItem.Current_MDS = v?.ModelItem
  }

  get Pick_MDS_View(){
    return util.GetPick_MDS_View(this)
  }

  OpenSave = () => {
    this.Is_Modal_Visible = true
    this.UseType = ut.save
  }

  get Save_MDS_View(){
    return util.GetSave_MDS_View(this)
  }

  get Commands(){
    return pmDataVM().Commands
  }

  // Table
  get Table(){
    return this.ModelItem.Table
  }
}