import { makeObservable, observable, action } from 'mobx';
import { PMDataVM } from '../../../../single/form/PMDataVM';
import { PMData } from '../../../../single/model/PMData';
import * as util from './LeftNavVMUtil'

export const pmDataVM = () => PMDataVM.Instance
export const pmData = () => PMData.Instance

export class LeftNavVM {
  constructor() {
    makeObservable(this, {
      // projectViewType : observable,
      title : observable,
      preTitle : observable
    })
  }

  preTitle = undefined
  get PreTitle (){
    if (!this.preTitle){
      this.preTitle = util.GetPreTitle(this);
    }
    return this.preTitle;
  } set PreTitle(v){
    this.preTitle = v;
  }

  title = undefined
  get Title (){
    if (!this.title){
      this.title = util.GetTitle(this);
    }
    return this.title;
  } set Title(v){
    this.title = v;
  }

  // ItemDict
  itemDict
  get ItemDict(){
    if (!this.itemDict){
      this.itemDict = util.GetItemDict(this)
      util.SetItems(this);
    } return this.itemDict;
  }

  currentProject
  get CurrentProject(){
    return this.currentProject;
  } set CurrentProject(v){
    this.currentProject = v;

    this.ModelItem.CurrentProject = v?.ModelItem;

    // MenuItems
    util.SetItems(this);

    this.PreTitle = util.GetPreTitle(this);
    this.Title = util.GetTitle(this);
  }

  get ProjectViewType(){
    return pmDataVM().ProjectViewType;
  } set ProjectViewType(v){
    pmDataVM().ProjectViewType = v;
  }
  
  get UpdatedDate(){
    return pmData().UpdatedDate;
  }
}