import { Phase_Dict } from "./Phase_Dict"
import * as itemUtil from '../item/PhaseUtil'
import { PMData } from "../../../single/model/PMData";

const pmData = () => PMData.Instance;

// Instance
export const get = (dict) => {
  var qI = new Phase_Dict();

  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => itemUtil.GetFromDb(x, q));
    q.IsGet_Items = false;
  }

  var apiData = {
    projectId : q.Project ? q.Project._id : pmData().CurrentProject._id
  }

  itemUtil.GetData(apiData, (data) => {
    setObj(data)
  })
}