import { LinkOutlined } from '@ant-design/icons';
import { message } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../../util/generalUtil'
import { PMData } from '../../../../../single/model/PMData';
import * as util from './ValueUtil'
import { url } from './Value_Url'

const pmData = () => PMData.Instance;

export class Value {
  constructor() {
    makeObservable(this, {
      currentItem: observable,
      data : observable
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Dict

  get Cell() {
    return this.Dict;
  }

  // Data
  OnSet_Data_Func = []

  data = undefined
  get Data() {
    if (this.data === undefined) {
      util.Handle_Data(this)
    } return this.data;
  } set Data(v) {
    this.data = v;
    
    // if (this.Cell.Row.Title === 'TNG 1'){
    //   console.log(v);
    // }

    this.Cell.Row.AddHandleTotalData_Cell(this.Cell);

    genUtil.Handle(this.OnSet_Data_Func,
      (v) => this.OnSet_Data_Func = v)
  }

  RefreshData = (handle) => {
    util.Handle_Data(this, handle)
  }

  currentItem = undefined
  get CurrentItem() {
    return this.currentItem
  } set CurrentItem(v) {
    this.currentItem = v;
  }

  // Api
  get ApiData() {
    var obj = {
      // _id : this._id,
      cellId: this.Cell._id,
      reportId: this.Cell.Column.Report._id,
      valueData: this.Data.map(x => x.ApiData)
    }

    return obj;
  }

  Save = (handle) => {
    this.SaveDatabase(false, handle);
  }

  // Database
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }
}