import { Type } from './Type'
import * as massInfoUtil from '../../massinfo/base/MassInfoUtil'
import * as filterUtil from '../filter/FilterUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  
  qI.Name = data.name;
  qI.Filter = filterUtil.GetFromDb(data.filter, qI)
  qI.MassInfos = data.massInfos.map(x => massInfoUtil.GetFromDb(x, qI))

  return qI;
}

export const get = (dict) => {
  var qI = new Type();

  qI.Dict = dict;
  
  return qI;
}

// Property
export const GetFilter = (q) => {
  return filterUtil.get(q);
}

// api
