import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';

import * as genUtil from '../../../../../../util/generalUtil'
import * as inputUtil from '../../../../../../util/inputUtil'
import * as comUtil from '../../../../../../util/commandUtil'

import * as util from './ExportSetVMUtil'
import * as rs from '../../../../../text/TextRenderState';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../text/EditableTextView';
import './ExportSet.css'
import SelectView from '../../../../../select/SelectView';
import TextView from '../../../../../text/TextView';
import * as pct from '../../../command/ProjectCommand'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as erf from './ExportSet_RetrieveFrom'

const pmDataVM = () => PMDataVM.Instance

export class ExportSetVM {
  constructor() {
    makeObservable(this, {
      isActive: observable,
      massItems: observable,
      massItems_Table: observable,
      renderState: observable,
      MassType_RenderState: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  get projectId() {
    return this.ModelItem.projectId;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.NameVM.RenderState = v;
    this.DescriptionVM.RenderState = v;

    if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      this.nameVM = inputUtil.GetInput(this, 'Name', () => this.Save());
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  get NameWrapper_View() {
    return <div className='mgs__cell__wrapper'>
      {this.NameView}
    </div>
  }

  OnSet_MassType_Func = []

  massType
  get MassType() {
    if (!this.massType) {
      util.Handle_MassType(this)
    }
    return this.massType
  } set MassType(v) {
    this.massType = v;

    if (v) {
      this.massTypeId = v?.id;
      this.MassType_Render.Content = v.Name;
    }

    genUtil.Handle(this.OnSet_MassType_Func,
      (v) => this.OnSet_MassType_Func = v)
  }

  get massTypeId() {
    return this.ModelItem.massTypeId;
  } set massTypeId(v) {
    this.ModelItem.massTypeId = v;
  }

  massType_Render
  get MassType_Render() {
    if (!this.massType_Render) {
      this.massType_Render = util.GetMassType_Render(this)
    } return this.massType_Render
  }

  massType_Select
  get MassType_Select() {
    if (!this.massType_Select) {
      this.massType_Select = util.GetMassType_Select(this)
    } return this.massType_Select
  }

  MassType_RenderState = rs._default

  MassType_Edit = () => {
    if (comUtil.Validate(pct.edit)) {
      this.MassType_RenderState = rs.edit
    }
  }

  MassType_EditOK = () => {
    if (!this.MassType_Select.Open) {
      this.MassType_RenderState = rs._default

      if (this.TempMassType === this.MassType) return;
      this.MassType = this.TempMassType;
      this.SaveDatabase(false);
    }
  }

  get MassType_View() {
    switch (this.MassType_RenderState) {
      case rs._default:
        return <TextView store={this.MassType_Render} />
      case rs.edit:
        return <SelectView store={this.MassType_Select} />
    }
  }

  // Description
  get Description() {
    return this.ModelItem.Description;
  } set Description(v) {
    this.ModelItem.Description = v;
  }

  descriptionVM
  get DescriptionVM() {
    if (!this.descriptionVM) {
      this.descriptionVM = inputUtil.GetInput(this, 'Description', () => this.Save());
    }
    return this.descriptionVM;
  }

  get DescriptionView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.DescriptionVM} />
  }

  get DescriptionWrapper_View() {
    return <div className='mgs__cell__wrapper'>
      {this.DescriptionView}
    </div>
  }

  get InfoView() {
    return util.GetInfoView(this)
  }

  get RetrieveFrom() {
    return this.ModelItem.RetrieveFrom
  }

  // Mass Multiply
  get Mass_Multiply() {
    return this.ModelItem.Mass_Multiply;
  } set Mass_Multiply(v) {
    this.ModelItem.Mass_Multiply = v;
  }

  mass_MultiplyVM
  get Mass_MultiplyVM() {
    if (!this.mass_MultiplyVM) {
      this.mass_MultiplyVM = inputUtil.GetInput(this, 'Mass_Multiply', () => this.Save());
    }
    return this.mass_MultiplyVM;
  }

  // Project
  get Project() {
    return this.Dict.Project
  }

  // Active
  isActive = false
  get IsActive() {
    return this.isActive
  } set IsActive(v) {
    this.isActive = v;
  }

  get ActiveStyle() {
    return this.IsActive ? 'active' : ''
  }

  Set_Active = () => {
    var dict
    var currentField

    switch (this.RetrieveFrom) {
      case erf.model:
        dict = this.Dict.ModelView;
        currentField = "CurrentItem";
        break;
      case erf.input:
        dict = this.Dict.InputView
        currentField = "TempCurrentItem";
        break;
    }

    if (dict[currentField]) {
      dict[currentField].IsActive = false
    }

    dict[currentField] = this;
    this.IsActive = true;
  }

  UnSet_Active = () => {
    var dict
    var currentField

    switch (this.RetrieveFrom) {
      case erf.model:
        dict = this.Dict.ModelView;
        currentField = "CurrentItem";
        break;
      case erf.input:
        dict = this.Dict.InputView
        currentField = "TempCurrentItem";
        break;
    }

    dict[currentField] = undefined
    this.IsActive = false;
  }

  activeTool
  get ActiveTool() {
    if (!this.activeTool) {
      this.activeTool = util.GetActiveTool(this)
    } return this.activeTool;
  }

  // Row
  get RowView() {
    return util.GetRowView(this)
  }

  // Save
  Save = (handle) => {
    this.ModelItem.Save(handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // MassItem
  OnSet_MassItems_Func = []

  massItems = undefined
  get MassItems() {
    if (!this.massItems) {
      util.Handle_MassItems(this)
    } return this.massItems
  } set MassItems(v) {
    this.massItems = v;

    genUtil.Handle(this.OnSet_MassItems_Func,
      (v) => this.OnSet_MassItems_Func = v)
  }

  // MassItems_Table
  massItems_Table = undefined
  get MassItems_Table() {
    if (!this.massItems_Table) {
      util.Handle_MassItems_Table(this)
    } return this.massItems_Table
  } set MassItems_Table(v) {
    this.massItems_Table = v;
  }

  // Save MassItems
  SaveMassItems = () => {
    this.ModelItem.SaveMassItems();
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }

  CheckCommand = () => {
    return this.Commands.includes(pct.edit)
  }
}