import { MassDetailSetting_Dict } from "./MassDetailSetting_Dict"
import * as iUtil from '../item/MassDetailSettingUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import * as saveUtil from '../save/SaveUtil'
import { PMData } from "../../../../single/model/PMData";

export const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new MassDetailSetting_Dict();

  qI.Dict = dict

  return qI;
}

// Property
export const ReHandle_Items = (q) => {
  q.IsGet_Items = false;
  Handle_Items(q);
}

export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => iUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  const getApi = () => {
    var apiData = {

    }

    iUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}

export const GetSave = (q) => {
  return saveUtil.get(q);
}

// Method
