import { Save } from "./Save"
import * as genUtil from '../../../../../../util/generalUtil'
import * as mgsUtil from "../item/base/MassGroupSettingUtil";
import { PMData } from "../../../../single/model/PMData";
import * as mgsd_ut from '../dict/base/MGS_Dict_UseType'
import { message } from "antd";

export const pmData = () => PMData.Instance;

// Instance
export const get = (dict) => {
  var qI = new Save();
  qI.Dict = dict;
  return qI;
}

// Property


// Method
export const Run = (q) => {
  var host = q.Dict;

  const setObj = () => {
    var items = host.Items
    var name = q.Name

    const setObj2 = () => {
      var item = genUtil.SingleFilter(items, (x) => x.Name === name);
      if (!item) {
        item = mgsUtil.get(host);
        item.Name = name;
        // items.push(item);
        host.AddItem(item);
      }

      const saveFunc = () => {
        item.SaveDatabase(false, () => {
          const successFunc = () => {
            // Khi lu thnh cng, ko cho php cch command trc 
            // pmData().Current_MassTable.CommandDict.Clear();
            message.success('save successfully!');
          }

          successFunc();
        });
      }

      // Nu Setting l template th truy xut thng tin 
      // CommandItems cn lu trc
      if (item.UseType === mgsd_ut.template) {
        const setCommand_Items = () => {
          item.CommandData.Items = q.Command_Items;
          saveFunc();
        }

        if (item.CommandData) {
          setCommand_Items()
        } else {
          genUtil.Add_2(item.OnSet_CommandData_Func,
            setCommand_Items)
        }
      } else {
        saveFunc();
      }
    }

    setObj2()
  }

  if (host.Items) {
    setObj()
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      setObj
    )
  }
}

