import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { DictVM } from '../dict/base/DictVM';
import './Table.css'
import TableItemView from './TableItemView';
import TabView from '../../../tab/TabView';

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {/* <table>
          <tbody>
            {data.Items?.map(x => {
              return <TableItemView key={x.id} store={x} />
            })}
          </tbody>
        </table> */}

        <div>
          {data.Items?.map(x => {
            return <TableItemView key={x.id} store={x} />
          })}
        </div>
      </>
    );
  }
}

export default observer(TableView);
