import { SubGroup } from "./SubGroup"
import * as itemUtil from '../item/ItemUtil'
import { url } from './SubGroup_Url'
import * as api from '../../../../../../../../util/apiUtil';
import * as genUtil from '../../../../../../../../util/generalUtil';

// Instance
export const get = (dict) => {
  var qI = new SubGroup();
  qI.Dict = dict;
  return qI;
}

// Property
export const GetItem = (q) => {
  return itemUtil.get(q);
}

// Method
export const Save = (q, mainHandle) => {
  // save
  const finish = () => {
    if (mainHandle){
      mainHandle();
    }
  }

  // var subs = q.Dict.SubCells
  var subs = q.Dict.HaveValue_SubCells
  
  // save subgroup of subcells
  var sub_sg_Save_length = subs.length;
  var sub_sg_Save_i = 0;
  const sub_sg_Save_incre = () => {
    sub_sg_Save_i++
    if (sub_sg_Save_i === sub_sg_Save_length) {
      finish();
    }
  }

  const saveSub_SubGroup = () => {
    subs.forEach(sub => {
      sub.SubGroupData.Save(sub_sg_Save_incre)
    })
  }

  // Save subgroup item
  const saveItem = (handle) => {
    q.Item.Save(handle);
  }

  // save subgroup
  const save = (handle) => {
    q.SaveDatabase(false, handle)
  }

  // find subgroup
  const find = (handle) => {
    var apiData = q.ApiData

    const apiFunc = () => api.post(`${url}/find`, apiData);
    apiFunc().then(res => res.data).then(data => {
      data = data.length === 1 ? data[0] : undefined
      handle(data)
    })
  }

  // hm x l chnh
  const main = () => {
    find(data => {
      if (!data) {
        save(() => saveItem(saveSub_SubGroup))
      } else {
        q._id = data._id;

        saveItem(saveSub_SubGroup)
      }
    })
  }

  // save cc subcell - valuedata
  var subSave_length = subs.length;
  var subSave_i = 0;
  const subSave_incre = () => {
    subSave_i++
    if (subSave_i === subSave_length) {
      main();
    }
  }

  subs.forEach(sub => {
    sub.Save(subSave_incre);
  })
}