import { BaseVM } from "./BaseVM";
import * as listDataUtil from '../list/dict/DictVMUtil'
import * as categoryBindingDataUtil from '../categorybinding/dict/DictVMUtil'
import * as materialBindingDataUtil from '../materialbinding/dict/DictVMUtil'
import * as systemBindingDataUtil from '../systembinding/dict/DictVMUtil'

// Instance
export const get = (q, dict) => {
  var qI = new BaseVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const GetListData = (q) => {
  return listDataUtil.get(q.ModelItem.ListData, q)
}

export const GetCategoryBindingData = (q) => {
  return categoryBindingDataUtil.get(q.ModelItem.CategoryBindingData, q)
}

export const GetMaterialBindingData = (q) => {
  return materialBindingDataUtil.get(q.ModelItem.MaterialBindingData, q)
}

export const GetSystemBindingData = (q) => {
  return systemBindingDataUtil.get(q.ModelItem.SystemBindingData, q)
}