import * as valueItemUtil from './MI_ValueItemUtil'
import * as genUtil from '../../../../../../util/generalUtil'

// Property
export const Handle_FullData = (q) => {
  if (q.IsGet_FullData) return undefined;
  q.IsGet_FullData = true;

  var massItem = q.Dict;

  const setObj = () => {
    var typlevel_count = massItem.TLS_LevelGroup ?
      massItem.TLS_LevelGroup.Count : 1

    var exportset = massItem.ExportSet

    const setObj2 = (es_multi) => {
      q.FullData = q.Data.map(x => {
        var obj = valueItemUtil.Clone(x);
        obj.Value *= typlevel_count * es_multi;
        return obj;
      });
      q.IsGet_FullData = false;
    }

    if (!massItem.Phase){
      var es_multi = exportset.Mass_Multiply ? exportset.Mass_Multiply : 1
      setObj2(es_multi);
    } else {
      exportset.PhaseDict.GetItem(massItem.Phase._id, (item) => {
        if (item.Mass_Multiply){
          setObj2(item.Mass_Multiply);
        } else {
          genUtil.Add_2(item.OnSet_Mass_Multiply_Func,
            () => setObj2(item.Mass_Multiply))
        }
      })
    }    
  }

  var incre = genUtil.IncreFunc(2, setObj);

  var host = massItem;
  if (host.TLS_LevelGroup || host.Get_TLS_LevelGroup) {
    incre();
  } else {
    genUtil.Add_2(host.OnGet_TLS_LevelGroup_Func_Dict, incre);
  }

  if (host.ExportSet) {
    incre();
  } else {
    genUtil.Add_2(host.OnSet_ExportSet_Func, incre)
  }
}