import * as genUtil from '../../../../../../../util/generalUtil'
import * as api from '../../../../../../../util/apiUtil'
import { PMData } from '../../../../../single/model/PMData'
import * as mif from '../../../../mass/field/MassItemField'
import * as mut from '../../../../mass/unit/MassUnitType'
import * as cgt from '../column/Column_GroupType'
import * as colGroupUtil from '../columngroup/ColumnGroupUtil'
import { Table } from './Table'
import { url } from './TableUrl'
import MassItem from '../../../../mass/item/base/MassItem'
import * as ut from './Table_UseType'
import { ExportSetVM } from '../../base/ExportSetVM'
import { MassDetailVM } from '../../../../mass/detail/MassDetailVM'
import { PMDataVM } from '../../../../../single/form/PMDataVM'

const pmData = () => PMData.Instance
const pmDataVM = () => PMDataVM.Instance

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  qI._id = data._id;
  qI.Infos = data.infos;
  qI.Values = data.values;
  return qI;
}

export const get = (dict) => {
  var qI = new Table();

  if (dict instanceof ExportSetVM) {
    qI.UseType = ut.input
  } else if (dict instanceof MassDetailVM) {
    // Lu vt trong pmData
    pmData().MassDetail_Table = qI;

    qI.UseType = ut.detail
  }

  qI.Dict = dict;
  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data
    q.IsGet_Items = false;
  }

  var host = q.Dict
  if (host instanceof ExportSetVM) {
    if (host.MassItems) {
      setObj(host.MassItems)
    }

    genUtil.Add_2(
      host.OnSet_MassItems_Func,
      () => setObj(host.MassItems),
      false
    )
  }
}

export const GetDefaultInfos = (q) => {
  return [mif.workItem, mif.level]
}

export const GetInfos = (q) => {
  return q.GetColumnGroup(cgt.info)
    .Columns.map(x => x.Field);
}

export const GetDefaultValues = (q) => {
  return [mut.each]
}

export const GetValues = (q) => {
  return q.GetColumnGroup(cgt.value)
    .Columns.map(x => x.Field);
}

export const GetColumnGroups = (q) => {
  var qInfo = colGroupUtil.get(cgt.info, q)
  qInfo.Fields = q.Infos;

  var qValue = colGroupUtil.get(cgt.value, q)
  qValue.Fields = q.Values;

  return [
    qInfo, qValue
  ]
}

export const GetColumnCount = (q) => {
  return q.ColumnGroups.reduce((a, b) => a + b.Columns.length, 0);
}

// Method
export const GetItems_FromPaste = (q, rawData, mainHandle) => {
  q.Refresh_Info();

  var infos = q.Infos;
  var infoCount = infos.length
  var values = q.Values;
  var totalCount = infoCount + values.length;

  var length = rawData.length
  var massItems = []
  var isHandleItem_Done
  var isHandleInfo_Done

  const handle = () => {
    if (isHandleItem_Done && isHandleInfo_Done) {
      if (mainHandle) {
        mainHandle(massItems);
      }
    }
  }

  var item_i = 0;
  const handle_item = () => {
    item_i++;
    if (item_i === length) {
      isHandleItem_Done = true;
      handle();
    }
  }

  var info_i = 0;
  const handle_itemInfo = () => {
    info_i++;
    if (info_i === length) {
      isHandleInfo_Done = true;
      handle();
    }
  }

  rawData.forEach(x => {
    var qI = new MassItem();
    var infoDict = qI.Info = {}
    var valueDict = qI.ValueDict
    valueDict.Dict = qI;

    // Khi xng trc value each = 1 
    // cho ng b cc d liu khc
    valueDict.GetItem(mut.each).Value = 1;

    // Truy xut InfoValues v ValueValues
    var q_infos = [...infos];
    var infoValues = x.filter((y, i) => i < infoCount);
    var valueValues = x.filter((y, i) => i >= infoCount && i < totalCount);

    // Kim tra c Level, WorkItem field ko? nu ko th thm undefine
    var dbindexFields = [mif.level, mif.workItem]

    dbindexFields.forEach(y => {
      if (!q_infos.includes(y)) {
        q_infos.push(y);
        infoValues.push(undefined);
      }
    })

    // Handle valueItem
    var info_item_i = 0;
    var info_length = infoValues.length
    const handle_itemInfo_item = () => {
      info_item_i++;
      if (info_item_i === info_length) {
        handle_itemInfo();
      }
    }

    // Gn Info Value
    for (let i = 0; i < infoValues.length; i++) {
      var infoField = q_infos[i]
      var infoVal = infoValues[i];

      if (dbindexFields.includes(infoField)) {
        const setInfo = (f, v) => qI[f] = v;

        switch (infoField) {
          case mif.level:
            var host = pmData().CurrentProject.LevelDict;
            host.GetLevel(infoVal, (item) => {
              setInfo('Level_Obj' ,item);
              handle_itemInfo_item();
            })
            break;
          case mif.workItem:
            var host = pmData().WorkItem_Dict;
            host.GetItem(infoVal, (item) => {
              setInfo('WorkItem_Obj',item);
              handle_itemInfo_item();
            })
            break;
        }
      } else {
        infoDict[infoField] = infoVal;
        handle_itemInfo_item();
      }
    }

    // Gn Value Value
    for (let i = 0; i < valueValues.length; i++) {
      var valueVal = valueValues[i];

      if (!isNaN(valueVal)) {
        valueDict.GetItem(values[i])
          .Value = parseFloat(valueVal);
      }
    }

    massItems.push(qI);
    handle_item();
  })
}

// Save
export const GetSave = (q) => {
  switch (q.UseType) {
    case ut.input:
      return q.SaveDatabase
    case ut.detail:
      return pmDataVM().Mds_Dict_Dict.OpenSave
  }
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}