import * as genUtil from '../../../../../../../../util/generalUtil'
import * as util from './SystemUtil'
import * as typeUtil from '../type/TypeUtil'

export class System {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Classification

  // Types
  Types = []

  AddType = () => {
    var q = typeUtil.get(this)
    this.Types.push(q)
    return q
  }

  // Api
  get ApiData() {
    return {
      classification : this.Classification,
      types : this.Types.map(x => x.Name)
    }
  }

  // Index
  get CurrentList() {
    return this.Dict.Systems
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
  }
}