import * as util from './ReportUtil'
import * as genUtil from '../../../../../util/generalUtil'
import {url} from './ReportUrl'
import { message } from 'antd'

export class Report {
  projectId

  versionIds

  phaseIds

  IsUpdate_MassType = false

  // Api
  get ApiData() {
    return {
      _id: this._id,
      projectId : this.projectId,
      versionIds : this.versionIds,
      phaseIds : this.phaseIds,
      isUpdate_MassType : this.IsUpdate_MassType
    }
  }

  Update = (handle) => {
    this.IsUpdate_MassType = true;
    this.SaveDatabase(false, handle);
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }
}