import * as genUtil from '../../../../../../../util/generalUtil'
import { MGS_Dict_Dict } from './MGS_Dict_Dict'
import * as mgsdUtil from '../base/MassGroupSetting_DictUtil'
import * as mgsd_ut from '../base/MGS_Dict_UseType'

// Instance
export const get = () => {
  return new MGS_Dict_Dict();
}

// Property
export const GetItems = (q) => {
  return [
    mgsdUtil.get(mgsd_ut.project, q),
    mgsdUtil.get(mgsd_ut.template, q),
  ]
}