  GetValue = (field, handle, isMappingLevel = true) => {
    if (!field) {
      return;
    }

    var isHandle

    if (field === mif.level && isMappingLevel) {
      // Truy xut thng tin Level
      var obj = this.LevelItem;

      // Cha truy xut Value
      if (!obj.IsGet_Value && handle) {
        isHandle = true;

        genUtil.Add_2(
          obj.OnSet_Value_Func_Dict,
          () => {
            handle(obj.Value)
          }
        );
      }

      // return obj.Value;
      return {
        Value: obj.Value,
        IsHandle: isHandle
      }
    } else if (field === mif.MassType){
      // Truy xut thng tin MassType
      isHandle = true;

      if (this.MassType_Obj){
        handle(this.GetValue_Mas(field))
      } else {
        genUtil.Add_2(this.OnSet_MassType_Obj_Func,
          () => handle(this.GetValue_Mas(field)))
      }

      return {
        IsHandle : isHandle
      }
    } else if (field === mif.Project){
       // Truy xut thng tin Project
       isHandle = true;

       if (this.Project){
         handle(this.GetValue_Mas(field))
       } else {
         genUtil.Add_2(this.OnSet_Project_Func,
           () => handle(this.GetValue_Mas(field)))
       }
 
       return {
         IsHandle : isHandle
       }
    }

    return {
      Value: this.GetValue_Mas(field),
      IsHandle: isHandle
    }
  }

  GetValue_Mas = (field) => {
    if (!field) {
      return;
    }

    var data = mif.GetFullFields(this, field);

    const getVal_FromFields = (dt) => {
      var { fields, isFromElement } = dt;

      const getValFunc = () => {
        var obj = this;
        var isValid = true;

        fields.forEach(x => {
          if (isValid) {
            var temp = obj[x];

            obj = temp;
            if (obj === undefined || obj === null) {
              isValid = false;
            }
          }
        })

        if (field === mif.Value) {
          var roundingFactor = 2;

          if (this.Unit === 'each') {
            roundingFactor = 0;
          }

          obj = obj.toLocaleString('en-US', { maximumFractionDigits: roundingFactor });
        }

        return obj;
      }

      if (!isFromElement || this.Element) {
        var val = getValFunc();
        return val;
      } else {
        return undefined;
      }
    }

    const getVal = () => {
      if (Array.isArray(data)) {
        var isValid = false;
        var finalVal

        data.forEach(x => {
          if (isValid) return;

          var val = getVal_FromFields(x);
          if (val) {
            finalVal = val;
            isValid = true;
          }
        })

        return finalVal;
      } else {
        return getVal_FromFields(data);
      }
    }

    return getVal();
  }

  get ApiData() {
    return {
      _id: this._id,
      projectId: this.projectId,
      elementId: this.elementId,
      workpackageId: this.workpackageId,
      exportSetId: this.exportSetId,
      exportSetVersionId: this.exportSetVersionId,
      value: this.Value,
      unit: this.Unit,
      massType: this.MassType,
      info: this.Info
    }
  }

  get ApiData_ForInput() {
    var obj = {
      valueData: this.ValueDict.Data.map(x => x.ApiData),
      info: this.Info,
      levelId: this.levelId,
      workItemId: this.workItemId
    }

    return obj;
  }

  AddDatabase = (handle) => {
    var apiData = this.ApiData;
    var { _id } = apiData;
    var url = `https://api.bimdev.vn/api/hbm`;

    var command = _id ? 'edit' : 'add';
    const apiFunc = () => _id ?
      api.put(`${url}/${command}`, apiData) : api.post(`${url}/${command}`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        if (command === 'add') {
          this._id = data._id;
        }
        if (handle) {
          handle(data);
        }
        message.success(`${command} mass successfully`);
      })
  }

  DeleteDatabase = (handle) => {
    var apiData = {
      _id: this._id
    };
    var url = `https://api.bimdev.vn/api/hbm`;
    const apiFunc = () => api.put(`${url}/delete`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        if (handle) {
          handle(data);
        }
        message.success(`delete mass successfully`);
      })
  }

