export const HandleItem = (q, item, col, rowFields, initialRow) => {
  var rows = initialRow ? [initialRow] : [];
  HandleRowFieldIndex(q, item, col, rows, rowFields, 0);
}


const HandleRowFieldIndex = (q, item, col, rows, rowFields, rowFieldIndex) => {
  var rowField = rowFields[rowFieldIndex]

  const func = (val) => {
    GetRow_Value_FieldIndex(q, item, col, rows, val, rowFields, rowFieldIndex);
  }

  // NewVersion: tr v data v c x l sau  ko (isHandle ?)
  // u mc t to khng c field
  if (!rowField) {
    func();
  } else {
    // Lu vt cc Handled_ValueItems khi async
    var vid = q.ValueItem_Data
    var valueItem = {
      Item: item,
      Field: rowField
    }
    var isHandleVal = false;

    var data = item.GetValue(rowField, (val) => {
      func(val)
      // Xa phn t sau khi thc hin ly Value thnh cng
      vid.DeleteItem(valueItem);

      isHandleVal = true;
    });

    if (!data.IsHandle) {
      var val = data.Value;
      func(val);
    } else {
      // Lu vt Value cn ly async
      if (!isHandleVal) {
        vid.AddItem(valueItem);
      }
    }
  }
}

// X l to Row vi gi tr tng ng v Cell bn trong
const GetRow_Value_FieldIndex = (q, item, col, rows, val, rowFields, rowFieldIndex) => {
  var table = q.Table;

  // Sup Row ca dng khi lng ang xt
  var lastRow = rows.length !== 0 ? rows[rows.length - 1] : undefined

  var rowField = rowFields[rowFieldIndex];
  if (!rowField) {
    // AddHandledItems(q, item);
    return;
  }

  var valueString = mif.GetValueString(val, rowField);
  var fullValue = lastRow ? `${lastRow.FullValue}__${valueString}` : valueString;

  var row = undefined;
  const handleRow = (row) => {
    // Nu Field l Cng tc, thm vo cc u mc cng tc ca Table
    row.Field = rowField;

    // // Thit lp tham s truy xut  gom nhm v sau
    // row.QueryData.Set_FromMassItem(item);

    // Gn thng tin gc vo Row
    // row.RawValue = valueString;
    rows.push(row);

    // var cell = table.GetCell(col, row);
    var cell = row.GetCell(col);
    // Thit lp tham s truy xut  gom nhm v sau
    cell.QueryData.Set_FromMassItem(item);
    cell.Items.push(item);

    if (rowFieldIndex === rowFields.length - 1) {
      // AddHandledItems(q, item);
    } else {
      HandleRowFieldIndex(q, item, col, rows, rowFields, rowFieldIndex + 1);
    }
  }

  row = table.GetRow(valueString, lastRow)
  // addRow(lastRow);

  handleRow(row);
}

