import { DictVM } from "./DictVM";
import * as genUtil from '../../../../../../../util/generalUtil'
import { PMData } from "../../../../../single/model/PMData";
import * as imUtil from '../../item/ItemUtil'
import * as iUtil from '../../item/ItemVMUtil'
import { EntDelete } from '../../../../../../delete/EntDelete';
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import * as d_vt from '../../../../../../delete/EntDelete_VisibleType'
import * as ut from './Dict_UseType'
import { Button } from 'antd';
import './Dict.css'

export const pmData = () => PMData.Instance

// Instance
export const get = (q, dict) => {
  var qI = new DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  q.OnAddItem = (v) => qI.AddItem(v)
  q.OnSet_Items = (v) => qI.Items = GetItems(qI)

  return qI;
}

// Property
export const GetItems = (q) => {
  var list = q.Dict.Data;
  var field = q.Dict.CurrentField

  switch (q.UseType) {
    case ut.partial:
      var massType = q.Dict.CurrentMassType;    
      return list.filter(x => x.MassType === massType && 
        x.Field === field);
    case ut.all:
      return list.filter(x => x.Field === field);
  }
}

export const GetAddData = (q) => {
  var qI = new EntDelete();

  const func = () => {
    var host = q.Dict.ModelItem;
    host.AddData();
  }

  qI.ToggleType = d_tt.manual
  qI.Retrieve_WrapperView = () => <Button type='primary' size='small'
    className='sg__additem'
    onClick={func}>+</Button>

  return qI;
}