import * as phaseDictVMUtil from '../../phase/dict/Phase_DictVMUtil'
import ProjectVM from "./ProjectVM";
import * as levelSettingUtil from '../../setting/level/base/LevelSettingVMUtil'
import * as typicalLevelUtil from '../../setting/typicallevel/base/TypicalLevelSettingVMUtil'
import * as exportSetDictUtil from '../../exportset/dict/base/ExportSet_DictVMUtil'

import * as genUtil from '../../../../../util/generalUtil'
import * as inputUtil from '../../../../../util/inputUtil'
import * as comUtil from '../../../../../util/commandUtil'
import * as dragUtil from '../../../../../util/dragUtil'

import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { DragOutlined } from "@ant-design/icons";
import { EntDelete } from '../../../../delete/EntDelete';

export const get = (q, dict) => {
  if (!q) return undefined;
  var qI = new ProjectVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

export const GetPhase_DictVM = (q) => {
  var phaseDict = q.ModelItem.Phase_Dict;
  return phaseDictVMUtil.get(phaseDict);
}

export const GetExportSet_Dict = (q) => {
  return exportSetDictUtil.get(q.ModelItem.ExportSet_Dict, q)
}

// LevelSetting
export const Handle_LevelSetting = (q) => {
  if (q.IsGet_LevelSetting) return undefined
  q.IsGet_LevelSetting = true;

  const setObj = (v) => {
    q.LevelSetting = levelSettingUtil.get(v, q);
  }

  var qI = q.ModelItem;
  if (qI.LevelSetting) {
    setObj(qI.LevelSetting)
  } else {
    genUtil.Add_2(
      qI.OnSet_LevelSetting_Func_Dict,
      () => {
        setObj(qI.LevelSetting)
      },
      false)
  }
}

// TypicalLevelSetting
export const GetTypicalLevelSetting_Func = (model, project) => {
  return typicalLevelUtil.get(model, project);
}

export const GetTypicalLevelSetting = (q) => {
  var project = q.ModelItem;
  var tls = project.TypicalLevelSetting;
  if (tls) {
    return GetTypicalLevelSetting_Func(tls, q)
  } else {
    // project.OnGetTypicalLevelSetting_Funcs.push(() =>
    //   q.TypicalLevelSetting = GetTypicalLevelSetting_Func(project.TypicalLevelSetting, q));

    genUtil.Add_2(
      project.OnGetTypicalLevelSetting_Func_Dict,
      () =>
        q.TypicalLevelSetting = GetTypicalLevelSetting_Func(project.TypicalLevelSetting, q),
      false
    )
  }
}

export const GetTypicalLevelSetting_ForEdit = (q) => {
  var project = q.ModelItem;
  var tls = project.TypicalLevelSetting_ForEdit;
  if (tls) {
    return GetTypicalLevelSetting_Func(tls, q)
  } else {
    // project.OnGetTypicalLevelSetting_Funcs.push(() => {
    //   q.TypicalLevelSetting_ForEdit = GetTypicalLevelSetting_Func(project.TypicalLevelSetting_ForEdit, q)
    // });

    genUtil.Add_2(
      project.OnGetTypicalLevelSetting_Func_Dict,
      () =>
        q.TypicalLevelSetting_ForEdit = GetTypicalLevelSetting_Func(project.TypicalLevelSetting_ForEdit, q),
      false
    )
  }
}

// Commands
export const Handle_Commands = (q) => {
  if (q.IsGet_Commands) return undefined;
  q.IsGet_Commands = true;

  const setObj = (data) => {
    q.Commands = data
    q.IsGet_Commands = false;
  }

  var host = q.ModelItem
  if (host.Commands) {
    setObj(host.Commands)
  }

  genUtil.Add_2(
    host.OnSet_Commands_Func,
    () => setObj(host.Commands),
    false
  )
}

// Delete
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

// View
export const GetRowView = (q, i) => {
  return <Draggable key={q.id} draggableId={q.id} index={i}>
    {(provided, snapshot) =>
      <tr {...provided.draggableProps} ref={provided.innerRef}
        style={dragUtil.getItemStyle(
          snapshot.isDragging,
          provided.draggableProps.style
        )}>
        <td {...provided.dragHandleProps}
          className='move-td'>
          <DragOutlined />
        </td>
        {/* Name */}
        <td
          onMouseEnter={
            () => inputUtil.Edit(q.NameVM, () => comUtil.Validate('edit'))}
          onMouseLeave={
            () => inputUtil.EditOK(q.NameVM)
          }
        >{q.NameView}</td>

        {/* Code */}
        <td
          onMouseEnter={
            () => inputUtil.Edit(q.CodeVM, () => comUtil.Validate('edit'))}
          onMouseLeave={
            () => inputUtil.EditOK(q.CodeVM)
          }
        >{q.CodeView}</td>

        {/* Tester */}
        {comUtil.Validate('admin') ?
          <td className='text-center'>
            {q.FortestVM.View}
          </td> : undefined}

        {/* Commands */}
        <td>
          <div className='f-jc'>
            <div className='f'>
              {comUtil.Validate('edit') ? q.DeleteVM.View : ''}
            </div>
          </div>
        </td>
      </tr>
    }
  </Draggable>
}