import { makeObservable, observable, action } from 'mobx';
import { PMDataVM } from '../../../single/form/PMDataVM';
import { PMData } from '../../../single/model/PMData';
import * as projectUtil from '../base/ProjectUtil'
import * as projectVMUtil from '../base/ProjectVMUtil'
import * as util from './ProjectCompVMUtil'
import * as pvt from '../base/ProjectViewType'
// import TypicalLevelSettingView from '../../setting/typicallevel/TypicalLevelSettingView';
import * as pct from '../base/ProjectCommandType'
import { Button } from "antd";
import ProjectCompView from './ProjectCompView';
import * as dot from '../../design/DesignOptionType'

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export const pmData = () => {
  return PMData.Instance;
}


export default class ProjectCompVM {
  constructor() {
    makeObservable(this, {
      projectVM: observable,
    })
  }

  _id
  get id() {
    return this._id;
  }
  set id(v) {
    this._id = v;
    projectUtil.getById(v, (item) => {
      this.ProjectVM = projectVMUtil.get(item);
    })
  }

  projectVM = undefined
  get ProjectVM() {
    return this.projectVM;
  }
  set ProjectVM(v) {
    this.projectVM = v;

    pmDataVM().CurrentProject = v;

    if (pmDataVM().DesignOption === dot.web) {
      var list = [v];
      pmDataVM().Project_DictVM.Items = list;
      // pmData().Phase_Dict = v.ModelItem.Phase_Dict;
    }

    var massResComp = this.MassResultCompVM;
    massResComp.ProjectVMStorageList = list;
    massResComp.ProjectSelectVM.InitialItems = list;

    // var setItemsFuncs = () => massResComp.PhaseVMStorageList = phase_DictVM.Items;

    // var phase_DictVM = v.Phase_DictVM;
    // if (phase_DictVM.Items) {
    //   setItemsFuncs();
    // } else {
    //   phase_DictVM.OnGetItemsDone_Funcs.push(() => {
    //     setItemsFuncs();
    //   })
    // }
  }

  massResultCompVM
  get MassResultCompVM() {
    if (!this.massResultCompVM) {
      var obj = this.massResultCompVM = util.GetMassResultCompVM(this);
      pmDataVM().MassResultCompVM = obj;
    }
    return this.massResultCompVM;
  }

  get ProjectViewType() {
    return pmDataVM().ProjectViewType;
  } set ProjectViewType(v) {
    pmDataVM().ProjectViewType = v;
  }

  // View
  get ViewElement() {
    if (!this.ProjectVM) return undefined;

    var commands = pmDataVM().Commands;
    if (commands && (commands.includes(pct.view) || commands.includes(pct.edit))) {
      var id = this.ProjectVM.id;

      switch (this.ProjectViewType) {
        case pvt.mass:
          return <div className='massresult__comp__wrapper'>
            {this.MassResultCompVM.View}
          </div>

        case pvt.mappinglevel:
          return <>
            {this.ProjectVM.TypicalLevelSetting_ForEdit ?
              this.ProjectVM.TypicalLevelSetting_ForEdit.MappingView
              // <TypicalLevelSettingView key={id} store={this.ProjectVM.TypicalLevelSetting_ForEdit} />
              : ''}
          </>
        case pvt.typicallevel:
          return <>
            {this.ProjectVM.TypicalLevelSetting_ForEdit ?
              this.ProjectVM.TypicalLevelSetting_ForEdit.TypicalView
              // <TypicalLevelSettingView key={id} store={this.ProjectVM.TypicalLevelSetting_ForEdit} />
              : ''}
          </>

        case pvt.exportset:
          return this.ProjectVM.ExportSet_Dict.ModelView.View;
        case pvt.phase:
          return this.ProjectVM.Phase_DictVM.View
        case pvt.inputdata:
          return this.ProjectVM.ExportSet_Dict.InputView.View;
        // return <ExportSetMain_View key={id} store={this.ProjectVM.ExportSet_Dict} />
      }
    }

    return pmDataVM().UnAuthorized_View
  }

  get View() {
    return <ProjectCompView store={this} />
  }
}