import * as genUtil from '../../../../../../util/generalUtil'
import * as it from './TLS_FilterValue_InputType'
import * as util from './TLS_FilterValueUtil'

export class TLS_FilterValue {
  value
  get Value(){
    return this.value;
  } set Value(v) {
    this.value = v;
  }
  
  InputType = it.select

  // Api
  get ApiData(){
    var obj = {
      value: this.Value,
      inputType : this.InputType
    }

    return obj;
  }

  // Equal
  IsEqual = (other) => {
    return util.IsEqual(this, other);
  }
}