import { message } from 'antd'
import * as genUtil from '../../../../../../../util/generalUtil'
import { url } from './CommandData_Url'
import * as util from './CommandDataUtil'
import { PMData } from '../../../../../single/model/PMData'
import * as mgsd_ut from '../../dict/base/MGS_Dict_UseType'

const pmData = () => PMData.Instance

export class CommandData {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  get Table() {
    return pmData().Current_MassTable
  }

  get Table_CommandDict() {
    return this.Table.CommandDict
  }

  Items

  get ApiData() {
    var obj = {
      _id: this._id,
      dictId: this.Dict._id,
      items: this.Items.map(x => x.ApiData),
    }

    return obj;
  }

  Save = (handle) => {
    if (this.Dict.UseType === mgsd_ut.project) {
      this.Items = this.Table_CommandDict.AllItems;
    }

    this.SaveDatabase(false, handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      }, 'edit');
  }
}