import { PMDataVM } from '../../../../single/form/PMDataVM';
import { LevelSettingDataVM } from './LevelSettingDataVM';
import * as itemUtil from '../item/LS_ItemVMUtil'
import TableVM from '../../../../../table/TableVM';
import TableColumn from '../../../../../table/TableColumn';

import * as genUtil from '../../../../../../util/generalUtil';
import * as inputUtil from '../../../../../../util/inputUtil';
import * as comUtil from '../../../../../../util/commandUtil';

import EditableTextView from '../../../../../text/EditableTextView';
import * as pct from '../../../command/ProjectCommand'

export const pmDataVM = () => PMDataVM.Instance;

// Instance
export const get = (q, dict) => {
  var qI = new LevelSettingDataVM();
  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetItems = (q) => {
  return q.ModelItem.Items.map(x => itemUtil.get(x, q));
}