import { makeObservable, observable, action } from 'mobx';
import DictView from "./DictView"

export class Dict {
  constructor() {
    makeObservable(this, {
      Items: observable,
      CurrentItem: observable
    })
  }

  WrapperCss

  get Refine_WrapperCss(){
    if (!this.WrapperCss) return []
    if (!Array.isArray(this.WrapperCss)) return [this.WrapperCss]
    return this.WrapperCss;
  }

  get Current_WrapperCss(){
    return this.Refine_WrapperCss.length > 0 ? this.Refine_WrapperCss[0] : ''
  }

  ItemCss

  get Refine_ItemCss(){
    if (!this.ItemCss) return []
    if (!Array.isArray(this.ItemCss)) return [this.ItemCss]
    return this.ItemCss;
  }

  Items = undefined

  CurrentItem = undefined

  get View() {
    return <DictView store={this} />
  }
}