import * as util from './CommandItemUtil'
import { makeObservable, observable, action } from 'mobx';
import { PMData } from '../../../../../../single/model/PMData';
import * as tct from './CommandType'

const pmData = () => PMData.Instance

export class CommandItem {
  Data

  Dict

  get Table() {
    return pmData().Current_MassTable
  }

  target
  get Target() {
    if (!this.target) {
      this.target = util.GetTarget(this)
    } return this.target
  } set Target(v) {
    this.target = v;
  }

  Do = (handle) => {
    util.Do(this, handle);
  }

  Result = (obj) => {
    util.Result(this, obj)
  }

  get ApiData() {
    return this.Data.ApiData;
  }

  Clone = () => {
    return util.Clone(this)
  }

  // Template
  get CanUser_ForTemplate() {
    var data = this.Data;

    switch (data.Action) {
      case tct.replace:
        return false;
      case tct.add:
        return !data.Target.key
      default:
        return data.Target.key.length === 1
    }
  }
}