// Method
// p dng cho c Query Row v Column
export const AddQuery_Base = (q, obj, type) => {
  const itemFunc = (item) => {
    var val = item.valueId;
    if (!val) return;

    const func = (propname) => {
      if (type === 'object') {
        obj[propname] = val;
      }
      else {
        // array
        propname = `${propname}s`
        if (!obj[propname]) {
          obj[propname] = []
        }
        obj[propname].push(val);
      }
    }

    const getPropName = () => {
      var propertyName = qt.GetPropertyName_ForApi(item.field)

      propertyName = `${propertyName}Id`
      return propertyName;
    }

    var propertyName = getPropName();

    if (propertyName) {
      func(propertyName)
      return true;
    } else {
      throw 500;
    }
  }

  q.Items.forEach(x => {
    itemFunc(x);
  })
}